import actiontypes from '../lib/constants/actionTypes'

const initialState = {};

const configData = (state = initialState, action) => {  

  switch (action.type) {

    case actiontypes.SET_CONFIG:
      return action.config;
    
    default:
      return state;
  }}

export default configData;



