// These values are also set in the SASS variables files
const deviceWidth = 536;
const deviceHeight = 633;
const controlHeight = 50;
const landingHeight = 587;
export const categoryFilterHeight = 55;

//For Quill
const quillDeviceWidth = 702;
const quillHeight = 844;
const quillControlHeight = 50;
const quillLandingHeight = 798;
export const quillCategoryFilterHeight = 60;

const epd = {
  //For piper
  DEVICE_WIDTH: deviceWidth,
  DEVICE_HEIGHT: deviceHeight,
  PAGE_HEIGHT: deviceHeight - controlHeight,
  LANDING_PAGE_HEIGHT: landingHeight - controlHeight,
  CAROUSEL_COVER_WIDTH: 115, // Must match CSS value

  //For Quill
  QUILL_DEVICE_WIDTH: quillDeviceWidth,
  QUILL_DEVICE_HEIGHT: quillHeight,
  QUILL_PAGE_HEIGHT: quillHeight - quillControlHeight,
  QUILL_LANDING_PAGE_HEIGHT: quillLandingHeight - quillControlHeight,
  QUILL_CAROUSEL_COVER_WIDTH: 150, // Must match CSS value
}

export default epd;



