//
// This class allows us to make async JSNI/Nook native API calls to iOS from Shop 
// even though Android JSNI & Nook calls are synchronous.
// 
// iOS native functions are "decorated" with the suffix "Async" when they call back
// to JS.
//

// $JSNI returns a promise
class APromise {
  constructor(that, js_interface, fname) {
    this.that = that;
    this.js_interface = js_interface;
    this.fname = fname;
    setTimeout(() => {
      this._exec();
    }, 1);
  }

  // Execute a call to the appropriate native interface without passing params (getter functions)
  _exec() {
    this.that._async_exec(this.js_interface, this.fname, this.success, this.failure);
  }

  then(success, failure) {
    this.success = success;
    this.failure = failure;
  }
}


class JsniAsync {
  constructor () {
    this._async_call_counter = 0; // Used to create dynamic global callback functions
    this._async_calling_disabled = true;

    window.js_async_callback_success_noop = () =>
      console.log('--> noop success function called');

    window.js_async_callback_failure_noop = () => {
      console.log('--> noop failure function called');
    }

    // Map synchronous JSNI functions
    this._jsni_getters_decorated = [
      'getSessionInfo',
    ];

    // These getters have params, they also call a global callback 
    this._jsni_getters_params_callback_decorated = [
      'getRecentLibraryItems',
      'getLibraryItems',
      'getCategories',
      'closeFavCatModal'
    ];

    this._jsni_setters = [
      'reload',
      'tagEvent',
      'tagScreen',
      'close',
    ];

    this._jsni_setters_decorated = [
      'setUpEnabled',
      'modifyHeader',
      'showGetConnected',
      'showError',
      'showManageActionSheet',
      'showMenu',
      'showMenu2',
      'launchUrl',
      'startYourSubscription',
      'launchBrowseAudiobooks',
      'editFavCategories',
      'openSubcategoriesModal',
      'closeModalGotoShop',
      'closeFavCatModal',
      'closeSubCategoriesModal',
      'closeSubscriptionScreen',
      'setCategories',
    ];


    // Map synchronous Nook functions
    this._nook_getters_decorated = [
      'isConnected',
      'isSelectedFavInSession',
      'isOrientationLandscape'
    ];
    this._nook_setters_decorated = [
      'openProductDetails',
      'showToast'
    ];

    // When making the synchronous call on Android the params need to be in order
    this._functionParamOrder = {
      'tagEvent': ['event','data','context'],
      'tagScreen': ['location'],
      'setUpEnabled': ['enabled'],
      'modifyHeader': ['enableBack', 'title', 'background', 'hideTools', 'hideBottomNav'],
      'openProductDetails': ['ean'],
      'showToast': ['message'],
      'getRecentLibraryItems': ['count', 'successCallback', 'failureCallback'],
      'getLibraryItems': ['eans', 'successCallback', 'failureCallback'],
      'showError': ['message'],
      'showManageActionSheet': ['ean', 'x', 'y', 'imageUrl'],
      'showMenu': ['dictionary','x', 'y', 'title', 'label', 'successCallback'],
      'showMenu2': ['dictionary','topX', 'topY','bottomX','bottomY', 'title', 'label', 'successCallback'],
      'launchUrl': ['url'],
      'openSubcategoriesModal': ['categoriesRelativePath'],
      'closeModalGotoShop': ['categoriesRelativePath'],
      'closeFavCatModal': ['selectedFavCategories', 'successCallback', 'failureCallback'],
      'getCategories': ['key', 'successCallback', 'failureCallback'],
      'setCategories': ['key', 'value'],
    };
  }


  //
  // Only need async calling on iOS. Can enable it here
  // 
  async_calling_enabled(enable) {
    console.log("=> ASYNC CALLING: " + (enable ? "enabled" : "disabled"));
    this._async_calling_disabled = (enable === false);
  }


  //
  // This is called by the APromise whenever a $JSNI function is called.
  // The interface (JSNI/Nook) must be passed.
  //
  _async_exec(js_interface, fname, good, bad) {
    if(this._async_calling_disabled) {
      this._async_exec_sync(js_interface, fname, good, bad);
    }
    else {
      this._async_exec_async(fname, good, bad);
    }
  }


  //
  // Make the call to the interface (JSNI/Nook) asynchronously (iOS) without params
  // iOS methods have the string 'Async' appended to them (decoration).
  //
  _async_exec_async(fname, good, bad) {
    if (fname.slice(-5) !== 'Async') {
      fname += 'Async'; // Append decoration to the function name to match iOS function names
    }
    this._async_call_counter++;

    // Global callback function defaults
    let sucessFuncName = "js_async_callback_success_noop";
    let failureFuncName = "js_async_callback_failure_noop";

    if (good !== undefined) {
      // Create global success callback
      sucessFuncName = "js_async_callback_success_" + this._async_call_counter;
      window[sucessFuncName] = function() {
        const responseArgs = arguments;
        // Return to client a little asynchronously so we don't block native code
        setTimeout(() => {
          console.log("--> processing async response", responseArgs[0], responseArgs[1]);
          good.apply(this, responseArgs);
          if(window[sucessFuncName])
          delete window[sucessFuncName]; // remove single-use callback
        }, 1);
      }
    }

    if (bad !== undefined) {
      // Create global failure callback
      failureFuncName = "js_async_callback_failure_" + this._async_call_counter;
      window[failureFuncName] = function() {
        console.error("--> async failure received");
        const responseArgs = arguments;
        // Return to client a asynchronously so we don't block native code
        setTimeout(() => {
          console.error("--> processing async failure", responseArgs[0], responseArgs[1]);
          bad.apply(this, responseArgs);
          if(window[failureFuncName])
          delete window[failureFuncName]; // Remove single-use callback
        }, 1);
      }
    }

    // Use webkit messageHandlers interface to talk to the iOS native code
    console.log("--> executing async call: ", fname, sucessFuncName, failureFuncName);
    if (window.webkit !== undefined && window.webkit.messageHandlers !== undefined) {
      window.webkit.messageHandlers[fname].postMessage({
        successCallback: sucessFuncName,
        failureCallback: failureFuncName
      });
    }
  }


  //
  // Make the call to the interface (JSNI/Nook) synchronously (Android) without params
  //
  _async_exec_sync(js_interface, fname, good, bad) {
    if (js_interface === undefined || js_interface[fname] === undefined) {
      return undefined;
    }
    console.log("--> making sync call: ", fname);
    let response = js_interface[fname]();
    
    // Even though Android interfaces are synchronous, the calling code is written
    // to handle both cases by expecting an asynch response.
    setTimeout(() => {
      good.call(this, response);
    }, 1);
  }


  //
  // Create the synthetic asynchronous version of the JSNI/Nook interfaces -> $JSNI
  // 
  build$JSNI() {
    const that = this;


    //
    // Function to parse arguments into an ordered array for synchronous calls (Android)
    //
    const parseArgs = (fcn, args) => {
      if (that._functionParamOrder[fcn] !== undefined) {
        return that._functionParamOrder[fcn].map((arg) => {
          let a = args[0][arg];
          return typeof a === 'object' ? JSON.stringify(a) : a;
        });
      }
    }

    // Build async $JSNI object with functions mirroring native interfaces
    let _$JSNI = {};

    //
    // Getters (no arguments)
    //
    // Add a function for each decorated JSNI getter with parameters
    this._jsni_getters_decorated.forEach((fname, idx) => {
      _$JSNI[fname] = function() { return new APromise(that, window.JSNI, fname) };
    });

    // Add a function for each decorated JSNI getter with parameters
    this._nook_getters_decorated.forEach((fname, idx) => {
      _$JSNI[fname] = function() { return new APromise(that, window.Nook, fname) };
    });

    // Add a function for each decorated JSNI getter method (these pass arguments).
    // Android JSNI will call a callback when complete
    this._jsni_getters_params_callback_decorated.forEach((fname, idx) => {
      if (that._async_calling_disabled) {
        _$JSNI[fname] = function() {
          if (window.JSNI !== undefined && window.JSNI[fname] !== undefined) {
            window.JSNI[fname].apply(window.JSNI, parseArgs(fname, arguments));
          }
        }
      }
      else if (window.webkit !== undefined) {
        _$JSNI[fname] = function() {
          window.webkit.messageHandlers[fname+'Async'].postMessage(arguments[0]);
        };
      }
    });


    //
    // Setters (pass arguments)
    //
    // Add a function for each defined JSNI setter method (these pass arguments)
    this._jsni_setters.forEach((fname, idx) => {
      _$JSNI[fname] = function() {

        // Synchronous JSNI (for Android)
        if (that._async_calling_disabled) {
          if (window.JSNI !== undefined && window.JSNI[fname] !== undefined) {
            window.JSNI[fname].apply(window.JSNI, parseArgs(fname, arguments));
          }
          // If not JSNI maybe it's in Nook interface
          // else if (window.Nook !== undefined && window.Nook[fname] !== undefined) {
          //   window.Nook[fname].apply(window.Nook, parseArgs(fname, arguments));
          // }
        }

        // Call the iOS async interface with arguments
        else if (window.webkit !== undefined) {
          window.webkit.messageHandlers[fname].postMessage(arguments[0]);
        }
      };
    });

    // Add a function for each decorated JSNI setter method (these pass arguments)
    this._jsni_setters_decorated.forEach((fname, idx) => {
      _$JSNI[fname] = function() {

        // Synchronous JSNI (for Android)
        if (that._async_calling_disabled) {
          if (window.JSNI !== undefined && window.JSNI[fname] !== undefined) {
            window.JSNI[fname].apply(window.JSNI, parseArgs(fname, arguments));
          }
          // If not JSNI maybe it's in Nook interface
          // else if (window.Nook !== undefined && window.Nook[fname] !== undefined) {
          //   window.Nook[fname].apply(window.Nook, parseArgs(fname, arguments));
          // }
        }
        // Call the iOS async interface with arguments
        else if (window.webkit !== undefined) {
          if (arguments.length === 0) {
            window.webkit.messageHandlers[fname+'Async'].postMessage({});
          }
          else {
            window.webkit.messageHandlers[fname+'Async'].postMessage(arguments[0]);
          }
        }
      };
    });

    // Add a function for each decorated Nook setter method (these pass arguments)
    this._nook_setters_decorated.forEach((fname, idx) => {
      _$JSNI[fname] = function() {

        // Synchronous Nook (for Android)
        if (that._async_calling_disabled) {
          if (window.Nook !== undefined && window.Nook[fname] !== undefined) {
            window.Nook[fname].apply(window.Nook, parseArgs(fname, arguments));
          }
          // If not Nook maybe it's in JSNI interface
          // else if (window.JSNI !== undefined && window.JSNI[fname] !== undefined) {
          //   window.JSNI[fname].apply(window.JSNI, parseArgs(fname, arguments));
          // }
        }
        // Call the iOS async interface with arguments
        else if (window.webkit !== undefined) {
          window.webkit.messageHandlers[fname+'Async'].postMessage(arguments[0]);
        }
      };
    });

    return _$JSNI;
  }
}

export default JsniAsync;



