/**
 * This component renders Nook Channels. Nook Channels is a specially-created 
 * list of lists, built in the LMS.
 *
 * Technically, Nook Channels is a "list of lists of lists" since there are two
 * lists of lists: affinity & popular.
 * HOWEVER - we only render the popular lists and treat it like any other LoL
 * 
 * As the user scrolls to the bottom of Channels, more content is automatically
 * fetched and rendered. This is called "demand loading".
 */
import React, { Component } from 'react'
import Lol from './Lol'
import Scroller from '../classes/Scroller'
import platforms from '../lib/constants/platforms'
import analytics from '../classes/Analytics'
import { fetchChannels } from '../lib/fetch'
import { screens, events, attributes } from '../lib/constants/analytics'

import '../../style-dist/components/Channels.css'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'
if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/Channels.epd.css')


class Channels extends Component {

  constructor() {
    super();

    // State for this component only, not shared with others
    this.state = {
      isFetchingChannels: false, // Control the loading animation
      isFetchingMoreChannels: false // Control the demand-loading animation
    }

    this.isEpd = window.location.href.match(platforms.epd);
    this.isQuill = window.location.href.match(platforms.quill);

    // Demand loading state is kept here locally for convenience
    this.offset = 1;
    this.listsPerSpot = 5;
    // this.itemCount = 10; Not used
    this.oldPopularListsCount = 0;
    this.allLoaded = false;
    this.isLoadingMore = false;
    this.demandLoadOffset = this.isEpd || this.isQuill ? 900 : 400;


    // Bind methods
    this.initAnalytics = this.initAnalytics.bind(this);
    this.coverClicked = this.coverClicked.bind(this);
    this.listClicked = this.listClicked.bind(this);
    this.demandLoad = this.demandLoad.bind(this);
  }


  initAnalytics() {
    this.analyticsEvent = Object.assign({}, events.channelsViewed);
    this.analyticsEvent.data = Object.assign({}, events.channelsViewed.data);
  }


  componentDidMount() {

    // Load the content
    this.openChannels();

    // Setup a scrolling handler to load more list items on scroll
    this.scroller = new Scroller();
    this.scroller.register(this.demandLoad, {
      scrollOffset: this.demandLoadOffset
    });
    this.refinementType = this.props.refinementType;
    // Analytics
    analytics.enter(window.SHOW_AUDIOBOOKS ? screens.audioBooks.channels : screens.ebooks.channels);
    this.initAnalytics();
  }


  componentWillMount() {

    // Show the native back button and title
    window.Device.modifyHeader({
      enableBack: true,
      title: textReplace(KEY.nook_channels)
    });

    // Always start at the top of page
    window.scroll(0,0);
  }


  componentWillUnmount() {

    // Disable the scroller so events don't fire after this component is gone
    this.scroller.unregister();

    // Localytics Event
    this.sendAnalytics();
  }


  componentDidUpdate() {
    if (window.location.href.match(platforms.android) && this.refinementType !== this.props.refinementType) {
      window.location.reload(false);  
    }

    // If demand loading has finished, check if all lists for each spot have been loaded
    if (this.isLoadingMore && !this.state.isFetchingMoreChannels) {
      this.isLoadingMore = false;
      let popularLists = this.props.data.popularLists;
      if (popularLists.length === this.oldPopularListsCount)  {
        this.allLoaded = true; // Loading is done for both LOLs
      }
      this.oldPopularListsCount = popularLists.length;
    }
  }


  render() {
    return <div className="shop-channels">
      <div className="channel-wrapper">
        <div className="popular">
          <Lol
            inStore={ this.props.inStore } 
            isFetching={ this.state.isFetchingChannels } 
            isFetchingMore={ this.state.isFetchingMoreChannels } 
            lists={ this.props.data.popularLists }
            eanData={ this.props.eanData }
            coverClicked={ this.coverClicked }
            listClicked={ this.listClicked } />
        </div>
      </div>
    </div>;
  }


  coverClicked(data) {
    this.analyticsEvent.data[attributes.listTitle] = data.listTitle;
    this.analyticsEvent.data[attributes.listEanPosition] = data.listEanPosition;
    this.analyticsEvent.data[attributes.listViewAllClicked] = 'No';
    this.analyticsEvent.data[attributes.listPosition] = data.listPosition;
    
    // Component doesn't unmount when goign to PDP
    this.sendAnalytics();

    // Reset the event data since we'll come back from PDP
    this.initAnalytics();
  }


  listClicked(data) {
    this.analyticsEvent.data[attributes.listTitle] = data.listTitle;
    this.analyticsEvent.data[attributes.listEanPosition] = data.listEanPosition;
    this.analyticsEvent.data[attributes.listViewAllClicked] = 'Yes';
    this.analyticsEvent.data[attributes.listPosition] = data.listPosition;
  }


  sendAnalytics() {
    this.analyticsEvent.data[attributes.shopType] = window.Device.getShopType();
    // Send the event
    analytics.exit(this.analyticsEvent);
  }


  // 
  // Either load the initial content, or, load more content into the LOL. 
  // 
  openChannels(options={}) {
    const refinementType = this.props.refinementType;
    const device = this.props.device;
    const customer = this.props.customer;
    const urlParams = {
      countryCode: customer.country,
      accountId: customer.accountId,
      profileId: customer.profileId,
      productDeviceId: device.productDeviceId,
      refinementType: refinementType,
      // itemCount: this.itemCount, // EANs per list (not used)
      startat: this.offset,
      size: this.listsPerSpot
    };

    // Only load data into the redux store if we don't already have it or
    // if we are demand loading (options.loadMore).
    const data = this.props.data;
    const isLoaded = data.popularLists.length > 0;
    if (!isLoaded || options.loadMore) {

      // Load Channels content
      this.loadChannels(urlParams, options);
    }
  }


  //
  // Load the initial Nook Channels data
  // 
  loadChannels(urlParams, options={}) {
    if (options.loadMore) {
      this.setState({ isFetchingMoreChannels: true });
    }
    else {
      this.setState({ isFetchingChannels: true });
    }

    fetchChannels(urlParams, (err, response) => {
      if (err) {
        console.error('Error fetching channels items.', err.message);
        window.Device.showError('Error fetching channels items.' + err.message);        
        response = {affinityLists:[], popularLists:[], eans: []};
      }
      else {
        this.setState({ isFetchingChannels: false, isFetchingMoreChannels: false });
      }

      // Demand load more lists (triggered by scrolling)
      if (options.loadMore) {
        this.props.updateChannels(response);
      }

      // Initial channels load
      else {
        this.props.setChannels(response);
      }
    }, {cachedResponse: false});
  }


  //
  // Callback when the user scrolls to the bottom
  // 
  demandLoad(scrollPos) {
    if (this.allLoaded || (this.isLoadingMore)) {
      return;
    }
    this.isLoadingMore = true;
    this.offset += this.listsPerSpot;
    this.openChannels({loadMore: true});
  }
}

export default Channels;



