import React, { Component } from 'react'
import NoFavoriteCategories from './NoFavoriteCategories'
import { constants, getImageName, addEllipsis } from '../lib/utils'
import { favoriteCategories } from '../lib/fetch'

import { Link } from 'react-router-dom'
import platforms from '../lib/constants/platforms'
import defaultBook from '../../../public/images/categories_icons_medium/default.svg'
import rightArrow from '../../images/right_arrow_black.svg'

import '../../style-dist/components/ExploreFavCategories.css'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'
if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/ExploreFavCategories.epd.css');
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/ExploreFavCategories.quill.css');
class ExploreFavCategories extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: []
        }
        this.slider = null;
        this.dummyFavCats = [
            { id: '-1', label: '' }, { id: '-2', label: '' }, { id: '-3', label: '' }
        ];
        this.isEPD = window.location.href.match(platforms.epd) || window.location.href.match(platforms.quill);
        this.state = {
            categoryImage: []
        }
        // bind events
        this.openFavCatWindow = this.openFavCatWindow.bind(this);
        this.editFavCategories = this.editFavCategories.bind(this);
        this.shopHome = window.SHOW_AUDIOBOOKS ? "audiobook" : "ebook";
        let customer = this.props.customer;
        this.urlParams = {
          accountId: customer.accountId,
          profileId: customer.profileId,
          shopHome: this.shopHome
        };

        this.selectedFavKey = `selectedFavCategories_${props.customer.accountId}-${props.customer.profileId}-${this.shopHome}`;
        this.focusEvent = window.location.href.match(platforms.ios) ? "focus" : "visibilitychange"; 
    }

  componentDidUpdate(prevProps, prevState) {
    if (window.location.hash.indexOf('/favupdate') !== -1) {
        let value = localStorage.getItem(this.selectedFavKey);
        this.setState({data: JSON.parse(value)});
        window.location.hash = window.location.hash.replace('/favupdate', '');
    }

    //add Ellipses
    let allCats = document.querySelectorAll(".category-name");
    for(let index = 0; index < allCats.length; index++) {
        addEllipsis(allCats[index]);
    }
  }
  componentWillReceiveProps(nextProps) {
    if (nextProps || this.props) {
        let value = localStorage.getItem(this.selectedFavKey);
        if(this.props !== nextProps && value) {
            this.setState({data: JSON.parse(value)});
        }
    }
  }

  componentDidMount() {
    this.getFavorites(); //call to reflect data immediately on first time load & sync.
    window.setTimeout(() => this.getFavorites() , 3000); //To retrieve the currently saved favs.
    window.addEventListener(this.focusEvent, this.getFavorites());//sync selected cats from profile on focus 
  }

  componentWillUnmount = () => {
    window.removeEventListener(this.focusEvent, this.getFavorites());
  }

  getFavorites() {
    favoriteCategories('get', this.urlParams, null, (err, resp) => {
        if (err) {
            console.error('Error fetching Favorite Categories.');
            resp = { favorites: [] };
        }
        else if(resp && resp.data) {
          let respData = this.shopHome === "ebook" ? resp.data.ebook : resp.data.audiobook;
          if(!respData) { // Remove this check once NOOKCLOUD-5665 goes to prod
            respData = resp.data.favorites;
          }
          localStorage.setItem(this.selectedFavKey, JSON.stringify(respData));
          this.setState({ data: respData });
        }
    });
  }

    openFavCatWindow() {
        if (window.LOCALHOST) {
            this.editFavCategories();
        } else {
            window.Device.editFavCategories();
        }
    }

    editFavCategories = () => {
        let hash = window.location.hash.split('/');
        hash.pop();
        window.location.hash = `${hash.join('/')}/favCategories`;
    }

    renderFavorite(category, i) {
        let drefId = category.id;
        let linkPath = '';
        const shopPlatform = window.Device.shopPlatform;
        let categoryTitle = this.state.categoryName[drefId] || category.label;
        const categoryImg = this.state.categoryImage && this.state.categoryImage[drefId];
        if (drefId && drefId !== '+') {
            linkPath = `/${shopPlatform}/home/categories/nkbk/${drefId}/list`;
        }

        let eachImage = <img src={ categoryImg } className="category-icon" alt={ categoryImg && categoryTitle } />;
        return (
          <div key={i} className="fav-category">
            <div className="img-holder">
                <Link key={drefId} to={ linkPath }>{eachImage}</Link>
            </div>
            <div className={ 'category-name ellipses ' }>
                { categoryTitle }
            </div>
          </div>
        );
    }

    render () {
        let favCategories = this.props.isFetchingFavCategories ? this.dummyFavCats : this.state.data;
        let categoryImage = [];
        let categoryName = [];
        this.state.categoryImage = [];

        if (favCategories && favCategories.length > 0) {
            for (let index = 0; index < favCategories.length; index++) {
                let cat = favCategories[index];

                const getFileName = (arg, catName) => {
                    try {
                        let images = require.context('../../../public/images/categories_icons_medium', true);
                        let imgsrc = images(`./${arg}`);
                        categoryImage[cat.id] = imgsrc;
                        categoryName[cat.id] = catName;
                    }
                    catch (error) {
                        categoryImage[cat.id] = defaultBook;
                    }
                    this.state.categoryImage = categoryImage
                    this.state.categoryName = categoryName
                }

                getImageName(cat, getFileName);
            }
        }
        const editFavButton = <div className="fav-category">
            <div className="img-holder">
                <div className="category-icon edit-fav-button"
                    alt={textReplace(KEY.edit_fav_categories)}
                    onClick={this.openFavCatWindow}></div>
            </div>
            <div className="category-name edit-fav-text ellipses">
                {constants.EDIT_FAVORITES_BUTTON_TEXT}
            </div></div>;
        if (!this.props.isFetchingFavCategories && favCategories && !favCategories.length) {
            return (
                <React.Fragment>
                    <div className="explore-fav-container">
                        <NoFavoriteCategories isSelectedFavFetchedFromNative={this.props.isSelectedFavFetchedFromNative} editFavCategories={ this.openFavCatWindow }  childProfile={this.props.childProfile}/>
                    </div>
                </React.Fragment>
            )
        } else {
            return (
                <React.Fragment>
                    <div className={'explore-fav-container ' +
                     (this.props.isFetchingFavCategories && !this.isNoSelectionDone ? 'loading ' : '')
                        + (favCategories && favCategories.length > 0 ? 'has-favorites' : '')}>
                        <div className="title">{ constants.EXPLORE_FAVORITES_TITLE }</div>
                        <div className="fav-cat-container">
                            {this.state.data && this.state.data.length > 0 && this.state.data.map(this.renderFavorite, this)}
                            {editFavButton}
                        </div>
                        {this.isEPD && <div className='edit-favorites' onClick={this.openFavCatWindow}> {constants.EDIT_FAVORITES_BUTTON_TEXT.toUpperCase()}
                            <img className="right-arrow" src={rightArrow} alt={textReplace(KEY.book_icon)} />
                        </div>}
                    </div>
                </React.Fragment>
            )
        }

    }
}

export default ExploreFavCategories;


