import React, { Component } from 'react'
import epdGridIcon from '../../images/grid-icons/grid.svg'
import epdListIcon from '../../images/list-icons/list.svg'
import gridIcon from '../../images/grid-icons/grid@3x.png'
import listIcon from '../../images/list-icons/list@3x.png'
import '../../style-dist/components/CustomDropdown.css'

import platforms from '../lib/constants/platforms'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'
if (window.location.href.match(platforms.epd)) {
  require('../../style-dist/components/CustomDropdown.epd.css');
  require('../../style-dist/loading.epd.css');
}
if (window.location.href.match(platforms.quill)) {
  require('../../style-dist/components/CustomDropdown.quill.css');
  require('../../style-dist/loading.epd.css');
}


class CustomDropdown extends Component {

  constructor(props) {
    super(props);

    this.showHTMLMenu = window.location.href.match('localhost') != null || !window.Device.canShowMenu();
    this.showHTMLMenuAud = window.location.href.match('localhost');
    this.isEpd = window.location.href.match(platforms.epd);
    this.isQuill= window.location.href.match(platforms.quill);
    this.isEPD = this.isEpd || this.isQuill;
    this.isCategoryList = window.location.hash.match(/categories.*\/list$/);
    this.state = {
      showList: (performance.navigation.type === 1  && sessionStorage.getItem("showList") !== 'list') ? true :  false, // Control the loading animation
    }

    // Bind Methods
    this.renderOption = this.renderOption.bind(this);
    this.renderValueSelected = this.renderValueSelected.bind(this);
    this.openSortMenu = this.openSortMenu.bind(this);
    this.closePopup = this.closePopup.bind(this);
    this.toggleList = this.toggleList.bind(this);

    // Global sort handler
    window.handleSortChange = value => this.props.onChange({
      target: { value }
    });
    window.handleGridChange = value => this.props.onChangeGrid({
      target: { value }
    });
  }


  renderOption(option, i) {
    const selected = this.props.value === option.props.value;
    if (option.props.value === undefined) {
      return '';
    }

    return <li key={i}
      className={ 'item' + (selected ? ' selected' : '') }
      onClick={ () => {
        this.closePopup();
        this.props.onChange({
          target: { value: option.props.value }
        });
      }}>
        <span>{ option.props.children }</span>
    </li>;
  }

  renderValueSelected() {
    const chosenOption = this.props.ebookOptions.find( c => {
      return c.props.value === this.props.value
    });
    return chosenOption !== undefined ? chosenOption.props.children : 'default';
  }


  openSortMenu(evt) {
    const appVersion = window.APP_VERSION;
    let isAppLower = false;
    const pixelRatio = window.devicePixelRatio || 1;
    const rect = evt.target.getBoundingClientRect();
    let topLeftX = rect.x * pixelRatio;
    let topLeftY = rect.y * pixelRatio;
    let bottomRightX = topLeftX + rect.width * pixelRatio;
    let bottomRightY = topLeftY + rect.height * pixelRatio;

    if (
      (appVersion < "5.2.5" &&
        window.Device.shopPlatform === platforms.ios) ||
      (appVersion < "5.0.4" && window.Device.shopPlatform !== platforms.ios)
    ) {
      isAppLower = true;
    } else if (
      window.Device.isLegacyAndroid() &&
      window.Device.shopPlatform === platforms.android
    ) {
      isAppLower = true;
    }
    let hasTopList = this.props.hasTopList;
    console.log(`hasTopList: ${hasTopList}`);
    const topX = (this.isQuill || this.isEpd) ? (this.isQuill ? 90 : 60) :
      (isAppLower ? evt.clientX : topLeftX.toFixed());
    const topY = (this.isQuill || this.isEpd) ? (this.isQuill ? (hasTopList ? 115 : 60) : (hasTopList ? 15 : 15)) :
      (isAppLower ? evt.clientY : topLeftY.toFixed());

    const bottomX = (this.isQuill || this.isEpd) ? (this.isQuill ? 210 : 125) :
      (isAppLower ? evt.clientX : bottomRightX.toFixed());
    const bottomY = (this.isQuill || this.isEpd) ? (this.isQuill ? (hasTopList ? 95 : 50) : (hasTopList ? 30 : 30)) :
      (isAppLower ? evt.clientY : bottomRightY.toFixed());

      // reset epd pageNo on native sort.
      window.resetPageNo = true; 
      window.categoryPageReset = true;

    if (window.paginationComponent && this.isEPD) {
      let currentPage = window.paginationComponent.getPageNo();
      if(!this.isCategoryList && currentPage) {
        window.paginationComponent.gotoPage(!this.isCategoryList ? 1 : currentPage);
      }
    }


    // Show the native sort menu
    if (!this.showHTMLMenu) {
      console.log(`Native-showHTMLMenu:topX:${topX}, topY:${topY}, bottomX:${bottomX}, bottomY:${bottomY}`);
      const menuItemValue = this.props.dictionary.find(d => d.value === this.props.value);
      const menuItemLabel = menuItemValue && menuItemValue.label;
      console.log(menuItemValue);
      return window.Device.showMenu(
        this.props.dictionary,
        topX,
        topY,
        bottomX,
        bottomY,
        textReplace(KEY.sort_by),
        menuItemLabel,
        'handleSortChange');
    }

    // Show HTML sort menu for debug
    else {
    console.log(`customDropDown: sortMenu - topX:${topX} topY:${topY} bottomX:${bottomX} bottomY:${bottomY}`)

      if (!this.props.showSort) {
        // document.documentElement.classList.add('no-scroll');
        // document.body.classList.add('no-scroll');
      }

      if (this.isPhone()) {
        // Update the native header
        console.log('isPhone');
        window.Device.modifyHeader({
          enableBack: true,
          title: textReplace(KEY.sort_list_by),
          background: 'E2E7F2',
          hideTools: true
        });
      }

      window.location.href += '/sort';
    }
  }

  toggleList() {
    this.setState({showList:this.state.showList === false ? true : false});
    this.props.onChangeGrid({
      target: { value:this.state.showList === false ? 'grid' : 'list' }
    });
  }

  closePopup(e) {
    window.history.back();
  }


  isPhone() {
    return window.Device.getMinDimension() < 500;
  }


  renderHTMLDropdown(isPhone) {
    return <div className="dropdown-wrapper">
      { this.props.showSort && !isPhone &&  <div className="smoke-screen" onClick={ this.closePopup } /> }

      <ul className={ 'dropdown-menu' + (this.props.showSort ? ' show' : '') }>
        { !isPhone && <p className="title">{textReplace(KEY.sort_list_by)}</p> }
        { this.props.children.map(this.renderOption) }
      </ul>
    </div>
  }


  render() {
    const isPhone = this.isPhone();
    const isEPD = this.isQuill || this.isEpd;
    const title = this.props.title || textReplace(KEY.sorted_by);
    const titleSuffix = <span className="count">{ this.props.suffix }</span>;
    const showIcon = this.state.showList === false ? listIcon : gridIcon;

    return <div className={ 'custom-dropdown' + (isPhone ? ' phone' : ' tablet') }>
      <label>
        <div>
          {  
            (
              <div className={'chosen-item ' + (isEPD ? (this.props.showGrid ? 'list' : 'grid') : '')} >
                <span className="ellipses"> 
                  <span className="sort-title named-sort">{ title }</span>
                  <span role="link" onClick={ this.openSortMenu } className="sort-type">
                    &nbsp; { this.renderValueSelected() }
                  </span>
                  { titleSuffix }
                </span>
                { isEPD ? <React.Fragment>
                            <div className="view-container">
                              <div className="img-holder">
                                <img src={ epdGridIcon } alt={textReplace(KEY.grid_icon)}
                                  className={'grid-icon ' + 
                                  (this.state.showList === false ? 'on' : 'off')} onClick={ () => this.state.showList === false ? '' : this.toggleList() }></img>
                                <img src={ epdListIcon } alt={textReplace(KEY.list_icon)}
                                  className={'list-icon ' + (this.state.showList === true ? 'on' : 'off')} onClick={ () => this.state.showList === false ? this.toggleList() : '' }></img>
                              </div>
                            </div>
                          </React.Fragment>
                                 :
                          <img src={ showIcon } alt={textReplace(KEY.list_icon)} className="list-icon" 
                                onClick={ () => this.toggleList() }></img>
                  }
              </div>
            )
          }
        </div>
      </label>
      
      { /* Show and HTML sort menu when debuggin */ }
      { this.showHTMLMenu && this.renderHTMLDropdown(isPhone) }
    </div>;
  }
}

export default CustomDropdown;



