import React, { Component } from 'react';
import '../../style-dist/components/CategoryLink.css'

import platforms from '../lib/constants/platforms'
import rightArrow from '../../images/right_arrow_black.svg'
import defaultBook from '../../../public/images/categories_icons_medium/default.svg'
import unCheckedBox from '../../images/static/icons/uncheck_fav_lcd.svg'
import { getImageName, addEllipsis } from '../lib/utils'
import textReplace from '../lib/i18n';
import KEY from '../lib/constants/languages/keys';

if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/CategoryLink.epd.css')
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/CategoryLink.quill.css')

class CategoryLink extends Component {

  constructor(props) {
    super(props);

    // Define state variables
    this.state = {
      isCategorySelected: false,
      categoryName: '',
      id:''
    }
    this.categoryLinks = React.createRef();
    this.isGridView = this.props.showCategoriesList;
    // Bind methods
    this.goToCat = this.goToCat.bind(this);
    this.selectCategory = this.selectCategory.bind(this);
  }

  goToCat() {
    window.categoryPageReset = false;
    const storeCode = this.props.storeCode;
    const dref = this.props.dref || '';
    let hash = window.location.hash;
    hash = hash.replace(/categories.*$/, `categories/${storeCode}/${dref}`);
    if (this.props.showList) {
      hash += '/list';
    }
    hash = hash.substr(1); // Remove the # symbol
    window.location.hash = hash;

    if (this.props.onClick) {
      this.props.onClick();
    }
  }

  componentDidMount() {
    this.setImage(this.props.cat);
    if(this.isGridView) {
      window.addEventListener('resize', this.handleEllipsesForCategoryLinks(this.categoryLinks));
      this.handleEllipsesForCategoryLinks(this.categoryLinks);
    }
  }

  handleEllipsesForCategoryLinks(categoryLink) {
    if (categoryLink && categoryLink.current)
      addEllipsis(categoryLink.current);
  }

  selectCategory(event) {
    if (this.props.addOrRemoveItem) {
      this.setState({isCategorySelected: event.target.checked, categoryName: event.target.value, id: event.target.id});
      this.props.addOrRemoveItem(event);
    }
  }

  render() {
    const DEBUG_CODE = this.props.dref ? this.props.dref : this.props.storeCode;
    const isFavCategoryPage = this.props.isInFavoriteCategoriesPage;
    const className = 'category-link' + (!this.props.dref ? ' store' : '') +
      (this.props.className ? ' ' + this.props.className : '');
    let categoryLabel = this.state.catName || this.props.name;
      
    return (
        <React.Fragment>
          { !isFavCategoryPage ? <div className={className}
                                           role={(this.props.className === 'store' && categoryLabel !== 'SEE ALL') ? "heading" : ""}
                                           aria-level={(this.props.className === 'store' && categoryLabel !== 'SEE ALL') ? "2" : ""}
                                           onClick={this.goToCat}
                                          >
            {(this.props.showIcon || this.props.showCategoriesList) &&
                <img className="categories-icon" src={this.state.categoryImage} alt={textReplace(KEY.categories_icon)} />}
                <div className="category-label">
                  <a  ref={ this.categoryLinks } className={this.props.isSeeMore ? ' seeMore' : this.props.className === 'l2-links' ? 'ellipses': ''} role="link">
                    { categoryLabel + (window.NOOK_DEBUG ? ' (' + DEBUG_CODE + ')' : '')}
                  </a> 
            </div>
            <img className="categories-icon-right" src={rightArrow} alt={textReplace(KEY.right_arrow)} />
            </div>
              :
            <div className={className + " " + 'fav-cat-section'} role={(this.props.className === 'store' && categoryLabel !== 'SEE ALL') ? "heading" : ""}
                 aria-level={(this.props.className === 'store' && categoryLabel !== 'SEE ALL') ? "2" : ""}>
              <img className="categories-icon" src={this.state.categoryImage} alt={textReplace(KEY.categories_icon)} />
              <div className={this.props.isSeeMore ? ' seeMore' : this.props.className === 'l2-links' ? 
              (isFavCategoryPage ? 'category-name ellipses' : 'ellipses') : ''} role="link">
                {categoryLabel + (window.NOOK_DEBUG ? ' (' + DEBUG_CODE + ')' : '')}
              </div>
              <input
                  type="image"
                  className="check-box-category"
                  alt={textReplace(KEY.submit)}
                  src={ unCheckedBox }
                  id={this.props.dref}
                  name={categoryLabel}
                  value={this.props.name}
                  onClick={ (e) => {this.selectCategory(e)} }
              />
            </div>
      }

        </React.Fragment>
    );
  }

  setImage(cat) {
    const getFileName = (arg, catName) => {
      try {
        const images = require.context('../../../public/images/categories_icons_medium', true);
        let imgsrc = images(`./${arg}`);
        this.setState({ categoryImage: imgsrc });
        this.setState({ catName: catName });
      }
      catch (error) {
        this.setState({ categoryImage: defaultBook });;
      }
    }
    getImageName(cat, getFileName, this.props.cats);
  }
}

export default CategoryLink;



