  JSNI.prototype.test_simulateDownload = function(ean) {
    var pct, update;
    pct = 0;
    update = (function(_this) {
      return function() {
        if (!(pct > 100)) {
          window.onLibraryItemDownloadProgress(ean, pct);
          pct += 1;
          return setTimeout(update, 30);
        } else {
          return window.onLibraryItemStateChange(ean, "DOWNLOAD_SUCCEEDED");
        }
      };
    })(this);
    window.onLibraryItemStateChange(ean, "DOWNLOADING");
    return setTimeout(update, 30);
  };

  JSNI.prototype.close = function() {
    if (confirm('App Closing (Browser reload)')) {
      return window.location.reload();
    }
    return window.Router.push(window.Router._default);
  };

  JSNI.prototype.reload = function() {
    return window.location.reload();
  };

  JSNI.prototype.reloadAsync = function(success, failure) {
    var handler;
    handler = (function(_this) {
      return function() {
        return window[success](_this.reload());
      };
    })(this);
    return setTimeout(handler, 100);
  };

  JSNI.prototype.showManageActionSheet = function(ean, x, y, src) {
    alert('Long Press: ' + ean + ', ' + x + ', ' + y + ', ' + src);
  };

  return JSNI;

