//
// F4 Lists
// 
export const fetchF4 = async (userParams, callback, options={}) => {
  const params = Object.assign({
    listCount: 6, // Chosen bt Maria Piquet
    // itemCount: 9 // TODO: make this dynamic based on device size??
  }, staticParams());
  if (options.childProfile) {
    params.storeCode = kidsStoreCode();
  }
  let paramString = `${paramsFromObj(params)}&${paramsFromObj(userParams)}`;
  const F4 = `${params.storeCode}_f4-data`;

  const getF6 = async (f4Data) => {
    if (options.cachedResponse) { //cache F4 data
      console.log(`Writing F4Data(${Buffer.byteLength(f4Data, 'utf8')} bytes) to ${F4}`);
      window.cache.set(F4, f4Data, 1);
    }

    await fetchF6(userParams, options, (err, resp) => {
      if (resp) {
        f4Data = arrangeF6inF4List(f4Data, resp);
      }
      callback(null, f4Data);
    });
  }

  const good = async (response) => {
    if (!response) {
      return callback('No F4 data received');
    }

    let f4Data = {
      lists: response.data.slice(0, 6),
      eans: response.eans,
      status: response.status.success
    }
    getF6(f4Data);

  };

  const bad = (err) => {
    console.error('F4 lists fetch failed');
    callback(true);
  };

  if (options.cachedResponse) {
    const cachedF4 = window.cache.get(F4);
    if (cachedF4) {
      console.log(`Fetching cached F4Data(${Buffer.byteLength(cachedF4, 'utf8')} bytes) from ${F4}`);
      getF6(cachedF4)
    }
  }

  return assemble('get', '/lists', paramString, null, good, bad);
}

