//
// List From Search
// 
export const fetchList = (userParams, callback, options={}) => {
  const params = Object.assign({}, staticParams());
  if (userParams.storeCode || userParams.store) {
    if (userParams.store && !userParams.storeCode) {
      userParams.storeCode = userParams.store;
      delete userParams.store;
    }
    delete params.storeCode;
  }

  let paramString = `${paramsFromObj(params)}&${paramsFromObj(userParams)}`;

  const good = (response) => {
    if (!response || !response.data || !response.data.length) {
      return callback('No list returned');
    }

    callback(null, {
      listId: response.context.params.category_id,
      listData: response.data[0],
      eans: response.eans
    });
  };

  const bad = (err) => {
    console.error('List fetch failed');
    callback(true);
  };

  return assemble('get', '/search/a10', paramString, null, good, bad);
}


//
// List Details (title & description)
// 
export const fetchListDetails = (userParams, callback) => {

  if (!window.SHOW_AUDIOBOOKS) {
    const params = Object.assign({}, staticParams());
    userParams.storeCode = params.storeCode;
  }
  let paramString = `${paramsFromObj(userParams)}`;

  const good = response => {
    if (!response || !response.data) {
      return callback('No list details in response');
    }

    callback(null, {
      total: response.data[0] ? response.data[0].total : '0',
      title: response.data[0] ? response.data[0].title : 'No Results Found',
      description: response.data[0]? response.data[0].description : '',
      listFormatType: response.data[0]? response.data[0].listFormatType : ''
    });
  };

  const bad = err => {
    console.error('List fetch failed');
    callback(true);
  };

  return assemble('get', '/listDetails', paramString, null, good, bad);
}


//
// Store (department) Categories
// 
// Store Categories can be cached for 12 hours since they rarely change.
//

export const fetchStoreCategories = async (storeCode, userParams, callback) => {

  const params = {
    storeCode: storeCode,
    retailer: 'BN2',
    context_locale: 'en_US',
    includeEanDetails: true,
    profileType:"both"
  }
  let paramString = `${paramsFromObj(params)}&${paramsFromObj(userParams)}`;
  const cacheKey = 'categories';
  const good = (response) => {
    if (!response || !response.data) {
      return callback('No Store Categories received.');
    }

    const catData = {
      data: response.data
    };

    callback(null, catData);
    if (window.LOCALHOST) {
      window.db.setCategoriesToNative(cacheKey, catData);
      window.cache.set(`categories_expiry` , {expiry: hrsUntilMidnight()}, hrsUntilMidnight(), true);
    }

  };

  const bad = (err) => {
    console.error('Category fetch failed', err);
    callback(err);
  };

  const dbsuccess = (value) => {
    if (value) {
      console.log(`fetched from native-fetchStoreCategories`);
      return callback(null, typeof value === 'object' ? value : JSON.parse(value));
    }else {
      console.log(`fetched from cloud-fetchStoreCategories`);
      return assemble('get', '/categories/all', paramString, null, good, bad);
    }
  }

  const dbfailed = () => {
    console.log(`Error/dbfailed: Unable to fetch categories from native db`);
    return assemble('get', '/categories/all', paramString, null, good, bad);
  }

  window.db.getCategoriesFromNative(cacheKey, dbsuccess, dbfailed);

  if (window.LOCALHOST) {
    const cached = window.cache.get(`categories_expiry`, true);
    if (cached) {
      window.db.getCategoriesFromNative(cacheKey, dbsuccess, dbfailed);
    } else {
      return assemble('get', '/categories/all', paramString, null, good, bad);
    }
  }
}


// 
// Sub Categories
// 
// Sub-categories can be cached for 12 hours since they rarely change. Only store
// them if the options.cacheResponse boolean is true.
// 
export const fetchSubCategories = (storeCode, dref, userParams, callback, options={}, retry=true) => {
  const cacheKey = `${storeCode}_categories-${dref}` ;
  const params1 = Object.assign({}, staticParams());
  const params = {
    refinementType: getRefinementType(storeCode, params1.storeCode),
    refinementId: dref,
    retailer: 'BN2',
    context_locale: 'en_US',
    includeParents: true,
    includeEanDetails: true
  }
  let paramString = `${paramsFromObj(params)}&${paramsFromObj(userParams)}`;

  const good = (response) => {
    if (!response) {
      return callback('No Store Categories received.');
    }

    const catData = {
      refinements: response.refinements,
      parent: response.parent,
      spots: response.spots,
      eans: response.eans
    };

    callback(null, catData);

    if (options.cacheResponse) {
      window.cache.set(cacheKey, catData, 12);
    }
  };

  const bad = (err) => {
    console.error('Sub Category fetch failed', err);

    if (retry) {
      fetchSubCategories(storeCode, dref, userParams, callback, options, false);
    }
    else {
      callback(err);
    }
  };

  const cached = window.cache.get(cacheKey);
  if (cached) {
    return callback(null, cached);
  }

  return assemble('get', '/shop/category/a10', paramString, null, good, bad);
}


//
// eanDetails are not returned by this API. Can only render the book cover!!
// 
export const fetchRecommendByRecentEan = (userParams, callback) => {

  const doFetch = (recentItems) => {
    const eans = Object.keys(recentItems);
    const randomEan = eans[Math.floor(Math.random()*eans.length)];

    const params = Object.assign(userParams, {
      requesttype: 'byEan',
      recprodrestrict: 1,
      eans: randomEan
    });
    let paramString = paramsFromObj(params);

    const good = (response) => {
      let recommended = {};
      if (response.RecommendationResult && response.RecommendationResult.recommendedEans) {
        recommended.eans = response.RecommendationResult.recommendedEans.ean || [];
      }

      callback(null, recommended);
    };

    const bad = (err) => {
      console.error('Recommend by EAN fetch failed');
      callback(true);
    };

    return assemble('get', '/proxy/recommendByAlgorithm', paramString, null, good, bad);
  }

  const error = () => {
    console.error('Error getting recentlyOpened.');
  }


  // We need to pass the user's recent library items to this call
  window.Device.getRecentLibraryItems(6, doFetch, error);
}


//
// Config API returns the device ID based on app version and device model name
//
export const fetchConfig = (appVersion, modelName, callback) => {
  const params = {
    appVersion,
    modelId: modelName,
  };
  const paramString = paramsFromObj(params);
  return assemble('get', '/config', paramString, null,
  response => {
    if (response && response.productDeviceId) {
      callback(null, response);
    }
    else {
      console.error('Invalid config response', response);
      callback(true);
    }
  },
  err => {
    console.error('Error fetching config', err);
    callback(true);
  });
}

export const postFavoriteCategoriesNative = async (data, callback) => {
  const successCallback = (value) => {
    console.log(`postFavoriteCategoriesNative:${value}`);
    return callback(null, value);
  }

  const failureCallback = (err) => {
    console.log(`postFavoriteCategoriesNative:${err}`);
    return callback(err);
  }
  //Do not change as android is expecting selectedFavCategories as input but iOS is not.
  if (window.location.hash.match(platforms.ios)) {
    window.Device.closeFavCatModal(data, successCallback, failureCallback);
  } else {
    window.Device.closeFavCatModal({ selectedFavCategories: data }, successCallback, failureCallback);
  }
}

export const favoriteCategories = async (httpMethod, userParams, data, callback) => {
  const {accountId, profileId} = userParams;
  const params = {
    accountId: accountId,
    profileId: profileId,
    shopHome: userParams.shopHome
  }

  let paramString = `${paramsFromObj(params)}`;

  const good = (response) => {
    if (!response) {
      return callback('No Favorite Categories found.');
    }

    let favCategoriesData = {
      data: httpMethod === 'get' ? response.data : response.status.success
    }

    callback(null, favCategoriesData);
  };

  const bad = (err) => {
    console.error('Favorite Categories fetch failed', err);
    callback(err);
  };
  
  return assemble(httpMethod, '/categories/favorite', paramString, data, good, bad);
}

//
// Helper methods
//
function paramsFromObj(obj) {
  let params = '';
  for (let key in obj) {
    // Required for JSlint
    if (!obj.hasOwnProperty(key)) {
      continue;
    }
    params += `${key}=${obj[key]}&`;
  }
  return params.slice(0, -1);
}



