import { connect } from 'react-redux'
import ListCover from '../components/ListCover'


const mapStateToProps = (state, ownProps) => {
  return {
    inStore: state.shellData.isInStore
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {}
}


const ListCoverContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(ListCover)

export default ListCoverContainer



