import JsniAsync from './JsniAsync';

//
// This class should be instantiated as a global (window.Device).
// We currently use $JSNI for all native requests that are supported by both
// Android (synchronous API) and iOS (synchronous).
// 
// We make calls directly to window.JSNI for methods only supported by Android.
//
class Device {

  constructor(shopPlatform) {
    this.shopPlatform = shopPlatform;
    this.isEpdHome = window.location.hash.match('device-home') ? true : false;
    this.library = {};
    this.purchases = {};
    this.getRecentLibraryItemsCounter = 0;
    this.getLibraryItemsCounter = 0;
    // Instantiate the Async JSNI class (iOS WKWebview requires async)
    let jsniAsync = new JsniAsync();
    if (window.location.hash.match(/ios/)) {
      jsniAsync.async_calling_enabled(true);
    }
    this.$JSNI = jsniAsync.build$JSNI();


    var _this = this;
    // These may still be called by the native app but we don't use them in Shop
    // because we don't have purchase buttons that need to show state.
    window.onLibraryStateChange = function () {
      let _this = this;

      var good = function (items) {
        console.log('LIBRARY STATE CHANGED');
      };
      var bad = function (err) {
        console.warn('Error updating library state - ', err);
      };
      if (_this && _this.Device) {
        _this.Device.getLibraryItems(good, bad);
      }
    }

    window.onLibraryItemStateChange = function(ean, state) {
      if (_this.Device) {
        _this.Device.onLibraryItemStateChange(ean, state);
      }
    }

    window.onLibraryItemDownloadProgress = function(ean, pct) {
      if (_this.Device) {
        _this.Device.onLibraryItemDownloadProgress(ean, pct);
      }
    }

  }


  //
  // Native Device and Customer data
  // 
  getSessionInfo(good, bad) {
    // this.$JSNI.getSessionInfo().then(good, bad);

    if (this.shopPlatform === 'ios') {
      const successCallback = 'getSessionInfoGood';
      const failureCallback = 'getSessionInfoBad';

      window[successCallback] = good;
      window[failureCallback] = bad;

      window.webkit.messageHandlers.getSessionInfoAsync.postMessage({
        successCallback,
        failureCallback
      });
    }
    else {
      let info = window.JSNI.getSessionInfo();
      try {
        info = JSON.parse(info);
      } catch (err) {
        console.error('Bad JSNI data');
        return bad(err);
      }
      good(info);
    }
  }


  getAndroidVersion(ua) {
    if (this.androidVersion === undefined) {
      ua = (ua || window.navigator.userAgent).toLowerCase();
      var match = ua.match(/android\s([0-9\.]*)/);
      this.androidVersion = match ? parseFloat(match[1]) : false;
    }
    return this.androidVersion;
  }


  isLegacyAndroid() {
    var version = this.getAndroidVersion();
    return version && ((this.androidVersion <= 4.4 || this.androidVersion === 6.0 || this.androidVersion === 5.1)  ? true : false);
  }


  //
  // LOCALYTICS
  // 
  tagScreen(location) {
    this.$JSNI.tagScreen(location);
  }


  tagEvent(eventData) {
    this.$JSNI.tagEvent(eventData);
  }


  //
  // NETWORKING
  //

  reload() {
    this.$JSNI.reload();
  }


  isConnected(cb) {

    // THIS IS FOR THE window.nook API, NOT JSNI!!!

    
    this.$JSNI.isConnected().then(cb);
  }

  isSelectedFavInSession(cb) {
    this.$JSNI.isSelectedFavInSession().then(cb);
  }

  isOrientationLandscape(cb) {
    this.$JSNI.isOrientationLandscape().then(cb);
  }

  showGetConnected(options) {
    if (options && options.epdHomeOffline) {
      window.epdHomeOffline(options.epdHomeOffline);
    }
    else {
      this.$JSNI.showGetConnected();
    }
  }


  //
  // MISCELLANEOUS
  //
  showManageActionSheet(ean, x, y, imageUrl) {
    if (!this.$JSNI.showManageActionSheet) {
      return;
    }
    this.$JSNI.showManageActionSheet({ 
      ean: ean.toString(),
      x: x,
      y: y,
      imageUrl: imageUrl
    });
    console.log('Event: longPress')
    console.log('EAN:', ean)
  }


  canShowMenu() {
    if (window.Device.shopPlatform === 'ios') {
      const appVersion = window.APP_VERSION;
      if (appVersion >= '5.2.5') {
        return !!window.webkit.messageHandlers.showMenu2Async;
      } else {
        return !!window.webkit.messageHandlers.showMenuAsync;
      }
      
    }
    else {
      return !!window.JSNI.showMenu;
    }
  }


  showMenu(dictionary, topX, topY, bottomX, bottomY, title, label, successCallback) {
    if (!this.$JSNI.showMenu) {
      return;
    }

    // The function signature changed in 5.0.4
    const appVersion = window.APP_VERSION;
    if ((appVersion >= '5.0.4' && window.Device.shopPlatform !== 'ios') ||
        (appVersion >= '5.2.5' && window.Device.shopPlatform === 'ios') || 
        (!this.isLegacyAndroid() && !this.shopPlatform === 'android')) {
      this.$JSNI.showMenu2({
        dictionary,
        topX,
        topY,
        bottomX,
        bottomY,
        title,
        label,
        successCallback
      });
    }

    else {
      this.$JSNI.showMenu({
        dictionary,
        x: topX, // Click X
        y: topY, // Click Y
        title,
        label,
        successCallback
      });
    }
  }


  //
  // WEBVIEW
  // 
  getBrowserVersion() {
    var ua = window.navigator.userAgent;
    var raw = ua.match(/Chrom(e|ium)\/([0-9]+)\./);
    var iOS = !!ua.match(/iPad/i) || !!ua.match(/iPhone/i);
    if (raw)
      return parseInt(raw[2], 10); // Chrome (android)
    else if (iOS) 
      return ua.split(' ').pop(); // Safari (iOS)
    return false;
  }


  //
  // ACCOUNT
  //
  // getGiftCardInfo(success, fail) {
  //   if window.JSNI.getGiftCardInfo {
  //     window.GGCB_success = function(data){ success(data) };
  //     window.GGCB_fail = function(e) { fail(e) };
  //     try {
  //       window.JSNI.getGiftCardInfo("GGCB_success", "GGCB_fail")
  //     }
  //     catch (e) {
  //       fail(e);
  //     }
  //   }
  //   else {
  //     fail();
  //   }
  // }
    

  //
  // LIBRARY
  //
  launchLibrary() {
    console.log("DEVICE: Launch library");
    window.JSNI.launchLibrary();
  }


  getCurrentlyReading(success, fail) {
    var _this = this;
    if (window.JSNI.getCurrentlyReadingItem) {
      window.GCRI_success = function(item) { 
        // keep track of the library items for later use
        _this.library[item.ean] = item;
        success(item);
      }
      window.GCRI_fail = function(e) { fail(e) };
      try {
        window.JSNI.getCurrentlyReadingItem("GCRI_success", "GCRI_fail");
      }
      catch (e) {
        fail(e);
      }
    }
    else {
      success(null);
    }
  }
    

  getRecentLibraryItems(cnt, success, fail) {
    var _this = this;
    
    this.getRecentLibraryItemsCounter++;
    var sname = "GRLI_success" + this.getRecentLibraryItemsCounter;
    var fname = "GRLI_fail" + this.getRecentLibraryItemsCounter;
    
    window[sname] = function(resp) {

      const items = {};
      Object.keys(resp).forEach(ean => {
        const item = resp[ean]

        // If we're parsing the legacy (array of objects) format of this response, "ean" var is an index and
        // the ean value is a property on the object
        ean = ean.length === 13 ? ean : item.ean || '';

        // keep track of the library items for later use
        _this.library[ean] = item;

        // Remove EANs from side-loaded content. These will be big 64 bit hex strings
        // e.x. 478f0d393e6c3777f10531484abe14451be07e2edab8b5087a5209d7d7d663d2
        if (ean.match(/\d{13}/)) {
          items[ean] = item;
        }
      });

      success(items);
      window[sname] = null;
      window[fname] = null;
    }

    window[fname] = function(e) { 
      fail(e);
      window[sname] = null;
      window[fname] = null;
    }
    try {
      this.$JSNI.getRecentLibraryItems({
        count: cnt, 
        successCallback: sname,
        failureCallback: fname
      });
    }
    catch (e) {
      console.error(e);
      fail(e);
    }
  }
    

  getLibraryItems(eans, success, fail) {
    if (!this.$JSNI.getLibraryItems) {
      return console.error('ERROR: JSNI.getLibraryItems not found!!');
    }
    this.getLibraryItemsCounter++;
    var sname = "GLI_success" + this.getLibraryItemsCounter;
    var fname = "GLI_fail" + this.getLibraryItemsCounter;
    
    window[sname] = function(items) {
      success(items);
      window[sname] = null;
      window[fname] = null;
    }

    window[fname] = function(e) { 
      fail(e);
      window[sname] = null;
      window[fname] = null;
    }

    try {
      this.$JSNI.getLibraryItems({
        eans,
        successCallback: sname,
        failureCallback: fname
      });
    }
    catch (e) {
      fail(e);
    }
  }


  itemIsInLibrary(ean) {
    return this.library[ean] !== undefined;
  }
    

  itemIsDownloaded(ean) {
    var data = this.library[ean]
    return data.filePath ? true : false;
  }


  openLibraryItem(ean) {
    if (this.library[ean]) {
      var item = this.library[ean];
      if (!this.itemIsVideo(ean)) {
        window.JSNI.openItem(JSON.stringify(item));
      }
      else {
        this.showProductDetails(item.title, ean);
      }
    }
  }


  downloadLibraryItem(ean) {
    if (this.library[ean]) {
      window.JSNI.downloadItem(JSON.stringify(this.library[ean]));
    }
  }


  //
  // SHOP
  //
  // launchShop() {
  //   console.log("DEVICE: Launch shop");
  //   window.JSNI.launchShop();
  // }


  // Show the native PDP for a given EAN
  showProductDetails(title, ean) {

    // THIS IS FOR THE window.nook API, NOT JSNI!!! 


    this.$JSNI.openProductDetails({
      ean: ean.toString()
    });
    console.log("Event : toPDP");
    console.log("Target: " + title);
    console.log("EAN   : " + ean);
  }


  // Open multiple items in a PDP carousel, starting with a given item
  showProductDetailCarousel(title, eans, pos) {
    this.$JSNI.openProductDetails({
      ean: eans.split(',')[Number(pos)].toString()
    });
    console.log("Event : toPDP");
    console.log("Target: " + title);
    console.log("EANS  : " + eans);
    console.log("Index : " + pos);
  }


  showListContents(title, url) {
    this._search(title, url);
  }


  search(title, url) {
    this._search(title, url);
  }


  _search(title, url) {
    window.Nook.openProductSearch(url, title, true, false, true, false);
    console.log("Event : toSearchResults");
    console.log("Target: " + title);
    console.log("URL  : " + url);
  }
  

  launchUrl(url) {
    console.log("LAUNCH:", url);
    //if (window.session.getEnv() === 'dev') {
    if (window.SHOP_ENV === 'dev') {
      if (!confirm("Open link destination?")) {
        return;
      }
      url = url.replace("http://www.nook.com/app", window.location.origin + "/shop.html?goto=");
      window.open(url, "shopTab");
    }
    //window.Nook.openUrl(url);
    this.$JSNI.launchUrl(url);
  }

  
  startYourSubscription() {
    console.log('Start subscription JSNI-----------');
    this.$JSNI.startYourSubscription();
  }

  launchBrowseAudiobooks() {
    console.log('Launch Browse Audiobooks JSNI-----------');
    this.$JSNI.launchBrowseAudiobooks();
  }

  editFavCategories() {
    this.$JSNI.editFavCategories();
  }

  openSubcategoriesModal(categoriesRelativePath) {
      this.$JSNI.openSubcategoriesModal(categoriesRelativePath);
  }

  closeModalGotoShop(categoriesRelativePath) {
      this.$JSNI.closeModalGotoShop(categoriesRelativePath);
  }

  closeSubCategoriesModal() {
    this.$JSNI.closeSubCategoriesModal();
  }

  closeFavCatModal(selectedFavCategories, successCallback, failureCallback) {
      if (!window.LOCALHOST) {
        try {
          if (this.shopPlatform === 'ios') {
            const successCallback = 'savedFavoritesGood';
            const failureCallback = 'savedFavoritesBad';
      
            window[successCallback] = successCallback;
            window[failureCallback] = failureCallback;
      
            return window.webkit.messageHandlers.closeFavCatModalAsync.postMessage({
              selectedFavCategories,
              successCallback,
              failureCallback
            });
          } else {
            this.$JSNI.closeFavCatModal(selectedFavCategories, successCallback, failureCallback);
          }
        } catch (err) {
            console.error(`Error in Saving favorite Categories:${err}`);
            failureCallback(err);
        }
      } else {
        this.$JSNI.closeFavCatModal(selectedFavCategories, successCallback, failureCallback);
      }
  }

  closeSubscriptionScreen() {
    this.$JSNI.closeSubscriptionScreen();
  }
  
  setCategories(param) {
    this.$JSNI.setCategories(param);
  }
  
  //
  // Native UI
  //
  modifyHeader(options) {
    // PARAMS
    // {
    //   enableBack: true|false,
    //   title: "",
    //   background: "", // HEX
    //   hideTools: true|false
    //   hideBottomNav: true|false
    // }

    // Set defaults (enableBack is REQUIRED)
    if (options.title === undefined) options.title = "";
    if (options.background === undefined) options.background = "";
    if (options.hideTools === undefined) options.hideTools = false;
    if (options.hideBottomNav === undefined) options.hideBottomNav = false;

    if (this.shopPlatform === 'ios') {
      if (options.enableBack !== undefined) options.enableBack = options.enableBack.toString();
      if (options.hideTools !== undefined) options.hideTools = options.hideTools.toString();
      if (options.hideBottomNav !== undefined) options.hideBottomNav = options.hideBottomNav.toString();
    }
    this.$JSNI.modifyHeader(options);
  }

  // setUpEnabled(bool) {
  //   if (this.shopPlatform === 'ios') {
  //     bool = bool.toString();
  //   }
  //   this.$JSNI.setUpEnabled({enabled: bool});
  // }

  showToast(message) {

    // THIS IS FOR THE window.nook API, NOT JSNI!!!


    this.$JSNI.showToast(message);
  }

  showError(message = '') {
    if (this.shopPlatform === 'ios') {
      this.$JSNI.showError && this.$JSNI.showError({ message });
    }
    else {
      this.$JSNI.showError && this.$JSNI.showError(message);
    }
  }

  close() {
    this.$JSNI.close();
  }

  onLibraryItemStateChange(ean, state) {
    this.$JSNI.onLibraryItemStateChange(ean, state);
  }

  onLibraryItemDownloadProgress(ean, pct) {
    this.$JSNI.onLibraryItemDownloadProgress(ean, pct);
  }

  
  // 
  // Usage log is for tracking "high latency events". It is an empty method in Android
  // and is not implemented in iOS.
  // 
  // usageLog(event, data = {}) {
  //   data = JSON.stringify(data);
  //   window.Nook.usageLog("Shop", event, data);
  //   console.log(`USAGE LOG: ${event} -- ${data}`);
  // }
  

  getSize() {
    return Math.max(window.innerWidth, window.innerHeight);
  }


  getMinDimension() {
    return Math.min(window.innerWidth, window.innerHeight);
  }
  
  isGreaterOrEqualsAudioBookVersion () {
    let boolval = this.isNewerorSameVersion( '6.0', window.APP_VERSION)  && window.SHOW_AUDIOBOOKS;
    return boolval;
  }

  isNewerorSameVersion(oldVer, newVer) {
    const oldParts = oldVer.split('.')
    const newParts = (newVer || "").split('.')
    for (var i = 0; i < newParts.length; i++) {
      const a = ~~newParts[i] // parse int
      const b = ~~oldParts[i] // parse int
      if (a >= b) return true;
      if (a < b) return false;
    }
    return false;
  }
  
  getShopType(){
    return window.SHOW_AUDIOBOOKS ? "AUDIOBOOK" : "EBOOK";
  }

  //This is a temporary solution. Remove this logic and use the store name as is once audiobook drefs are ready.
  getStoreName(store) {
    return window.SHOW_AUDIOBOOKS ? (store.code === "kids" ? "Kids Audiobooks" : (store.code ===  "nkbk" ? "Audiobooks" : store.name)) : store.name;
  }

  getStoreNameForSubCategories(store) {
    return window.SHOW_AUDIOBOOKS ? (store.code === "kids" ? "Kids Audiobooks" : (store.code ===  "nkbk" ? "Audiobooks" : store.name)) : 
    (store.code === "kids" ? "Kids eBooks" : (store.code ===  "nkbk" ? "eBooks" : store.name));
  }
}

export default Device;



