/**
 * This component renders a Shop list.
 *
 * As the user scrolls to the bottom of a list, more content is automatically
 * fetched and rendered. This is called "demand loading".
 *
 * When a list is opened in this view it's possible we won't have the 
 * metadata (list title, description, etc.). This happens when we deep link
 * directly to this component (/:shopPlatform/list/listId). If we do not have 
 * these details, we will get them when we call this.props.loadListDetails for 
 * list total count.
 */
import React, { Component } from 'react'

import { fetchList, fetchListDetails, fetchF3 } from '../lib/fetch'
import { filterList, getTitle, getCanvasFontSize, getCssStyle, getTextWidth } from '../lib/utils'
import { screens, events, attributes } from '../lib/constants/analytics'
import analytics from '../classes/Analytics'
import ListCoverContainer from '../containers/ListCoverContainer'
import ListGridCoverContainer from '../containers/ListGridCoverContainer'
import ListSort from './ListSort'
import ListTnc from './ListTnc'
import platforms from '../lib/constants/platforms'
import Scroller from '../classes/Scroller'
import Spinner from './Spinner'
import storeCodes, { storeCodeMap } from '../lib/constants/storeCodes'
import { fetchF1} from '../lib/fetch'
import F3Links from './F3Links'
import '../../style-dist/components/List.css'
import ContainerFiller from "./ContainerFiller";
import KEY from '../lib/constants/languages/keys'
import textReplace from '../lib/i18n'
import AllCategories from '../../../public/images/UnionEPD.svg'

if (window.location.href.match(platforms.epd) || window.location.href.match(platforms.quill)) {
  require('../../style-dist/components/List.epd.css')
  require('../../style-dist/components/List.quill.css')
}

const LIST = 'list';
const GRID = 'grid';

class List extends Component {

  constructor(props) {
    super(props);
    // State for this component only, not shared with others
    this.state = {
      isFetchingList: false, // Control the loading animation
      isFetchingListDetails: false, // Control the loading animation
      isFetchingMore: false,
      inLibrary: {}, // EANs that are in the user's library. Used to show the proper label
      sortKey: this.props.defaultSort || '', // Sort order for the list
      audioFilter: this.props.defaultAudFilter ||
        (window.SHOW_AUDIOBOOKS ? (props.customer.childProfile ? "kidsdigaud" : "digaud")
          : "dig"), //Filter for audiobook
      showGrid: LIST
    }

    if (!sessionStorage.getItem('showList')) {
      sessionStorage.setItem("showList", "list");
    }
    this.isEpd = window.Device.shopPlatform === platforms.epd || window.Device.shopPlatform === platforms.quill;
    this.isiOS = window.Device.shopPlatform === platforms.ios;
    this.isCategories = !!window.location.href.match('categories');

    // Define a list of dummy EANs to show when loading
    this.loadingEans = [null, null, null, null, null, null, null, null, null, null, null, null];

    if (this.props.listId !== 'rec_from_ract_pod') {
      this.props.data.listData.eans = []; // to avoid displaying covers from previously fetched list.
    }

    // List title is shown in the native header when the user scrolls down
    this.titleInNative = false;

    // Demand loading state is kept here locally for convenience
    this.offset = 1;
    this.itemCount = 36;
    this.oldListLength = 0;
    this.allLoaded = false;
    this.isLoadingMore = false;

    // Values used for analytics
    this._demandLoadCount = 0;
    this._sortUsed = false;

    // Bind methods
    this.initAnalytics = this.initAnalytics.bind(this);
    this.handleSort = this.handleSort.bind(this);
    this.coverClicked = this.coverClicked.bind(this);
    this.demandLoad = this.demandLoad.bind(this);
    this.handleGrid = this.handleGrid.bind(this);
    this.handleFilter = this.handleFilter.bind(this);
    this.updateLibrary = this.updateLibrary.bind(this);
    this.isRecommendation = window.location.hash.match('rec_from_ract_pod');

    if (performance.navigation.type === 1) {
      let sortKey = localStorage.getItem((props.customer ? props.customer.profileId : "") + "-sortkey");

      // eslint-disable-next-line
      this.state.sortKey = sortKey ? sortKey : ''; //setstate didn't work, Not sure why. So adding eslint disable for this line.

      if (this.isRecommendation) {
        this.setRecomAfterRefresh = true;
      }
    }
    else {
      try {
        localStorage.setItem(props.customer.profileId + "-sortkey", this.state.sortKey);
      } catch (e) {
        console.log(e)
      }
    }
  }


  initAnalytics() {
    this.analyticsEvent = Object.assign({}, events.listViewed);
    this.analyticsEvent.data = Object.assign({}, events.listViewed.data);
  }


  componentWillMount() {

    // Show the native back button and no title
    if (!this.props.noModifyHeader) {
      if (this.isRecommendation) {
        window.Device.modifyHeader({
          title: textReplace(KEY.recommended_for_you),
          enableBack: true
        });
      }
      else {
        window.Device.modifyHeader({
          title: '',
          enableBack: true,
          hideTools: this.isCategories
        });
      }
    }

    // If the data has already been loaded, check for items in the library to show purchased badges
    if (this.props.data.listData && this.props.data.listData.eans.length) {
      window.Device.getLibraryItems(this.props.data.listData.eans,
          inLibrary => {
            this.setState({ inLibrary });
          },
          err => console.error('Error getting user\'s library items.', err)
      );
    }
    if(this.isEpd && window.paginationComponent)
    window.paginationComponent.gotoPage(1); // start from page 1.
  }

  componentDidMount() {

    
    // Open the list content
    this.openList();

    // Setup a scrolling handler to load more list items on scroll
    this.createDemandScroller();

    // Set up the scrolling handler that puts the 
    // title in the native bar and removes it. Only need to create once.
    if (!this.titleScroller) {
      this.createTitleScroller();
    }

    // Analytics
    if (!this.props.noAnalytics) {
      analytics.enter(window.SHOW_AUDIOBOOKS ? screens.audioBooks.shop_list : screens.ebooks.shop_list);
      this.initAnalytics();
    }

    // Always start at the top of the list
    if (!this.props.noAnalytics) {
      window.scroll(0, 0);
    }

    let interval = setInterval(() => {
      if (document.readyState === 'complete') {
        clearTimeout(interval);
        if (performance.navigation.type !== 1) {
          window.scroll(0, 10);
          window.scrollTo(0, 10);
        }
        setTimeout(() => {
          window.scroll(0, 0);
          window.scrollTo(0, 0);
        }, 5);
     
      }
    }, 2000);

    this.displayF3Links();
  }

  displayF3Links() {
    const customer = this.props.customer || {};
    const device = this.props.device || {};

    let urlParams = {
      countryCode: customer.country,
      accountId: customer.accountId,
      profileId: customer.profileId,
      productDeviceId: device.productDeviceId
    };

    this.setState({ isFetchingF3: true });
    fetchF3(urlParams, (err, resp) => {
      if (err) {
        console.error('Error fetching F3 data.');
        resp = { eans: [], links: [] };
      }
      this.setState({ isFetchingF3: false });

      const data = {
        data: resp,
        eanData: resp.eans
      };

      const affinityMap = {};
      data.data.links.forEach((link, idx) => {
        let affinityIds = link.affinity_ids || [];
        affinityIds.forEach(affinity => {
          affinityMap[affinity] = idx; // Store the index of the link in the links array
        });
      });
      this.setState({ affinities: affinityMap });
      this.setState({ f3Data: data });
    }, {
      childProfile: customer.childProfile
    });
  }


  getF3Links() {
    const customer = this.props.customer
    const device = this.props.device
    //F3 Links Change
    let urlParams = {
      countryCode: customer.country,
      accountId: customer.accountId,
      profileId: customer.profileId,
      productDeviceId: device.productDeviceId
    }
    this.setState({ isFetchingF3: true })
    fetchF3(urlParams, (err, resp) => {
      if (err) {
        console.error('Error fetching F3 data.')
        resp = { eans: [], links: [] }
      }
      this.setState({ isFetchingF3: false })

      const data = {
        data: resp,
        eanData: resp.eans
      }

      const affinityMap = {}
      data.data.links.forEach((link, idx) => {
        let affinityIds = link.affinity_ids || []
        affinityIds.forEach(affinity => {
          affinityMap[affinity] = idx // Store the index of the link in the links array
        })
      })
      this.setState({ affinities: affinityMap })
      this.setState({ f3Data: data })
    }, {
      childProfile: customer.childProfile
    })
  }
  
  alignTitles = () => {
    if (!this.covers) { return; }
    let el = this.covers.querySelectorAll('.title');
    let rowElements = [];
    let singleLineTitleRow = true;

    for (let index = 0; index < el.length; index++) {
      let currentElementTop = el[index].getBoundingClientRect().top;
      let nextElementTop = el[index + 1] && el[index + 1].getBoundingClientRect().top;

      rowElements.push(el[index]);

      if (singleLineTitleRow) {//Skip atleast once this flag becomes false for a row. Which means we have one or more multiline title(s).
        const text = el[index].innerText;
        singleLineTitleRow = el[index].clientWidth > getTextWidth(text, getCanvasFontSize(el[index])) //Check if the title length overflows actual width
      }
      if (Math.abs(currentElementTop - nextElementTop) > 100) {//comes here at the end of row.
        if (singleLineTitleRow) { //Alter the title height only when when the entire row has titles with single line.

          for (let ind = 0; ind < rowElements.length; ind++) {
            let height = (getCssStyle(rowElements[ind], 'height') || '16px').replace("px", "") /2;
            let fontSize = (getCssStyle(rowElements[ind], 'font-size') || '12px').replace("px", "");

            if (height > fontSize) { //Do this check to avoid getting text truncation issues.
              rowElements[ind].style.height = height + "px";
            }
          }
        }
        singleLineTitleRow = true;
        rowElements = [];
      }
    }
  }

  componentWillUpdate(nextProps, nextState) {
    // List is ready
    if (nextProps.data.listData && (nextProps.data.listData.eans.length !== this.props.data.listData.eans.length ||
      nextProps.data.listData.loadedListId !== this.props.data.listData.loadedListId)) {

      // Find out which EANs the user already owns so we can show the appropriate label
      window.Device.getLibraryItems(
          nextProps.data.listData.eans,
          inLibrary => {
            this.setState({ inLibrary });
          },
          err => console.error('Error getting user\'s library items.', err)
      );
    }
  }


  componentDidUpdate(prevProps, prevState) {
    // Load a new list if the dref changes
    let listTotalCount = 0;
    if (prevProps.dref !== this.props.dref) {
      this.offset = 1; // On filter change reset offset to 1 for search api to return results
      this.openList();
    }

    // If demand loading has finished, check if the entire list has been loaded
    if (this.isLoadingMore && !this.state.isFetchingList && !this.state.isFetchingMore) {
      this.isLoadingMore = false;
      let eans = this.props.data.listData.eans;
      if (eans.length === 0 || (eans.length && eans.length === this.oldListLength)) {
        this.allLoaded = true; // Loading is done
      }
      this.oldListLength = eans.length;
    }

    // Update the list sorting by calling search again
    if (this.state.sortKey !== prevState.sortKey) {
      this.openList({ replace: true }); // Force the redux store to update
    }

    // Update the list filter by calling search again
    if (this.state.audioFilter !== prevState.audioFilter) {
      this.offset = 1; // On filter change reset offset to 1 for search api to return results
      this.openList({replace: true});

    }

    // Update the title in the native header for EPD to always match the title of this list
    if (this.isEpd) {
      if (!this.props.noModifyHeader) {
        let listTitle = this.props.data.listDetails.listTitle !== prevProps.data.listDetails.listTitle ?
          this.props.data.listDetails.listTitle : prevProps.data.listDetails.listTitle;

        window.Device.modifyHeader({
          title: (this.props.title || listTitle || ""),
          enableBack: true,
          hideTools: this.isCategories
        });
      }

      if (prevProps && prevProps.data) {
        listTotalCount = prevProps.data.listData ? prevProps.data.listData.total : listTotalCount;
      }
      this.HandleF3Alignment(listTotalCount);
    }
  }

  generateFillers(data, listType, noOfRows) {
    let fillers = [];
    let noTopLists = data && data.length;
    if(data % noOfRows !== 0) {
      let noOfFillers = noOfRows - (noTopLists % noOfRows);
      for (let i = 0; (i < noOfFillers && noOfFillers < noOfRows); i++) {
        fillers.push(<ContainerFiller key={i} fillContainer={ listType } />);
      }
    }
    return fillers;
  }
  
  HandleF3Alignment(listLength) {
    if (this.covers) {
      let gridCovers = this.covers.getElementsByClassName("list-cover");
      let listCovers = this.covers.getElementsByClassName("list-grid-cover");
      let f3Link = document.getElementsByClassName("f3-links")[0];
      let isSubscription = window.location.href.match("subscription");

      if ((gridCovers || listCovers) && f3Link) {
        if (f3Link.classList.contains("align-f3")) {
          f3Link.classList.remove("align-f3");
        }
        if (f3Link.classList.contains("align-f3-grid-view")) {
          f3Link.classList.remove("align-f3-grid-view");
        }
        if (f3Link.classList.contains("no-list-so-align-f3")) {
          f3Link.classList.remove("no-list-so-align-f3");
        }
        if (f3Link.classList.contains("align-f3-list-view")) {
          f3Link.classList.remove("align-f3-list-view");
        }

        if (this.covers.children.length === 0 && !f3Link.classList.contains("no-list-so-align-f3")) {
          f3Link.classList.add("no-list-so-align-f3");
        }
      }
      if (gridCovers && gridCovers.length > 0) {

        if ((gridCovers.length % 8 > 4 || gridCovers.length % 8 === 0) && f3Link) {
          f3Link.classList.add("align-f3-grid-view");
        }
        //To Do: need to handle in css
        // loop through gridCovers 
        for (let i = 0; i < gridCovers.length; i++) {
          for (let j = 0; j < gridCovers.length; j += 8) {
            let selectedFours = Array.prototype.slice.call(gridCovers, j, j + 4);
            selectedFours.forEach(element => {
              if (!element.classList.contains("first-row") && !isSubscription) {
                element.classList.add("first-row");
              }
            });
          }
        }
      }
      if (listCovers && listCovers.length > 0) {
        if (f3Link && f3Link.classList.contains("align-f3-list-view")) {
          f3Link.classList.remove("align-f3-list-view");
        }
        //To Do: need to handle in css
        for (let i = 0; i < listCovers.length; i++) {
          for (let j = 0; j < listCovers.length; j += 4) {
            let selectedFours = Array.prototype.slice.call(listCovers, j, j + 1);
            selectedFours.forEach(element => {
              if (!element.classList.contains("first-row") && !isSubscription) {
                element.classList.add("first-row");
              }
            });
          }
        }
        if ((listCovers.length % 4 >= 0) && f3Link) {
          if (!f3Link.classList.contains("align-f3-list-view")) {
            f3Link.classList.add("align-f3-list-view");
          }
        }
      }
    }
  }

  componentWillUnmount() {
    sessionStorage.removeItem('showList');
    // Disable the scroller so events don't fire after this component is gone
    if (this.scroller) {
      this.scroller.unregister();
    }
    if (this.titleScroller) {
      this.titleScroller.unregister();
    }
    if (this.isEpd) {
      window.removeEventListener('onEPDLastPage', this.demandLoad);
    }

    // Localytics Event
    if (!this.props.noAnalytics) {
      this.sendAnalytics();
    }

  }


  createDemandScroller() {
    // EPD uses the pagination buttons which fire events
    if (this.isEpd) {
      window.addEventListener('onEPDLastPage', this.demandLoad);
    }

    // LCD uses regular browser scrolling
    else {
      this.scroller = new Scroller();
      this.scroller.register(this.demandLoad, {
        scrollOffset: 400 // Scrolled 400px from bottom
      });
    }
  }


  createTitleScroller() {

    // LCD uses regular browser scrolling to show the title in the native header
    // EPD always shows it in the native header
    if (!this.isEpd) {
      this.titleScroller = new Scroller();
      this.titleScroller.register(() => {
        let title = this.props.data.listDetails.listTitle || this.props.title || '';
        if (title && "Search Results" === title) {
          title = this.props.title
        }
        this.setPageTitleOnScroll(title);
      }, {
        scrollGreaterThan: 65 // Scrolled 80px
      });
    }
  }

  setPageTitleOnScroll(title) {

    // We are adding the title to the top when the user scrolled down
    if (title.length > 1) {
      this.titleScroller.unregister();
      this.titleScroller.register(() => this.setPageTitleOnScroll(' '), {
        scrollLessThan: 65 // Scrolled 80px
      });
    }

    // We are removing the page title when the user scrolls back up
    else if (title.length === 1) {
      let title = this.props.data.listDetails.listTitle || this.props.title || '';
      if ("Search Results" === title) {
        title = this.props.title
      }
      this.titleScroller.unregister();
      this.titleScroller.register(() => this.setPageTitleOnScroll(title), {
        scrollGreaterThan: 65 // Scrolled 80px
      });
    }

    // Show the native back button and no title
    if (!this.props.noModifyHeader) {
      window.Device.modifyHeader({
        title: (title.trim() && !this.isEpd) ? title : getTitle(this.props.isChildProfile),
        enableBack: true,
        hideTools: this.isCategories
      });
    }
  }


  renderCover(ean, i) {
    const { isInLibrary, eanObj, productData } = this.getListData(ean);

    if (!eanObj.ean || (this.isRecommendation && isInLibrary)) { return false; }

    return <ListCoverContainer key={ean + i}
      filler={ean < 0}
      inStore={this.props.inStore}
      index={i + 1}
      isInLibrary={isInLibrary}
      loading={ean === null}
      eanObj={eanObj}
      showDetails={true}
      onClick={this.coverClicked}
      productData={productData}
      storeCode={this.state.audioFilter}
      updateLibrary={this.updateLibrary} />;
  }


  getListData(ean) {
    const eanObj = this.props.eanData[ean] || {}
    const isInLibrary = this.state.inLibrary[ean] !== undefined &&
      (!this.state.inLibrary[ean].isSample || this.state.inLibrary[ean].isSample === 'false')
    const productData = this.props.data.listData
    return { isInLibrary, eanObj, productData }
  }

  renderListGridCover(ean, i) {
    const { isInLibrary, eanObj, productData } = this.getListData(ean);

    return <ListGridCoverContainer key={ean + i}
      filler={ean < 0}
      inStore={this.props.inStore}
      index={i + 1}
      isInLibrary={isInLibrary}
      loading={ean === null}
      eanObj={eanObj}
      showDetails={true}
      onClick={this.coverClicked}
      productData={productData}
      storeCode={this.state.audioFilter}
      updateLibrary={this.updateLibrary} />;
  }

  updateLibrary() {
    window.Device.getLibraryItems(this.props.data.listData.eans,
      inLibrary => {
        this.setState({ inLibrary });
      },
      err => console.error('Error getting user\'s library items.', err)
  );
  }


  render() {
    let loading = false;
    let totalCount = 0;
    const NO_OF_ITEMS_PER_PAGE_GRID_VIEW = 8;
    const NO_OF_ITEMS_PER_PAGE_LIST_VIEW = 4;
    const loadingAriaAttribs = {
      "aria-busy": true, "aria-live": "off"
    };
    // Show an inline spinner when demand loading
    let spinner = <div />;
    if (this.state.isFetchingMore || this.state.isFetchingList) {
      spinner = <Spinner />;
    }

    // When we're loading, show default covers so it looks good
    let eans = this.props.data.listData.eans.slice();
    if (this.state.isFetchingList) {
      loading = true;
      eans = this.loadingEans;
    }

    // Inject "filler" covers so that the list appears as a grid at the bottom (flexbox)
    if (!this.isEpd) {
      eans.push(-1000, -2000, -3000);
    }

    // if (this.isEpd) {
    //   if (eans.length % 8 === 1) {
    //     eans.push(-1000, -2000, -3000, -4000, -5000, -6000, -7000);
    //   }
    //   if (eans.length % 8 === 2) {
    //     eans.push(-1000, -2000, -3000, -4000, -5000, -6000);
    //   }
    //   if (eans.length % 8 === 3) {
    //     eans.push(-1000, -2000, -3000, -4000, -5000);
    //   }
    //   if (eans.length % 8 === 4) {
    //     eans.push(-1000, -2000, -3000, -4000);
    //   }
    //   if (eans.length % 8 === 5) {
    //     eans.push(-1000, -2000, -3000);
    //   }
    //   if (eans.length % 8 === 6) {
    //     eans.push(-1000, -2000);
    //   }
    //   if (eans.length % 8 === 7) {
    //     eans.push(-1000);
    //   }
    // }

    let eansArray;
    
    if (!this.isCategories) {
      eansArray = filterList(this.props.data.listData, this.props.eanData);
      eans = eansArray[0];
    }

    let initial = eans.length;
    eans = this.removeDuplicateEans(eans);
    let final = eans.length;

    let sortKey = this.state.sortKey;

    let eanDataArray = [];
    for (let index = 0; index < eans.length; index++) {
      let singleEanObject = Object.values(this.props.eanData).find(eanDataObj => Number(eanDataObj.ean) === Number(eans[index]));
      if (singleEanObject) {
        eanDataArray.push(singleEanObject)
      }
    }

    if (sortKey === 'pd' || sortKey === 'pa') {
      eans = eanDataArray.sort((a, b) => {
        if (sortKey === 'pd') {
          return Number(b.price) - Number(a.price);
        } else if (sortKey === 'pa') {
          return Number(a.price) - Number(b.price);
        }
      }).map(value => value.ean);
    }

    if (this.props.data.listDetails.listTotal && !this.isCategories) {
      totalCount = parseInt(this.props.data.listDetails.listTotal, 10) - eansArray[1] - (initial - final);
    }

    let covers = [];
    let gridListCovers = [];
    if (sessionStorage.getItem('showList') !== GRID) {
      covers = eans.map(this.renderCover, this);
      if (this.isEpd && window.location.href.match('/search/')) {
        covers.push(this.generateFillers(covers, "list-cover", NO_OF_ITEMS_PER_PAGE_GRID_VIEW));
      }
    }
    else {
      gridListCovers = eans.map(this.renderListGridCover, this);
      if (this.isEpd && window.location.href.match('/search/')) {
        gridListCovers.push(this.generateFillers(gridListCovers, "list-grid-cover", NO_OF_ITEMS_PER_PAGE_LIST_VIEW));
      }
    }

    let tncMessage = ''
    const tncData = localStorage.getItem('showTnC') ? JSON.parse(localStorage.getItem('showTnC')) : '';
    if (tncData) {
      for (let i = 0; i < tncData.length; i++) {
        if (Array.isArray(tncData[i].list_cloud_id) && tncData[i].list_cloud_id.indexOf(this.props.listId) !== -1) {
          tncMessage = tncData[i].tnc; break;
        } else if (tncData[i].list_cloud_id === this.props.listId) {
          tncMessage = tncData[i].tnc; break;
        }
      }
    }
    let tnc = tncMessage && <ListTnc tnc={tncMessage} />;

    const listDetails = this.props.data.listDetails;

    // Set the list title
    let listTitle = listDetails.listTitle;
    if (this.props.title) {
      listTitle = this.props.title;
    }
    listTitle = (listTitle || '').replace('&quest;', '?');

    // Some views (category results) have the count in the list sort "overline"
    let sortSuffix;
    let hasTotal = listDetails.listTotal || this.props.data.listData.total;
    let total = this.props.data.listData.total || listDetails.listTotal; // If search results have total, use it. TO DO: subtract filtered out eans from total.
    if (totalCount && window.location.href.match('/list/')) {
      total = totalCount;
    }
    const count = hasTotal && total > 0 ? ` (${(total).toLocaleString() || ' '})` : ' ';

    if (this.props.countInTitle) {
      if (hasTotal) {
        listTitle += count;
      }
    }
    else if (hasTotal) {
      sortSuffix = window.location.href.match('/search/') ? ` (${this.props.data.listData.total ? this.props.data.listData.total : "0" })` : count;
    }
    return <div {...(loading ? loadingAriaAttribs : { "aria-live": "off" })} ref={elt => window.shopList = elt} className={'shop-list' + (loading ? ' loading' : '')}>

      <div className={(listTitle !== undefined && listTitle.indexOf(textReplace(KEY.all_results)) !== -1 ? "sortalign " : " ")
    + (this.isEpd && this.isCategories && this.props.hasFilter ? "has-filter" : "no-filter") 
    + (this.isEpd && this.isCategories && this.props.hasTopLists ? " has-top-list" : "")
    + ((!this.props.hasTopLists && !this.isEpd && this.isCategories) ? "allresults-padding" : "")}>
        {
          !this.props.noSortMenu ?
            (
              this.props.listId === 'rec_from_ract_pod' ?
                <div className="sortMenuHide"> {/*  Cover images position will not change / do not move upwards for F1 list */}
                  <ListSort sortKey={this.state.sortKey} />
                </div>
                :
                <ListSort showSort={this.props.showSort}
                  hasTopLists={ this.isEpd && this.isCategories && this.props.hasFilter }
                  sortKey={this.state.sortKey}
                  audioFilter={this.state.audioFilter}
                  title={this.props.sortTitle}
                  suffix={sortSuffix}
                  hideDefault={this.props.defaultSort !== undefined}
                  onChange={this.handleSort}
                  onChangeFilter={this.handleFilter}
                  showGrid={sessionStorage.getItem('showList') !== 'list'}
                  onChangeGrid={this.handleGrid}
                  showAudFilter={this.props.showAudFilter} />
            )
            : <div></div>
        }

    <div className="list-title" role="heading" aria-level="1" aria-label={listTitle} >
      {listTitle || ""}
      {tnc || ""}
    </div>

      </div>

      {/* Render the covers for the list based on the "view type" (list/grid view) */}
      <div className="list">
        <div className={"covers" + (window.SHOW_AUDIOBOOKS ? " audio" : "")} ref={elt => this.covers = elt}>
          {sessionStorage.getItem('showList') !== GRID ? covers : gridListCovers}
          {this.isCategories && this.isEpd && <div id='show-all-categories-container' className={"show-all-categories-container " + (sessionStorage.getItem('showList') !== GRID ? "list-cover" : "list-grid-cover") } ref={el => window.seeAll = el}>
          <img className="all-categories-icon" src={AllCategories} alt={textReplace(KEY.see_all_categories)} />
          <div className="name" onClick={this.props.showAllCategories}>{textReplace(KEY.see_all_categories)}</div>
        </div>}
        </div>
        {spinner}
        {this.displayF3 && this.props.showF3 && this.state.f3Data && <F3Links
          childProfile={this.props.customer.childProfile}
          isFetchingF3={this.state.isFetchingF3}
          data={this.state.f3Data.data}
          affinities={this.state.affinities} // This is only the user's top 5 affinities at most
          eanData={this.state.f3Data.eanData}
          onLinkClick={data => this.prepareAnalytics(data)}
        />}
      </div>
    </div>;
  }

  prepareAnalytics(data) {
    this.analyticsEvent.data[attributes.shopType] = window.Device.getShopType();
    this.analyticsEvent.data[attributes.f3Title] = data.f3Title;
    this.analyticsEvent.data[attributes.f3Position] = data.f3Position;
    this.analyticsEvent.data[attributes.shopSection] = 'F3';
  }



  removeDuplicateEans(eans) {
    const seenEans = {};
    return eans.filter(e => {
      if (e > 0 && seenEans[e]) {
        console.log('DUPLICATE EAN FOUND', e);
        return false;
      }
      seenEans[e] = true;
      return true;
    });
  }


  coverClicked(listEanPosition) {

    // Analytics for List Viewed event
    if (!this.props.noAnalytics) {
      this.analyticsEvent.data[attributes.listEanPosition] = listEanPosition;

      // Component doesn't unmount when goign to PDP
      this.sendAnalytics();

      // Reset the event data since we'll come back from PDP
      this.initAnalytics();
    }

    // Analytics for a parent component (if any - like category results) 
    else if (this.props.onCoverClicked) {
      this.props.onCoverClicked(listEanPosition);
    }
  }


  sendAnalytics() {
    // Set attribute values
    const title = this.props.data.listDetails.listTitle || this.props.title || '';
    this.analyticsEvent.data[attributes.shopType] = window.Device.getShopType();
    this.analyticsEvent.data[attributes.listTitle] = title;
    this.analyticsEvent.data[attributes.demandLoadCount] = this._demandLoadCount;
    if (this._sortUsed) {
      this.analyticsEvent.data[attributes.sortUsed] = 'Yes';
    }

    // Send the event
    analytics.exit(this.analyticsEvent);
  }


  // 
  // Either load the initial content, or, load more content into the already
  // open list. Fetch the list metadata if we don't have it (deep linking).
  // 
  openList(options = {}) {
    const props = this.props;
    const listId = props.listId;
    const device = props.device || {};
    const customer = props.customer || {};
    const useSearch = props.searchParams;
    const useDref = props.useDref;

    let urlParams = {
      countryCode: customer.country,
      accountId: customer.accountId,
      profileId: customer.profileId,
      productDeviceId: device.productDeviceId,
      category_id: listId,
      offset: this.offset,
      itemCount: this.itemCount
    };

    // List is "Search Results" for a given store and category code (DREF)
    if (props.dref) {
      delete urlParams.category_id;
      urlParams.dref = props.dref;
      urlParams.storeCode = props.storeCode;
    }

    // List is generic search results with search query params
    else if (useSearch) {
      delete urlParams.category_id;
      props.searchParams.split('&').forEach(keyVal => {
        urlParams[keyVal.split('=')[0]] = keyVal.split('=')[1];
      });
    }

    if (this.state.sortKey) {
      urlParams.sort = this.state.sortKey;
    }

    try {
      localStorage.setItem(urlParams.profileId + "-sortkey", this.state.sortKey ? this.state.sortKey : '');
    } catch (e) {
      console.log(e)
    }

    if (this.state.audioFilter && window.Device.isGreaterOrEqualsAudioBookVersion()) {
      urlParams.storeCode = this.state.audioFilter;
    }

    // Only load data into the redux store if we don't already have it or
    // if we are demand loading (options.loadMore).
    const data = props.data;
    let isLoaded = useSearch ?
      data.listData.loadedSearchParams === props.searchParams :
      data.listData.loadedListId === listId;
    if (useDref) {
      isLoaded = data.listData.loadedListId === this.props.dref;
    }

    if (!this.state.isFetchingList && !this.state.isFetchingMore) {

      // Load the content
      if (!isLoaded || options.loadMore || options.replace || (isLoaded && listId !== 'rec_from_ract_pod')) {
        // Don't fetch details if we already have them for a list.
        // Don't fetch details for a category (DREF) list
        // Don't fetch details for a search query list
        // Never fetch details for the F1 fake list. We already added them to Redux
        const listDetails = data.listDetails;
        const listFormatType = data.listData.listFormatType;
        if ((listDetails.listId !== listId || !listDetails.listTotal) &&
          !props.searchParams &&
          !props.dref &&
          listId !== 'rec_from_ract_pod') {
            listDetails.listTitle = '';
          this.loadListDetails({
            listId,
            context_locale: 'en_US',
            countryCode: urlParams.countryCode,
            accountId: urlParams.accountId,
            profileId: urlParams.profileId,
            storeCode: urlParams.storeCode,
            productDeviceId: device.productDeviceId
          }, urlParams, options);
        }
        else {
          let store = this.storeCode ? this.storeCode : this.computeStoreCode(listFormatType, urlParams.storeCode);//categories
          if(store) {
            urlParams.storeCode = store;
          }
          this.loadList(urlParams, options);
        }
      }
    }
  }


  //
  // Fetch the list content
  // 
  loadList(urlParams, options = {}) {
    if (options.loadMore) {
      this.setState({ isFetchingMore: true });
    }
    else {
      this.setState({ isFetchingList: true });
    }
    if (this.setRecomAfterRefresh) {
      fetchF1(urlParams, (err, resp) => {
        if (err) {
          console.error('Error fetching F1 recommendations.', err);
          resp = { eans: [], pods: [], recommendations: {} };
        }
        this.setState({ isFetchingF1: false });
        const listData = {
          data: resp.recommendations || {},
          eanData: resp.eans
        };
        this.props.setList(listData);
        this.displayF3 = true;
      });
    }
    else {
      fetchList(urlParams, (err, data) => {
        if (err) {
          console.error('Error fetching list items.', err.message);
          window.Device.showError('Error fetching channels items.' + err.message);
          data = { eans: [], listData: { eans: [] } };
        }
        else {
          if (options.loadMore) {
            this.setState({ isFetchingMore: false });
          }
          else {
            this.setState({ isFetchingList: false });
          }
        }

        //Handled data mismatch (pending requests overwrites the actual response) in Coming Soon and New Releases.
        if (window.location.href.match("pubdate") && data.listData.id) {
          var pubDate = this.drefFromUrl(data.listData.id, /^pubdate/)
          if (!pubDate || !window.location.href.match(pubDate)) {
            return;
          }
        }

        const listData = {
          eanData: data.eans,
          data: {
            total: data.listData.total,
            loadedListId: data.listId || this.drefFromUrl(data.listData.id, /^dref/), // DREF lists don't have an "id", get the dref from the search URL
            eans: data.listData.eans,
            displayAlaCartePrice: data.listData.displayAlaCartePrice,
            displayAllFormats: data.listData.displayAllFormats,
            displayCredit: data.listData.displayCredit,
            freeList: data.listData.freeList,
            comingSoon: data.listData.comingSoon,
            maxPrice: data.listData.maxPrice,
            listFormatType: data.listData.listFormatType
          }
        };
        // If we fetched search results, we already have some of the details
        if (this.props.dref) {
          this.props.setListDetails({ data: data.listData });
        }

        // Demand load more list items (triggered by scrolling)
        if (options.loadMore || this.isFetchingMore) { // Demand load
          this.props.updateList(listData);
        }

        // Initial list list load
        else {
          this.props.setList(listData);
        }

        this.alignTitles();
        if (!this.isEpd) {
          window.Device.modifyHeader({
            title: getTitle(this.props.isChildProfile),
            enableBack: true,
            hideTools: this.isCategories
          });
        }
        this.displayF3 = true;

        this.updateLibrary();
      });
    }
  }


  //
  // We need to call the listDetails API to get the total number of items in the
  // list.
  //
  // List title/description are not returned by Search API. We must get it 
  // from the listDetails API as well, if we do not have them.
  // 
  loadListDetails(userParams, urlParams, options) {
    this.setState({ isFetchingListDetails: true });
    fetchListDetails(userParams, (err, response) => {

      // Set not fetching so we can still demand load even if there was an 
      // error getting details
      this.setState({ isFetchingListDetails: false });

      if (err) {
        console.error('Error fetching list details.', err.message);
        window.Device.showError('Error fetching list details.' + err.message);
        response = {};
      }

      const listData = {
        listId: userParams.listId,
        data: response
      };

      let store = this.computeStoreCode(listData.data.listFormatType, urlParams.storeCode);
      this.storeCode = store;

      this.props.setListDetails(listData);
      if(store) {
        urlParams.storeCode = store;
      }
      this.loadList(urlParams, options);

    });
  }

  computeStoreCode(listFormatType, storeCode) {
    const isChildProfile = this.props.customer.childProfile;
    const isAudiobooks = window.SHOW_AUDIOBOOKS;

    if (this.isCategories && isAudiobooks) {
      return isChildProfile ? storeCodes[storeCodeMap.kidsdigaud].code : storeCodes[storeCodeMap.digaud].code;
    }
    //Check for Audiobooks and type mixed list
    if (isAudiobooks && (this.isCategories || listFormatType !== 'Mixed')) {
      return isChildProfile ? storeCodes[storeCodeMap.kidsdigaud].code : storeCodes[storeCodeMap.digaud].code;
    }
    // Mixed has to be handled for both ebook and audiobook shop
    return listFormatType === 'Mixed' ? (isChildProfile ? storeCodes[storeCodeMap.kidsebookda].code : storeCodes[storeCodeMap.ebookda].code) : storeCode;
  }


  drefFromUrl(url, matchRegEx) {
    if (url) {
      const paramPairs = url.split('?').pop().split('&');
      const drefPair = paramPairs.find(d => d.match(matchRegEx));
      if (drefPair)
        return decodeURIComponent(drefPair.split('=').pop());
    }
  }


  handleSort(sortKey) {
    this.offset = 1; // Reset to the top of the list when sorting.
    this.oldListLength = 0;
    this._sortUsed = true;
    if (this.isEpd && this.state.sortKey !== sortKey) {
      console.log("Sort Changed...." + sortKey);
      window.resetPageNo = true;
      if (window.paginationComponent) {
        if (this.isCategories) {
          let categoryPageNo = Math.ceil(this.props.hasTopLists / 2) + 1;
          window.categoryPageReset = true;
          window.paginationComponent.gotoPage(categoryPageNo);
        } else {
          window.paginationComponent.gotoPage(1);
        }
      }
    }
    this.setState({ sortKey });
    this.allLoaded = false; // Reset allLoaded flag to false when sorting
  }

  handleFilter(audioFilter) {
    this.offset = 1; // Reset to the top of the list when sorting.
    this.oldListLength = 0;
    //this._sortUsed = true;
    this.setState({ audioFilter });
  }

  handleGrid(sortKey, audioFilter) {
    sessionStorage.setItem("showList", sortKey);
    this.setState({ showGrid: sortKey });
  }


  demandLoad(scrollPos) {
    if (scrollPos < 0) { return; }
    this.allLoaded = this.allLoaded && !window.sortChanged;
    if (this.allLoaded || this.isLoadingMore || this.state.isFetchingList || this.state.isFetchingMore || this.props.subscription) {
      return;
    }
    window.sortChanged = false;
    this.isLoadingMore = true;
    this.offset += this.itemCount;
    this._demandLoadCount++; // Analytics value

    // Send demand load count to parent component (like category results) for analytics
    if (this.props.onDemandLoad) {
      this.props.onDemandLoad(this._demandLoadCount);
    }
    this.openList({ loadMore: !this.isEpd ? true :  (this.isEpd && scrollPos.type === 'onEPDLastPage') ? true : false });
  }
}

List.defaultProps = {
  noSortMenu: false
}

export default List;



