import React, { Component } from 'react'
import platforms from '../lib/constants/platforms'
import ListCoverContainer from '../containers/ListCoverContainer'

import '../../style-dist/components/ListCarousel.css'

if (window.location.href.match(platforms.epd) || (window.location.href.match(platforms.quill))) {
  require('../../style-dist/components/ListCarousel.epd.css')
  require('../../style-dist/components/ListCarousel.quill.css')
  require('../../style-dist/loading.epd.css');
}


class F6Component extends Component {

  constructor(props) {
    super(props);
    this.isEpd = window.Device.shopPlatform === 'epd' || window.Device.shopPlatform === 'quill';
    this.state = {
      inLibrary: {},
      isTab: !this.isEpd && window.innerWidth > 480
    };
    window.addEventListener("resize", () => { this.setState({ isTab: !this.isEpd && window.innerWidth > 480 }); });
    
    if (this.props.list.eans) {
      console.log('Fetching library items');
      window.Device.getLibraryItems(this.props.list.eans,
        inLibrary => this.setState({ inLibrary }),
        err => console.error('Error getting user\'s library items.', err)
      );
    }
  }

  renderCover(ean, i) {
    const eanObj = this.props.eanData[ean] || {};
    const title = this.props.list.title;
    const description = this.props.list.description;
    const isInLibrary = this.state.inLibrary[ean] !== undefined &&
      (!this.state.inLibrary[ean].isSample || this.state.inLibrary[ean].isSample === 'false');

    if (!eanObj.ean || isInLibrary) { return false; }

    return <ListCoverContainer key={i}
      productData={this.props.list}
      title={this.props.list? this.props.list.title : ""}
      description={description}
      isTab={this.state.isTab}
      index={i + 1}
      eanObj={eanObj}
      isInLibrary={isInLibrary}
      f6={true}
      showDetails={true}
      onClick={idx => this.sendData(title, idx)} />;
  }

  render() {
    const list = this.props.list
    const title = list ? list.title : "";

    return <div ref={el => window.listcarousel = el} className={'f6 list-carousel' + (this.props.loading ? ' loading' : '')}>
      {!this.state.isTab && <div role="heading" aria-level="1">
        <div className="list-carousel-title">
          <div className="title ellipses lines-1" role='link'>{title}</div>
        </div>
      </div>}

      <div className="list" aria-label={title}>
        <div className="covers"
          ref={elem => this.listElem = elem}>
          {this.renderCover(list.ean, 0)}
        </div>
      </div>
    </div>;
  }

  sendData(title, eanPos = 0) {
    const data = {
      listTitle: title,
      listEanPosition: eanPos,
      listPosition: this.props.index
    };

    this.props.onCoverClick(data);
  }
}

export default F6Component;



