import React, { Component } from 'react'
import ItemCover from './ItemCover'
import platforms from '../lib/constants/platforms'
import UnSupportedPod from "./UnSupportedPod";
import textReplace from '../lib/i18n';
import KEY from '../lib/constants/languages/keys';


class SingleItemPromo extends Component {

  // static pod header title as props


  renderCover(eanObj) {
    return <div className="cover">
      <ItemCover eanObj={eanObj} />
    </div>;
  }


  render() {
    const pod = this.props.pod;

    if (!pod.data.ean) {
      return <UnSupportedPod />;
    }

    const eanObj = this.props.eanData[pod.data.ean.ean];
    const title = pod.data.title;
    const subtitle = pod.data.subtitle;
    const price = eanObj.price;
    const actionLabel = pod.data.action_label.toUpperCase();
    const discountAmt = eanObj.discountAmt;
    const discountPercent = "save " + eanObj.discountPercent + " %";
    const showDisplayAmount = discountAmt ? true : false;
    const showDisplayPercent = eanObj.discountPercent ? true : false;
    const showPrice = pod.html_template_name !== 'singleItemNoPricePromo';

    // iOS isn't allow to "sell" anything
    let cta = (!window.purchaseDisabled && actionLabel) ? actionLabel : textReplace(KEY.more_details);


    return (
      <div>
    <div role="link" className="pod single-item" onClick={() => this.props.onClick(pod)}>
      {this.renderCover(eanObj)}
      <div className="pod-details">
        <div className="text-container">
        { this.props.tnc.show_tnc &&
            <div className="subtitle ellipses lines-2 tnc"> {this.props.tnc.tnc.link} </div>
        }
          <div className="title ellipses lines-2">{title}</div>
          {subtitle && <div className={"subtitle ellipses lines-2"}>{subtitle}</div>}
          {showPrice && <div className="price">${price}</div>}
          {showDisplayAmount && showPrice && <div className="list-price"> {eanObj.displayListPrice}</div>}
          {showDisplayPercent && showPrice && <div className="sale-percent">&nbsp;{'| ' + discountPercent}</div>}
        </div>
        <div tabIndex="0" role="link" aria-label={cta} className="cta ellipses lines-1">{cta}</div>
      </div>
    </div>      
    </div>)
  }
}

export default SingleItemPromo;



