/**
 * This component renders a List of Lists. Each list is redered as a 
 * carousel of covers. Clicking on any list title will open that list. 
 * Clicking on an item cover will open the item in Native PDP.
 */
import React, { Component } from 'react'
import Lol from './Lol'
import platforms from '../lib/constants/platforms'
import analytics from '../classes/Analytics'
import { fetchLol } from '../lib/fetch'
import { screens, events, attributes } from '../lib/constants/analytics'

import '../../style-dist/components/ListOfLists.css'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'
if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/ListOfLists.epd.css')

class ListOfLists extends Component {

  constructor(props) {
    super(props);

    this.lolTitle = this.props.lolTitle || ("NOOK " + textReplace(KEY.lists));

    // State for this component only, not shared with others
    this.state = {
      isFetchingLol: false // Control the loading animation
    }

    // Bind methods
    this.coverClicked = this.coverClicked.bind(this);
    this.listClicked = this.listClicked.bind(this);
  }


  componentWillMount() {

    // Show the native back button and no title
    window.Device.modifyHeader({
      enableBack: true,
      title: decodeURIComponent(this.lolTitle)
    });

    // Always start at the top of the LOL
    let interval = setInterval(() => {
      if (document.readyState === 'complete') {
        clearTimeout(interval);
        window.scroll(0, 0);
        window.scrollTo(0, 0); //fallback if device doesn't support scroll.
      }
    }, 500);
  }


  componentDidMount() {

    // Open the content
    this.openLol();

    // Analytics
    analytics.enter(window.SHOW_AUDIOBOOKS ? screens.audioBooks.list_of_lists : screens.ebooks.list_of_lists);
    this.analyticsEvent = Object.assign({}, events.listOfListsViewed);
    this.analyticsEvent.data = Object.assign({}, events.listOfListsViewed.data);
  }


  componentWillUnmount() {
    sessionStorage.setItem('lolPosition', window.scrollY);
    // Localytics Event
    this.sendAnalytics();
  }


  render() {

    return <div className="shop-listoflists">
      {/* <div className="header">
        <div className="lol-title">
          { this.props.lolTitle || "NOOK Lists"} 
        </div>
      </div> */}
      <div className="lists">
        <Lol 
          isFetching={ this.state.isFetchingLol } 
          lists={ this.props.data.lists }
          eanData={ this.props.eanData }
          coverClicked={ this.coverClicked }
          listClicked={ this.listClicked } />
      </div>
    </div>;
  }


  coverClicked(data) {
    this.analyticsEvent.data[attributes.listTitle] = data.listTitle;
    this.analyticsEvent.data[attributes.listEanPosition] = data.listEanPosition;
    this.analyticsEvent.data[attributes.listViewAllClicked] = 'No';
    this.analyticsEvent.data[attributes.listPosition] = data.listPosition;
    
    // Component doesn't unmount when goign to PDP
    this.sendAnalytics();

    // Reset the event data since we'll come back from PDP
    this.analyticsEvent = Object.assign({}, events.listOfListsViewed);
    this.analyticsEvent.data = Object.assign({}, events.listOfListsViewed.data);
  }


  listClicked(data) {
    this.analyticsEvent.data[attributes.listTitle] = data.listTitle;
    this.analyticsEvent.data[attributes.listEanPosition] = data.listEanPosition;
    this.analyticsEvent.data[attributes.listViewAllClicked] = 'Yes';
    this.analyticsEvent.data[attributes.listPosition] = data.listPosition;
  }


  sendAnalytics() {
    this.analyticsEvent.data[attributes.listOfListsTitle] = this.lolTitle;
    this.analyticsEvent.data[attributes.shopType] = window.Device.getShopType();

    // Send the event
    analytics.exit(this.analyticsEvent);
  }


  // 
  // Either load the initial content, or, load more content into the LOL. 
  // 
  openLol(options={}) {
    const device = this.props.device;
    const customer = this.props.customer;
    const urlParams = {
      countryCode: customer.country,
      accountId: customer.accountId,
      profileId: customer.profileId,
      productDeviceId: device.productDeviceId,
      listIds: this.props.listIds
    };

    // Only load data into the redux store if we don't already have it or
    // if we are demand loading (options.loadMore).
    const data = this.props.data;
    const isLoaded = data.lists.length > 0;
    let sameContentLoaded = true;
    for (let l=0; l<data.lists.length; l++) {
      const list = data.lists[l];
      const listId = list.listCloudId;
      if (this.props.listIds && this.props.listIds.indexOf(listId) < 0) {
        sameContentLoaded = false;
      }
    }

    if (!isLoaded || (isLoaded && !sameContentLoaded)) {

      // Load Lol content
      this.loadLol(urlParams, options);
    }
  }


  //
  // Load the initial Lol data
  // 
  loadLol(urlParams) {
    this.setState({ isFetchingLol: true });

    fetchLol(urlParams, (err, response) => {
      if (err) {
        console.error('Error fetching lol items.' + err.message);
        window.Device.showError('Error fetching lol items.' + err);        
        response = {lists:[]};
      }

      // Initial Lol load
      else {
        this.props.setLists(response);
        this.setState({ isFetchingLol: false });
      }
    }, {cachedResponse: false});
  }
}

export default ListOfLists;



