//
// Callback (getters)
//
const getSessionInfo = {
  postMessage: (params) => {
    window[params.successCallback]( window.JSNI.getSessionInfo() );
  }
}

const getCategories = {
  postMessage: (params) => {
    window[params.successCallback]( window.JSNI.getCategories(params.key) );
  }
}

const setCategories = {
  postMessage: (params) => {
    window.JSNI.setCategories(params.key, params.value);
  }
}

const isConnected = {
  postMessage: (params) => {
    window[params.successCallback]( window.Nook.isConnected() );
  }
}

const isOrientationLandscape = {
  postMessage: (params) => {
    window[params.successCallback]( window.Nook.isOrientationLandscape() );
  }
}

const isSelectedFavInSession = {
  postMessage: (params) => {
    window[params.successCallback]( window.JSNI.isSelectedFavInSession(params.callback) );
  }
}

// Has params
const getRecentLibraryItems = {
  postMessage: (params) => {
    window.JSNI.getRecentLibraryItems(params.count, params.successCallback, params.failureCallback)
  }
}

const getLibraryItems = {
  postMessage: (params) => {
    window.JSNI.getLibraryItems(params.eans, params.successCallback, params.failureCallback);
  }
}


//
// No Callback (setters)
// 
const reload = {
  postMessage: (params) => {
    window.JSNI.reload();
  }
}

const tagEvent = {
  postMessage: (params) => {
    window.JSNI.tagEvent(params.event, params.data, params.context, params.ltv);
  }
}

const tagScreen = {
  postMessage: (params) => {
    window.JSNI.tagScreen(params.location);
  }
}

const close = {
  postMessage: (params) => {
    window.JSNI.close();
  }
}

const setUpEnabled = {
  postMessage: (params) => {
    window.JSNI.setUpEnabled(params.enabled);
  }
}

const showManageActionSheet = {
  postMessage: (params) => {
    window.JSNI.showManageActionSheet(params.ean, params.x, params.y, params.imageUrl);
  }
}

const showMenu = {
  postMessage: (params) => {
    window.JSNI.showMenu(params.dictionary, params.x, params.y, params.title, params.label, params.successCallback);
  }
}

// This function is for addition params for bottomX and bottomY to be passed
const showMenu2 = {
  postMessage: (params) => {
    window.JSNI.showMenu(params.dictionary, params.x, params.y, params.bottomX, 
      params.bottomY,params.title, params.label, params.successCallback);
  }
}

const modifyHeader = {
  postMessage: (params) => {
    window.JSNI.modifyHeader(params.enableBack, params.title, params.background, params.hideTools);
  }
}

const openProductDetails = {
  postMessage: (params) => {
    window.JSNI.openProductDetails(params.ean);
  }
}

const showToast = {
  postMessage: (params) => {
    window.Nook.showToast(params.message);
  }
}

const showGetConnected = {
  postMessage: () => {
    window.JSNI.showGetConnected();
  }
}

const showError = {
  postMessage: (params) => {
    window.JSNI.showError(params.message);
  }
}

const launchUrl = {
  postMessage: (params) => {
    window.JSNI.launchUrl(params.url);
  }
}

const onLibraryItemStateChange = {
  postMessage: (params) => {
    window.JSNI.onLibraryItemStateChange(params.ean, params.state);
  }
}

const onLibraryItemDownloadProgress = {
  postMessage: (params) => {
    window.JSNI.onLibraryItemDownloadProgress(params.ean, params.progress);
  }
}

const closeFavCatModal = {
  postMessage: (params) => {
    window.JSNI.closeFavCatModal(params.selectedFavCategories, params.successCallback, params.failureCallback);
  }
}

const postMessage = {

  // Create a global fake webkit.messageHandlers for in-browser iOS testing
  init: () => {
    window.webkit = {
      messageHandlers: {
        'getSessionInfoAsync': getSessionInfo,
        'getLibraryItemsAsync': getLibraryItems,
        'onLibraryItemStateChangeAsync': onLibraryItemStateChange,
        'onLibraryItemDownloadProgressAsync': onLibraryItemDownloadProgress,
        'getRecentLibraryItemsAsync': getRecentLibraryItems,
        'reload': reload,
        'tagEvent': tagEvent,
        'tagScreen': tagScreen,
        'close': close,
        'setUpEnabledAsync': setUpEnabled,
        'showManageActionSheetAsync': showManageActionSheet,
        'showMenuAsync': showMenu,
        'showMenu2Async': showMenu2,
        'modifyHeaderAsync': modifyHeader,
        'isConnectedAsync': isConnected,
        'isSelectedFavInSessionAsync': isSelectedFavInSession,
        'openProductDetailsAsync': openProductDetails,
        'showToastAsync': showToast,
        'showGetConnectedAsync': showGetConnected,
        'showErrorAsync': showError,
        'launchUrlAsync': launchUrl,
        'closeFavCatModalAsync': closeFavCatModal,
        'getCategoriesAsync': getCategories,
        'setCategoriesAsync': setCategories,
        'isOrientationLandscapeAsync': isOrientationLandscape
      }
    }
  }
}

export default postMessage;



