/**
 * This file implements a database for Shop. Data stored here will persist
 * until the user uninstalls the app or manually clears the webView cache.
 *
 * Items can be set with an optional "expiry" parameter which specifies a
 * how many hours an item should be stored for.
 */

import platforms from "../lib/constants/platforms";

class Database {

  constructor() {
    this._PREFIX = '__NOOK-DB_';
    this._store = window.localStorage;
    this._timeStampRegex = /\|\d{13}$/;

    this._checkExpiry();
  }


  //
  // Add a key-value pair to the database. Objects will be stored as strings
  // but will be returned as objects.
  // 
  // expiryHours: number of hours the item should be kept for.
  // 
  set(key, value, expiryHours, ignorePrefix) {
    try {
      this._checkExpiry();

      const newKey = (ignorePrefix ? "" : this._PREFIX) + key;

      if (typeof value === 'object') {
        try {
          value = JSON.stringify(value);
        } catch (e) {
          console.error('Cache error.', e);
          return false;
        }
      }

      if (expiryHours !== undefined) {
        const now = Date.now();
        const expiryMilliseconds = Math.round(expiryHours * 60 * 60 * 1000);
        const expiry = now + expiryMilliseconds;

        value += '|' + expiry.toString(); // Append the expiry date to the key
      }

      this._store.setItem(newKey, value);
    }
    catch (e) {
      console.log(e)
    }
    return true;
  }


  //
  // Return a value from the database. Return an object if an object was
  // stored.
  // 
  get(key, ignorePrefix) {
    this._checkExpiry();

    const newKey = (ignorePrefix ? "" : this._PREFIX) + key;

    let value = this._store.getItem(newKey);

    if (value && typeof value === 'string') {
      value = value.replace(this._timeStampRegex, '');
    }

    try {
      let obj = JSON.parse(value);
      if (typeof obj === 'object') {
        value = obj;
      }
    } catch (e) { /* No error, it's not an object. */ }

    // console.log(key, '==>', value)
    return value;
  }


  //
  // Check all items for expiration. Remove if expired.
  //
  _checkExpiry() {
    const now = Date.now();

    for (let key in this._store) {
      if (!this._store.hasOwnProperty(key)) {
        continue;
      }

      const val = this._store[key];
      if (val === undefined || typeof val !== 'string') {
        continue;
      }
      const matched = val.match(this._timeStampRegex);

      // Item has an expiry date
      if (matched) {
        const expiry = Number(matched[0].replace('|', ''));

        // Item is expired
        if (expiry < now) {
          console.warn('DB: Removing Expired Item "' + key.replace(this._PREFIX, '') + '"');
          this._store.removeItem(key);
        }
      }
    }
  }

  getCategoriesFromNative = (key, callback, failed) => {
    try {
      if (window.LOCALHOST) {//dev
        return callback(localStorage.getItem(key) || "");
      } else if (window.location.href.match(platforms.ios)) {//ios
        const successCallback = 'getCategoriesGood';
        const failureCallback = 'getCategoriesBad';

        window[successCallback] = callback;
        window[failureCallback] = failed;

        window.webkit.messageHandlers.getCategoriesAsync.postMessage({
          key: key,
          successCallback,
          failureCallback
        });
      } else {//android
        return callback(window.JSNI.getCategories(key) || "");
      }
    } catch (e) {
      console.error("err in getCategoriesFromNative:"  + e.message);
      failed();
    }
  }

  setCategoriesToNative = (key, value) => {
    window.Device.setCategories({ key: key, value: JSON.stringify(value) });
  }

}

export default Database;



