import actions from '../lib/constants/actionTypes'

const initialState = {
  isInStore: false,
  isAppActive: true
};

const shellData = (state = initialState, action) => {
  let stateCopy = Object.assign({}, state);

  switch (action.type) {

    case actions.SET_IN_STORE:
      stateCopy.isInStore = action.isInStore;
      return stateCopy;

    
    case actions.SET_APP_ACTIVE:
      stateCopy.isAppActive = action.isAppActive;
      return stateCopy;
    
    default:
      return state;
  }}

export default shellData



