import React, { Component } from 'react'
import ListCarousel from './ListCarousel'
import ShopRetry from './ShopRetry'
import Scroller from '../classes/Scroller'
import platforms from '../lib/constants/platforms'
import HeroListCarousel from './HeroListCarousel'

import '../../style-dist/components/F4Lists.css'
import F6Component from './F6Component'
import { constants } from '../lib/utils'


class F4Lists extends Component {

  constructor(props) {
    super(props);

    this.isEpd = window.Device.shopPlatform === 'epd' || window.Device.shopPlatform === 'quill';
    
    // Lazy rendering so we only show a subset of the carousels until the user scrolls down
    this.INITIAL_DISPLAY_COUNT = 2; // Load the first 2, then the last 4 after a delay
    this.LAZY_SCROLL_TRIGGER = 100; //Made 100 to work for kids profile as it doesn't have recommendations.

    this.state = {
      isLazyWaiting: true,
    };
    if(this.isEpd && sessionStorage.getItem('forMePageNo')) { //Increase the display count while retaining page for EPD.
      this.INITIAL_DISPLAY_COUNT = (sessionStorage.getItem('forMePageNo') * 2) - 2; //subtract 2 as for me page don't have F4/F6 list
    }
    // Define empty lists to render as carousels while loading
    this.loadingLists = new Array(6).fill({ title: '', eans: [null, null, null, null, null, null] });
  }
  
  
  componentDidMount() {

    // Use "lazy rendering" so that we don't have to fetch the images immediately
    // If the user scrolls down, we will render the rest of the carousels and the
    // images will be fetched/rendered.
    const lazyRender = () => {
      console.log('Lazy loading more F4s after scroll');
      this.setState({ isLazyWaiting: false });

      if (this.isEpd) {
        window.removeEventListener('onEPDPageDown', lazyRender);
      }
      else if (this.lazyScroller) {
        this.lazyScroller.unregister(); // Only call the callback once
      }
    };

    if (!this.isEpd) {
      this.lazyScroller = new Scroller();
      if(window.Device.shopPlatform === platforms.android && '7.1.1' === window.Device.getAndroidVersion()){
        this.lazyScroller.register(lazyRender.bind(this), { scrollGreaterThan: 550 });
      } else {
        this.lazyScroller.register(lazyRender.bind(this), { scrollGreaterThan: this.LAZY_SCROLL_TRIGGER });
      }
    }

    else {
      window.addEventListener('onEPDPageDown', lazyRender.bind(this));
    }
  }
  

  render() {
    let loading = false;
    let f4List = this.props.data;
    let data = (f4List && f4List.data) || [];
    if (this.props.isFetchingF4 || !data.length) {
      loading = true;
      data = this.loadingLists;
    }

    // Render the retry button
    if (!this.props.isFetchingF4 && data && !data.length) {
      return <div className="f4-lists"><ShopRetry status={ f4List.status } /></div>;
    }

    //Lazy rendering for performance. Shop has to render A LOT of covers on For Me
    if (this.state.isLazyWaiting && this.props.position < 600) {
      data = data.length > this.INITIAL_DISPLAY_COUNT ? data.slice(0, this.INITIAL_DISPLAY_COUNT) : data;
    }

    return <div className="f4-lists" ref={ ref => this.f4Lists = ref }>
      { data.map( (list, i) => (
        list.type === constants.F6 ? 
        <F6Component
        key={ i }
        index={ i+1 }
        list={ list }
        eanData={ this.props.eanData }
        onCoverClick={ this.props.onCoverClick } />
        :
        ((list.eans.length > 3 && (list.type == constants.PERFECT_COVER || list.type == constants.TWO_ROWS)) ? 
        <HeroListCarousel
          listCarousel={'debug-f4-list'}
          key={ i }
          loading={ loading }
          index={ i+1 }
          list={ list }
          eanData={ this.props.eanData }
          onCoverClick={ this.props.onCoverClick }
          onListClick={ this.props.onListClick } /> 
        
        : 
        <ListCarousel 
          listCarousel={'f4-list'}
          key={ i }
          loading={ loading }
          index={ i+1 }
          list={ list }
          eanData={ this.props.eanData }
          onCoverClick={ this.props.onCoverClick }
          onListClick={ this.props.onListClick } />
      ))) }
    </div>;
  }
}

export default F4Lists;



