import React, { Component } from 'react'
import { favoriteCategories } from '../lib/fetch'
import { constants, getImageName } from '../lib/utils'
import platforms from '../lib/constants/platforms'
import '../../style-dist/components/ManageFavCategories.css'
import defaultBook from '../../../public/images/categories_icons_medium/default.svg'

if (window.location.href.match(platforms.epd) || window.location.href.match(platforms.quill))
  require('../../style-dist/components/ManageFavCategories.epd.css');

class ManageFavCategories extends Component {
  constructor(props) {
    super(props);
    this.state = {
      ebookCats: {},
      audiobookCats: {},
      loading: false
    }
    let customer = this.props.customer;
    this.urlParams = {
      accountId: customer.accountId,
      profileId: customer.profileId,
      shopHome: "both"
    };
    this.focusEvent = window.location.href.match(platforms.ios) ? "focus" : "visibilitychange";
  }

  componentDidMount() {
    this.ebookKey = `selectedFavCategories_${this.urlParams.accountId}-${this.urlParams.profileId}-${'ebook'}`;
    this.audiobookKey = `selectedFavCategories_${this.urlParams.accountId}-${this.urlParams.profileId}-${'audiobook'}`;
    let ebookFavorites = localStorage.getItem(this.ebookKey) || null;
    let audiobookFavorites = localStorage.getItem(this.audiobookKey) || null;

    this.setState({ ebookCats: JSON.parse(ebookFavorites)});
    this.setState({ audiobookCats: JSON.parse(audiobookFavorites)});

    window.setTimeout(() => this.loadFavCategories(), 3000);

    if (window.screen.width <= 480) {
      window.Device.modifyHeader({
        enableBack: true,
        title: constants.FAV_CATEGORIES,
        hideTools: true,
        hideBottomNav: false
      });
    }
    window.addEventListener(this.focusEvent, () => this.loadFavCategories(), 3000);
  }

  componentWillUnmount = () => {
    window.removeEventListener(this.focusEvent, this.loadFavCategories());
  }

  loadFavCategories() {
    this.setState({ loading: true });

    favoriteCategories('get', this.urlParams, null, (err, resp) => {
      this.setState({ loading: false });
      this.loaded = true;

      if (err) {
        console.error('Error fetching Favorite Categories.');
        resp = { favorites: [] };
      }
      if (resp && resp.data) {
        if (resp.data.ebook) {
          this.setState({ ebookCats: resp.data.ebook });
          localStorage.setItem(this.ebookKey, JSON.stringify(resp.data.ebook));
        }
        if (resp.data.audiobook) {
          this.setState({ audiobookCats: resp.data.audiobook });
          localStorage.setItem(this.audiobookKey, JSON.stringify(resp.data.audiobook));
        }
      }
    });
  }

  renderCats(iseBook) {
    let shopType = iseBook ? constants.EBOOK : constants.AUDIOBOOK;
    let title = shopType + " " + constants.CATEGORIES;
    let noFav;

    //Navigate to Select Favorites screen
    const navigate = () => {
      const shopPlatform = window.Device.shopPlatform;
      let isEPD = window.location.href.match(platforms.epd) || window.location.href.match(platforms.quill);

      let linkPath = `#/${shopPlatform}/home/favCategories/${shopType.toLowerCase()}`;
      window.location.hash = linkPath;
    }

    //Handle No Favories Selected.
    if ((iseBook && (!this.state.ebookCats || !this.state.ebookCats.length)) ||
      (!iseBook && (!this.state.audiobookCats || !this.state.audiobookCats.length))) {
      noFav = <div className='no-fav'><span className='content'>
        {constants.NO_FAV_CONTENT}
      </span>
        <div>
          <button className={"edit-fav"} onClick={navigate} >
            {constants.ADD_CATEGORIES}
          </button>
        </div>
      </div>
    }

    let cats = iseBook ? this.state.ebookCats : this.state.audiobookCats;
    let categoryImage = [];
    let categoryLabel = [];
    this.state.categoryImage = [];

    //Display icons based on category id.
    for (let index = 0; index < (cats || {}).length; index++) {
      let cat = cats[index];

      const getFileName = (arg, catName) => {
        try {
          const images = require.context('../../../public/images/categories_icons_medium', true);
          let imgsrc = images(`./${arg}`);
          categoryImage[cat.id] = imgsrc;
          categoryLabel[cat.id] = catName;
        }
        catch (error) {
          categoryImage[cat.id] = defaultBook;
        }
        this.state.categoryImage = categoryImage
      }

      getImageName(cat, getFileName);
    }

    return <div className={'fav-container' + (this.state.loading ? ' loading' : '')}>
      <div className='fav-title-container'>
        <span className='fav-title'>{title}</span>
        {!noFav && <span onClick={navigate} className='fav-edit'>{constants.EDIT}</span>}
      </div>
      <div className='fav-item-container'>
        {noFav || cats.map( // render no fav design when no categories are chosen
          a => {
            return <div className='fav-item' key={a.id}> <img className='fav-image' src={categoryImage[a.id]}></img>
              <span className='fav-label'>{categoryLabel[a.id] || a.label}</span> </div>
          })
        }
      </div>
    </div>
  }

  render() {
    return (
      <div className="manage-fav-categories">
        <div className='intro'>
          <span>
            {constants.EXPLORE_FAVORITES_USAGE_INFO + constants.EDIT_FAVORITES_USAGE_INFO}
          </span>
        </div>
        {
          this.renderCats(true)//pass true for ebooks
        }
        {!window.noAudiobookSupport && <div className='separator'>
        </div>
        }
        {!window.noAudiobookSupport && this.renderCats(false) //pass false for audiobooks
        }
      </div>
    )
  }
}

export default ManageFavCategories;


