import React, { Component } from 'react'
import ListCarousel from './ListCarousel'
import ShopRetry from './ShopRetry'

import '../../style-dist/components/F1Recommendations.css'
import textReplace from '../lib/i18n';
import KEY from '../lib/constants/languages/keys';


class F1Recommendations extends Component {

  constructor(props) {
    super(props);

    // Define empty lists to render as carousels while loading
    this.loadingList = {
      id: 1,
      eans: [null, null, null, null, null, null]
    };
  }


  render() {
    let loading = false;
    let data = this.props.data;

    if (this.props.isFetchingF1 || !data.eans) {
      loading = true;
      data = this.loadingList;
    }

    // Render the retry button
    if (!this.props.isFetchingF1 && this.props.data && !this.props.data.id) {
      return <div className="f1-lists"><ShopRetry /></div>;
    }

    if (data && data.id) {
      data = {
        title: textReplace(KEY.recommended_for_you),
        listCloudId: data.id,
        eans: data.eans
      };
    }

    return <div className="f1-list f4-lists">
      <ListCarousel
        isRecommendation={ true }
        index={ 1 }
        loading={ loading }
        list={ data }
        eanData={ this.props.eanData }
        buildF1List={ this.props.buildF1List }
        onCoverClick={ this.props.onCoverClick }
        onListClick={ this.props.onListClick }/>
    </div>;
  }
}

export default F1Recommendations;



