import { connect } from 'react-redux'
import actionTypes from '../lib/constants/actionTypes'
import CategoryList from '../components/CategoryList'

const mapStateToProps = (state, ownProps) => {
  return {
    customer: state.sessionData.customer,
    device: state.sessionData.device,
    eanData: state.eanData,
    data: state.categoryData
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {

  const updateEanData = (eanData) => {
    dispatch({
      type: actionTypes.ADD_EAN_DATA,
      eans: eanData
    });
  };

  return {
    
    setDrefMap: (drefMap) => {
      console.log('LOADING DREF MAP');
      dispatch({
        type: actionTypes.SET_STATIC_DREF_MAP,
        data: drefMap
      });
    },
  
    setSubCategories: (data, dref) => {
      console.log('LOADING SUB-CATEGORIES');
      updateEanData(data.eanData);
      dispatch({
        type: actionTypes.SET_CATEGORY_DATA,
        code: dref,
        data: data.data
      });
    },

    setStoreCategories: (data, dref) => {
      console.log('LOADING STORE-CATEGORIES');
      dispatch({
        type: actionTypes.SET_DREF_HASH,
        data: data.data
      });
    }
  }
}

const CategoryListContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(CategoryList)

export default CategoryListContainer



