import { connect } from 'react-redux'
import ListCover from '../components/ListGridCover'


const mapStateToProps = (state, ownProps) => {
  return {
    inStore: state.shellData.isInStore
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {}
}


const ListGridCoverContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(ListCover)

export default ListGridCoverContainer



