import actions from '../lib/constants/actionTypes'


const initialState = {
  f1: {},
  becauseYouRead: {}
};

const epdHomeData = (state = initialState, action) => {
  let stateCopy = Object.assign({}, state);

  switch (action.type) {

    case actions.SET_F1_DATA:
      stateCopy.f1 = action.f1Data;
      return stateCopy;

    case actions.SET_BYR_DATA:
      stateCopy.becauseYouRead = action.byrData;
      return stateCopy;
    
    default:
      return state;
  }
}

export default epdHomeData



