//
// Cloud API functions
//
import axios from 'axios';
import { getAssemblePath } from '../lib/utils'


const _baseUrl = () => {
  if (window.SHOP_CLOUD_URL) {
    return window.SHOP_CLOUD_URL + "/api/internal/";
  }
  else { // Not distubing the existing logic as it could be used as fallback and also required for dev purpose.
    switch (window.SHOP_ENV) {
      case 'qa':
        return 'https://csqa.barnesandnoble.com/bncloud/api/internal/';
      case 'qa2':
        // QA2 assembler uses data from production
        return 'https://csqa2.barnesandnoble.com/bncloud/api/internal/';
      case 'prod':
      case 'dev':
      default:
        return 'https://bncs.barnesandnoble.com/bncloud/api/internal/'; //Default unknown env to prod.
    }
  }
}


const _ajax_assemble = async (httpMethod, path, params = "", data, succeeded, failed, retry) => {

  if (params.indexOf("appVersion") === -1) {
    params += "&appVersion=" + window.APP_VERSION;
  }
  
  let queryString = `${path}?${params}`;
  let retryCount = retry || 3;
  let requestObject = {
    method: httpMethod,
    responseType: 'json',
    url: queryString,
  };

  if(httpMethod === 'post' && data) {
    requestObject.data = data;
  }
  try {
    let response = await axios.request(requestObject);
    console.log('AJAX:', queryString);
    if (response && response.data) {
      succeeded(response.data);
    } else {
      retryCount -= 1;
      if (retryCount > 0) {
        console.log("Retrying...." + retryCount)
        setTimeout(() => {
          _ajax_assemble(httpMethod, path, params, data, succeeded, failed, retryCount);
        }, 1000);
      } else {
        return failed("Retry Failed...");
      }
      console.log("Cloudjs response failed...." + response)
    }
  } catch (err) {
    retryCount -= 1;
    if (retryCount > 0) {
      console.log("Retrying...." + retryCount)
      setTimeout(() => {
        _ajax_assemble(httpMethod, path, params, data, succeeded, failed, retryCount);
      }, 1000);
    }
    return failed(err);
  }
}


//
// Returns the XHR object so it can be cancelled by calling .done()
// async (httpMethod, path, params = "", data=[], succeeded, failed, retry)
// 
export const assemble = (httpMethod, path, params = "", data, succeeded, failed) => {
  const _assembleUrl = _baseUrl() + getAssemblePath(path, httpMethod);
  path = _assembleUrl + path;
  return _ajax_assemble(httpMethod, path, params, data, succeeded, failed);
}

export const proxy = (httpMethod, path, params = "", data, succeeded, failed) => {
  const _proxyUrl = _baseUrl() + 'proxy/v1';
  path = _proxyUrl + path;
  return _ajax_assemble(httpMethod, path, params, data, succeeded, failed);
}


