import React, { Component } from 'react';
import ChannelsContainer from '../containers/ChannelsContainer'


class ChannelsView extends Component {

  render() {
    return (
      <section className="shop-view shop-channels">
        <ChannelsContainer 
          refinementType={ this.props.match.params.refinementType }
          channelsTab={ this.props.match.params.channelsTab } />
      </section>);
  }
}

export default ChannelsView;



