import actiontypes from '../lib/constants/actionTypes'

const initialState = {
  affinities: []
};

const forMeData = (state = initialState, action) => {  
  let stateCopy = Object.assign({}, state);

  switch (action.type) {

    case actiontypes.SET_AFFINITY_DATA:
      stateCopy.affinities = action.affinities;
      return stateCopy;
    
    default:
      return state;
  }}

export default forMeData



