import React, { Component } from 'react'
import { Switch, Route, Link } from 'react-router-dom'
// import { CSSTransition, TransitionGroup } from 'react-transition-group';

// Import view wrappers (React components) for Shop children
import HomeView from '../components/HomeView'
import ListView from '../components/ListView'
import ChannelsView from '../components/ChannelsView'
import ListOfListsView from '../components/ListOfListsView'
import ManageFavCategoriesContainer from '../containers/ManageFavCategoriesContainer'
import EpdHomeContainer from '../containers/EpdHomeContainer'
import FavoriteCategoriesContainer from '../containers/FavoriteCategoriesContainer'
import AudiobookSubscription from './AudiobookSubscription'
import storeCodes, { storeCodeMap } from '../lib/constants/storeCodes'

import FourOhFour from './404'
import EpdPaginationContainer from '../containers/EpdPaginationContainer'
import { loadSessionData } from '../lib/session'
import { fetchAffinities, fetchConfig, fetchStoreCategories } from '../lib/fetch'

import Database from '../classes/Database'
import Cache from '../classes/Cache'
import shellEvent from '../lib/shellEvent'
import Device from '../classes/Device'
import { networkErrorListen } from '../lib/networking'
import gotoDeepLinks from '../lib/constants/gotoDeepLinks'
import platforms from '../lib/constants/platforms'

import analytics from '../classes/Analytics'
import { events, attributes } from '../lib/constants/analytics'

import '../../style-dist/components/Shop.css'
import Spinner from './Spinner'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'

if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/Shop.epd.css')
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/Shop.quill.css')


class Shop extends Component {

  constructor(props) {
    // Call the base class' constructor with props
    super(props);

    this.state = {
      isPb: false // Set a class on original paperback since it has a font rendering bug
    };

    // Create the global shellEvent for the native code to call
    window.onShellEvent = shellEvent.init({
      toggleAppActive: this.props.toggleAppActive,
      toggleInStore: this.props.toggleInStore
    });

    // Initialize the Shop
    this.initialize(this.props);

    // Bind Methods
    this.loadCustomerAffinities = this.loadCustomerAffinities.bind(this);
    this.AFFINITY_LIMIT = 3;
    this.SHOW_SHOP_OFFLINE = 2;
    }


  initialize(props) {

    // DEVELOPMENT ONLY
    // Instantiate a mock JSNI object.
    // iOS doesn't have global interfaces (JSNI/Nook), it uses webkit message handlers
    if (!window.JSNI && !window.webkit || window.location.href.match('localhost')) {
      console.warn('Using mock DEVELOPMENT JSNI service.');
      const JSNIClass = require('../development/JSNI').default;
      window.JSNI = new JSNIClass();
      window.Nook = {
        isConnected: () => navigator.onLine,
        showToast: msg => alert('TOAST: ' + msg),
        openProductDetails: ean => alert('Native PDP: ' + ean)
      }
      if (window.location.hash.match(/ios/)) {
        console.warn('Using mock DEVELOPMENT webkit postmessage API');
        const pm = require('../development/postMessage').default;
        pm.init(); // Creates a global 
      }
    }

    try {
      const shopPlatform = props.match.params.shopPlatform;
      // Create the global device class instance
      console.log('SHOP FOR ', shopPlatform);
      window.Device = new Device(shopPlatform);

      // Listen for AJAX errors and retry/disable Shop
      networkErrorListen(this.SHOW_SHOP_OFFLINE);

      // Create the global database (never cleared)
      window.db = new Database();

      // Create the global cache
      window.cache = new Cache();

      // Add the platform to the DOM so we can use it in CSS
      document.documentElement.classList.add(`platform-${shopPlatform}`);

      if (location.hash.match(/epd/)) {
        document.documentElement.classList.add('no-scroll');
        document.body.classList.add('no-scroll');
      }

      if (location.hash.match(/quill/)) {
        document.documentElement.classList.add('no-scroll');
        document.body.classList.add('no-scroll');
      }

      // Prevent EPD Home classes from overriding EPD Shop
      if (window.location.hash.match('device-home')) {
        document.documentElement.className += ` device-home`;
      }

      // Alert the user if they're pointing to the staging build
      if (window.location.href.match(/staging/)) {
        window.Device.showToast({ message: 'Staging URL: ' + window.location.href })
      }

      // Load the session (device and user) data from the native code
      this.loadSession();

      // Handle uncaught exceptions

      // window.onerror = (message) => {
      //   window.Device.showError(message);
      // };
    }
    catch (err) {
      console.log(err.message);
      window.Device.showError(err.message);
    }
  }

  componentWillMount() {
    window.addEventListener('online', () => { window.location.reload(true); });
  }

  // Deep link using the "old" goto param created for brannigan Shop
  componentDidMount() {
    const params = this.parseParams();
    const goto = params.goto;

    // Send webView Ready Localytics event
    this.sendWebViewAnalytics();

    if (goto) {
      console.log('GOTO: ', goto);
      const location = gotoDeepLinks.find( g => goto.match(g.match) );
      if (!location) {
        return console.error('Broken goto deep link!', goto);
      }
      location.goto(goto, params);
    }
  }

  sendWebViewAnalytics () {
    // Send webView Ready Localytics event
    let analyticsEvent = Object.assign({}, events.webviewLoaded);
    analyticsEvent.data = Object.assign({}, events.webviewLoaded.data);
    analyticsEvent.data[attributes.webviewVersion ] = window.Device.getBrowserVersion();
    analyticsEvent.data[attributes.appSection] = window.SHOW_AUDIOBOOKS ? "AUDIOBOOKS_SHOP" : "EBOOKS_SHOP";


    analytics.exit(analyticsEvent);
  }


  componentWillUpdate(nextProps, nextState) {

    // After we have the session data, we can fetch the Shop config (device ID)
    // and then the customer's affinities
    if (Object.keys(this.props.sessionData).length !== Object.keys(nextProps.sessionData).length) {
      console.log('SESSION:', nextProps.sessionData);

      const appVersion = nextProps.sessionData.appVersion;
      const modelName = nextProps.sessionData.device.nookModel;
      const deviceId = (typeof nextProps.sessionData.device.productDeviceId !== "undefined") ? 
                          nextProps.sessionData.device.productDeviceId : '';
      sessionStorage.setItem('appVersion', appVersion);

      // Check if the native session data gave us device ID. If not, call the config API to get it.
      if (deviceId) {
        const newSession = JSON.parse(JSON.stringify(nextProps.sessionData));
        newSession.device.productDeviceId = deviceId;
        this.props.setConfig(newSession, { productDeviceId: deviceId });
      }
      if (!deviceId) {
        fetchConfig(appVersion, modelName, (err, config) => {
          if (err) {
            // TODO - add a styled error popup
            alert('Something has gone wrong. Please check your Internet connection and try again later.');
          }
          else {
            const newSession = JSON.parse(JSON.stringify(nextProps.sessionData));
            newSession.device.productDeviceId = config.productDeviceId;
            this.props.setConfig(newSession, config);
          }
        })
      }

      // Add a CSS class for child profile
      if (nextProps.sessionData.customer.childProfile) {
        document.documentElement.classList.add('child');
      }
    }

    // Changing platforms
    // This only gets called when using the developer navigation menu
    if (nextProps.match.params.shopPlatform !== this.props.match.params.shopPlatform) {
      this.initialize(nextProps);
    }
    
    if((nextProps.sessionData.device && this.props.sessionData.device)) {
      this.loadCustomerAffinities(nextState, nextProps);
    }
  }

  loadSession() {

    // Allow the session data to be passed through the URL params
    // (easy browser testing)
    const urlParams = this.parseParams();

    loadSessionData(urlParams, (err, response) => {
      if (err) {
        console.error('Error loading session data!', err);
        response = {};
      }

      // There's a native EPD bug (AN-16280) that opens Android URL hash instead of EPD for deep links
      checkFixEPDDeepLinkBug(response);

      window.cache.updatePrefix(response.customer.profileId);

      this.props.setSession(response);

      // HACK for Papberback 1
      if (response && response.device.nookModel === 'BNTV450') {
        this.setState({ isPb: true });
      }
    });
  }


  loadCustomerAffinities(nextState, nextProps) {
    
    // Only fetch if we don't already have the data
    if (nextProps.affinityData.affinities.length > 0) {
      return;
    }

    const customer = nextProps.sessionData.customer;
    const device = nextProps.sessionData.device;
    const urlParams = {
      countryCode: customer.country,
      accountId: customer.accountId,
      profileId: customer.profileId,
      productDeviceId: device.productDeviceId,
      storeCode: window.SHOW_AUDIOBOOKS ? storeCodes[storeCodeMap.digaud].code : storeCodes[storeCodeMap.dig].code
    };

    fetchAffinities(urlParams, this.AFFINITY_LIMIT, (err, response) => {
      if (err) {
        console.error('Error fetching user affinities.', err);
        response = [];
      }
      this.props.setAffinities(response);
      console.log('AFFINITIES:', response);
    });


    let currentStore = storeCodes[storeCodeMap[customer.childProfile ? "kids" : "nkbk"]] || null

    const done = (err, response) => {
      if (err) {
        console.error('Error fetching category data.', err.message);        
        response = {eans:[], categories:[], links:[]};
      }
    };

    fetchStoreCategories(currentStore.code, {}, done); // Cached for 12 hrs
  }


  //
  // Developer navigation
  // 
  renderDevNav() {
    return (
      <section className="shop-dev-nav">
        <h3>Developer Navigation</h3>
        <ul>
          { Object.keys(platforms).map( (plat, i) => <li key={i}><Link to={`/${plat}/home/for_me`}>{plat.toUpperCase()}</Link></li>) }
          <li><Link to={`/epd/device-home`}>EPD HOME</Link></li>
        </ul>
      </section>
    );
  }


  render() {
    let epdControls = <div />;
    let selectedFavEPDControls = <div />;
    let editFavEPDControls = <div />;
    const sessionLoading = Object.keys(this.props.sessionData).length === 0;
    if (sessionLoading) {
      return <p></p>;//Loading Session...</p>; TODO
    }

    const affinityLoading = this.props.affinityData.length === 0;
    const loadingConfig = !this.props.configData || !this.props.configData.productDeviceId || !this.props.sessionData.device || !this.props.sessionData.device.productDeviceId;
    if (loadingConfig || affinityLoading) {
      return <Spinner />;
    }

    const shopPlatform = window.Device.shopPlatform;
    if (!shopPlatform) {
      return this.renderDevNav();
    }
    else if (!platforms[shopPlatform]) {
      return <FourOhFour />;
    }

    // Custom controls on the bottom of the page for EPD
    else if (shopPlatform === platforms.epd ) {
      let currentLoacation = window.location.href;
      let shopHomeCustom = '';
      let shopSeeAllCustom = '';
      let noborder = false;
      if (window.location.href.match('for_me')) {
        shopHomeCustom = textReplace(KEY.explore_more_lists);
      }
      if (window.location.href.match(/categories$/)) {
        shopHomeCustom = textReplace(KEY.explore_more_categories);
      }
      if (window.location.href.match(/categories/)) {
        shopSeeAllCustom = textReplace(KEY.all_categories);
        noborder = true;
      }
      if (window.location.href.match('list')) {
        shopSeeAllCustom = textReplace(KEY.see_all_titles);
      }
      if (window.location.href.match('listoflists')) {
        shopSeeAllCustom = null
      }
      if (window.location.href.match('list/')) {
        shopSeeAllCustom = null
      }
      if(window.location.href.match('favCategories')) {
        shopSeeAllCustom = null;
        noborder = false;
      }
      
      if(!window.location.href.match('favCategories')) {
        epdControls = <EpdPaginationContainer noborder={ noborder } firstPageCustomDown={ shopHomeCustom } screenLocation={currentLoacation} pageNo={1} seeAllCustom={shopSeeAllCustom} />;
      }

      if (window.location.href.match('favCategories/ebook') ||
        window.location.href.match('favCategories/audiobooks')) {
        epdControls = <EpdPaginationContainer noborder={noborder} firstPageCustomDown={shopHomeCustom} screenLocation={currentLoacation} pageNo={1} seeAllCustom={shopSeeAllCustom} categoryType={"profile"} />;
      }
    }

    else if (shopPlatform === platforms.quill) {
      let currentLoacation = window.location.href;
      let shopHomeCustom = '';
      let shopSeeAllCustom = '';
      let noborder = false;
      if (window.location.href.match('for_me')) {
        shopHomeCustom = textReplace(KEY.explore_more_lists);
      }
      if (window.location.href.match(/categories$/)) {
        shopHomeCustom = textReplace(KEY.explore_more_categories);
      }
      if(window.location.href.match(/categories/)) {
        shopSeeAllCustom = textReplace(KEY.all_categories);
        noborder = true;
      }
      if (window.location.href.match('list')) {
        shopSeeAllCustom = textReplace(KEY.see_all_titles);
      }
      if (window.location.href.match('listoflists')) {
        shopSeeAllCustom = null;
      }
      if (window.location.href.match('list/')) {
        shopSeeAllCustom = null;
      }
      if(window.location.href.match('favCategories')) {
        shopSeeAllCustom = '';
        noborder = false;
      }
      if(!window.location.href.match('favCategories')) {
        epdControls = <EpdPaginationContainer noborder={ noborder } firstPageCustomDown={ shopHomeCustom } screenLocation={currentLoacation} pageNo={1} seeAllCustom={shopSeeAllCustom} />;
      }

      if (window.location.href.match('favCategories/ebook') ||
        window.location.href.match('favCategories/audiobook')) {
        epdControls = <EpdPaginationContainer noborder={noborder} firstPageCustomDown={shopHomeCustom} screenLocation={currentLoacation} pageNo={1} seeAllCustom={shopSeeAllCustom} categoryType={"profile"} />;
      }
    }

    if(window.location.href.match('managefavcategories')) {
      epdControls = "";
    }

    const match = this.props.match; // This contains the URL "match" from the parent route
    return (
      <div id="shop-wrapper" className={ 'shop-wrapper' + (this.state.isPb ? ' PB1' : '') }>

        {/* These are all the child paths we can render in Shop */}
        <Switch>
          <Route path={ `${match.url}/home/managefavcategories` } component={ ManageFavCategoriesContainer } />
          <Route path={ `${match.url}/home/favCategories/:shopType` } component={ FavoriteCategoriesContainer } />
          <Route path={ `${match.url}/home/favCategories` } component={ FavoriteCategoriesContainer } />
          <Route path={ `${match.url}/home` } component={ HomeView } />
          <Route path={ `${match.url}/list/:listId/:sort?` } component={ ListView } />
          <Route path={ `${match.url}/list/:listId/:sort?/grid` } component={ ListOfListsView } />
          {/* <Route path={ `${match.url}/list/:listId/sort` } component={ ListView } /> */}
          <Route path={ `${match.url}/search/:listName/:searchParams` } component={ ListView } />
          <Route path={ `${match.url}/search/:listName/:searchParams/sort` } component={ ListView } />
          <Route path={ `${match.url}/channels/:refinementType/:channelsTab` } component={ ChannelsView } />
          <Route path={ `${match.url}/listoflists/:listIds/:lolTitle` } component={ ListOfListsView } />
          <Route path={ `${match.url}/audioBooks/subscription` } component={  AudiobookSubscription } />
        </Switch>
        
        {/* This is the Home Screen for EPD */}
        <Route path='/:platform/device-home' component={EpdHomeContainer} />
        { epdControls }
      </div>);
  }


  parseParams() {
    const params = {};

    let pstr = window.location.search.substr(1);
    pstr = pstr.replace ('?', '&'); // Some goto deep links have query param syntax (?) in the params string
    params.asString = pstr

    pstr.split('&').forEach( pair => {
      const keyVal = pair.split('=');
      if (keyVal.length === 2)
        params[keyVal[0]] = keyVal[1];
    });
    return params;
  }
}

export default Shop;


function checkFixEPDDeepLinkBug(session) {
  const isAndroidURL = !!window.location.hash.match('/android/');
  const hasModel = session && session.device && session.device.nookModel;
  if (isAndroidURL && hasModel) {
    // Piper, Jay, Quill
    const isEpd = ['BNRV510', 'BNRV520'].indexOf(session.device.nookModel) >= 0;
    const isQuill = 'BNRV700' === session.device.nookModel;

    if (isEpd) {
      window.location.hash = window.location.hash.replace('android', 'epd');
    }
    else if (isQuill) {
      window.location.hash = window.location.hash.replace('android', 'quill');
    }
  }
}



