  shopType: 'Shop Type',
  categorySection: 'Category Section',
  podPosition: 'Pod Position',
  podSection: 'Pod Section',
  podHeroName: 'Pod Hero Name',
  podRelatedCategoryName: 'Pod Related Category Name',
