  resultsEanPosition: 'Results EAN Position',

  categoryName: 'Category name',
  listOfListsTitle: 'List of Lists Title',
  listEanPosition: 'List EAN Position',
  listViewAllClicked: 'List View All clicked',
  listPosition: 'List position',
  listTitle: 'List title',
  demandLoadCount: 'Demand Load Count',
  sortUsed: 'Sort Used',
  promotionEan: 'Promotion EAN',
  f2Position: 'F2 position',
