import { connect } from 'react-redux'
import { setEpdPage } from '../lib/actions'
import EpdControls from '../components/EpdControls'

const mapStateToProps = (state, ownProps) => {
  return {
    currentPage: state.epdData.currentPage
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {
  return {
    setPage: (page) => {
      dispatch(setEpdPage(page));
    }
  }
}

const LoadSession = connect(
  mapStateToProps,
  mapDispatchToProps
)(EpdControls)

export default LoadSession



