import React from 'react'
import platforms from '../lib/constants/platforms'

import '../../style-dist/components/Spinner.css'
if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/Spinner.epd.css')
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/Spinner.quill.css')

const Spinner = (align) => {

  if (window.location.href.match(platforms.epd) || 
        window.location.href.match(platforms.quill)) {
    return <div className="three-dots">
      <div className={`spinner ${align ? "center" : ""}`}>
        <div className="bounce1"></div>
        <div className="bounce2"></div>
        <div className="bounce3"></div>
      </div>
    </div>
  }
  
  return <svg className="spinner-circle"
      width="40"
      height="40"
      version="1.1"
      xmlns="http://www.w3.org/2000/svg">
    <circle cx="20" cy="20" r="15" />
  </svg>;
}

export default Spinner;



