//
// Load the (device and customer) session data from the native code.
// 

import locale from '../lib/locale'
// import getModelMap from '../lib/constants/deviceModels'
// let DEVICE_MODEL_MAP = {};


//
// TODO
//
window.EventDispatcher = {
  on: () => {},
  trigger: () => {}
}
window.EVENTS = {};


export const loadSessionData = (urlParams, callback) => {
  let sdata = {};

  // Get data from the native API
  const good = (response) => {
    sdata = typeof response === "string" ? JSON.parse(response) : response;
    // DEVICE_MODEL_MAP = getModelMap(sdata.appVersion);

    if (sdata.customer === undefined) {
      sdata.customer = {};
    }
    if (sdata.device === undefined) {
      sdata.device = {};
    }
    if (sdata.flags === undefined) {
      sdata.flags = {};
    }

    // fill in more gaps
    if (urlParams) {
      if (sdata.customer.country === undefined) {
        sdata.customer.country = urlParams.countryCode || urlParams.country;
      }
      if (sdata.customer.retailer === undefined) {
        sdata.customer.retailer = urlParams.retailer;
      }
      if (sdata.customer.accountId === undefined) {
        sdata.customer.accountId = urlParams.accountId;
      }
      if (sdata.customer.profileId === undefined) {
        sdata.customer.profileId = urlParams.profileId;
      }
      if (sdata.customer.customerId === undefined) {
        sdata.customer.customerId = urlParams.customerId;
      }
      if (sdata.customer.locale === undefined) {
        sdata.customer.locale = urlParams.locale;
      }
      if (sdata.device.productDeviceId === undefined) {
        sdata.device.productDeviceId = urlParams.productDeviceId;
      }

      if (urlParams.group !== undefined) {
        sdata.customer.group.push(urlParams.group);
      }
    }

    // Device ID is inferred from the device Model
    // if (sdata.device.productDeviceId === undefined) {
    //   console.log(DEVICE_MODEL_MAP);
    //   sdata.device.productDeviceId = deviceIdMap(sdata);
    // }
    if (sdata.device.os === undefined) {
      sdata.device.os = ['IPHENTC','IPADNTC','IPADNTCHD','IPHENTCHD'].indexOf(sdata.device.nookModel) >= 0 ? 'ios' : 'android';
    }

    sdata.customer.retailer = locale.determineRetailer(sdata.customer.country);
    sdata.customer.shopDataLocale = locale.determineMerchandisingLocale(sdata.customer.country, sdata.customer.locale);
    if (sdata.customer.locale === undefined) {
      sdata.customer.locale = locale.defaultLocale(sdata.customer.country);
    }
    sdata.customer.shopUILocale = sdata.customer.locale;

    window.SHOP_ENV = getEnv(sdata);
    window.SHOP_CLOUD_URL = sdata.shopBNCloudUrl
    window.APP_VERSION = sdata.appVersion;
    window.SHOW_AUDIOBOOKS = sdata.showAudiobooks === 'true';
    window.oneCreditPrice = sdata.oneCreditPrice;
    window.purchaseDisabled = sdata.purchaseDisabled === 'true';
    window.noAudiobookSupport = sdata.noAudiobookSupport === 'true';
    window.shopPermissionEnable = !(sdata.shopPermissionEnable === 'false');
    callback(null, sdata);
  }


  const bad = (response) => {
    console.error("getSessionInfo failed", response);

    let data = {
      customer: {
        retailer: 'NOK',
        country: 'GB'
      }
    }

    return good(JSON.stringify(data));
  }

  if (!window.Device) {
    console.error("Device not loaded");
  }

  window.Device.getSessionInfo(good, bad);
}


//
// Use session data to determine what environment the native app says we're in
//
function getEnv(sdata) {
  let env = sdata.environment;
  if(env) { // check if native passes the environment in session info.
    return env.toLowerCase();
  }

  //Existing logic could be used as fallback. Also, needed for dev.
  
  const customerData = sdata.customer;
  if (customerData.gpbUrl) {
    if (/bncs/.test(customerData.gpbUrl)) {
      env = 'prod';
    }
    else if (/csqa2/.test(customerData.gpbUrl)) {
      env = 'qa2'; // QA2 Assembler uses data form production
    }
    else if (/csqa/.test(customerData.gpbUrl)) {
      env = 'qa';
    }
    else {
      env = 'dev';
    }
  }
  else {
    if (window.location.hostname === 'localhost') {
      env = 'dev';
    }
    else {
      env = 'prod';
    }
  }
  return env;
}


