import React, { Component } from 'react'
import headset from '../../images/audiobook-icon/audiobooks_logo.svg'
import bank from '../../images/static/icons/bank_epd.svg'
import credit from '../../images/static/icons/credit_epd.svg'
import mark from '../../images/static/icons/mark_epd.svg'
import stack from '../../images/static/icons/stack_epd.svg'
import ListContainer from '../containers/ListContainer'
import rightArrow from '../../images/right_arrow_black.svg'
import chat from '../../images/static/icons/icon_chat_epd.svg'
import platforms from '../lib/constants/platforms'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'
require('intersection-observer');

require('../../style-dist/components/AudiobookSubscription.epd.css')
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/AudiobookSubscription.quill.css')



class AudiobookSubscriptionEPD extends Component {

	constructor() {
		super();
		this.state = {
			isProfile: (window.location.href.match(platforms.epd) && window.innerWidth < 536) || 
			(window.location.href.match(platforms.quill) && window.innerWidth < 702)
		};
	}

	closeSubscriptionScreen() {
		window.Device.closeSubscriptionScreen();
	}

	launchFAQs() {
		window.Device.launchUrl({ url: 'http://www.nook.com/app/settings/faq' })
	}

	render() {
		return (
			<React.Fragment>
				<div className= {'subscription' + (this.state.isProfile ? " profile" : "") } >
					{ !this.state.isProfile && <div className='close' onClick={this.closeSubscriptionScreen}>
						x
					</div>}
					<div className="intro-container">
						<img src={headset} alt={ "B&N" + textReplace(KEY.audiobooks)} className="audiobook-icon" />
						<div className='title'>
						{textReplace(KEY.for_book_people)}
						</div>
						<div className='subtitle'>
						{textReplace(KEY.get_started_with)}
						</div>
						<div>
							<button className='trial-button' onClick={this.props.startSubscription} >{textReplace(KEY.start_free_trial)}</button>
						</div>
						<div className='oneCredit'>
							{window.oneCreditPrice + textReplace(KEY.cancel_anytime)}
						</div>
					</div>
					<div className="what-you-get-container">
						<div className='what-you-get'>
						{textReplace(KEY.what_you_get)}
						</div>
						<div className="perks">
							<div className="perksContainer">
								<div className="blockSection">
									<div className="innerBox">
										<img className="innerBoxIcon" src={bank} alt={textReplace(KEY.savings)} />
										<div className="separator"></div>
										<p className='title'>{textReplace(KEY.savings)}</p>
										<p>{textReplace(KEY.free_book)}</p>
									</div>
									<div className="innerBox">
										<img className="innerBoxIcon" src={credit} alt={textReplace(KEY.credit_1book)} />
										<div className="separator"></div>
										<p className='title'>{textReplace(KEY.credit_1book)}</p>
										<div className="separator"></div>
										<p>{textReplace(KEY.redeem_credit)}</p>
									</div>
								</div>
							</div>
							<div className="perksContainer">
								<div className="blockSection">
									<div className="innerBox">
										<img className="innerBoxIcon" src={mark} alt={textReplace(KEY.credits_save)} />
										<div className="separator"></div>
										<p className='title'>{textReplace(KEY.credits_save)}</p>
										<div className="separator"></div>
										<p className="saving"> {textReplace(KEY.saving_over)}
										</p>

									</div>
									<div className="innerBox">
										<img className="innerBoxIcon" src={stack} alt={textReplace(KEY.no_commitment)} />
										<div className="separator"></div>
										<p className='title'>{textReplace(KEY.no_commitment)}</p>
										<div className="separator"></div>
										<p>{textReplace(KEY.cancel)}</p>
									</div>
								</div>
							</div>
						</div>
					</div>
					<div className="listen-anytime-container">
						<div className="via-content">
							<div className="title">
							{textReplace(KEY.listen_anytime)}</div>
							<p className="para-bold">{textReplace(KEY.via) + " "} BN.COM</p>
							<p>{textReplace(KEY.listen_your_purchased_books)}<br /><span>{textReplace(KEY.account_library)}</span>
							</p>
							<p className="para-bold">{textReplace(KEY.via_devices)}</p>
							<p >{textReplace(KEY.download_free)}</p>

						</div>
						<div>
							<button className='trial-button' onClick={this.props.startSubscription}>{textReplace(KEY.start_free_trial)}</button>
							<div className='oneCredit'>
							{window.oneCreditPrice + textReplace(KEY.cancel_anytime)}
							</div>
						</div>
					</div>
					<div className="without-subscription-container">
						<div className="without-subscription">
							<span className='title'>{textReplace(KEY.interested_in_audiobooks)}</span></div>
						<div className="subtitle">{textReplace(KEY.browse_audiobook_titles)}</div>

					</div>
					<div className="audioBookList">
						<ListContainer noSortMenu={true} showSort={false} listId={this.props.subscriptionListId} onDemandLoad={false} subscription={true} />
						<div className="browse-bn-container">
							<button className="title" onClick={this.props.browseAudiobooks}><h3>{textReplace(KEY.browse_audio_books)} </h3>
								<img className="icon-right" src={rightArrow} alt={textReplace(KEY.right_arrow)} />
							</button>
						</div>
					</div>
					<div className="faqs-container">
						<div className="faq-icon-container">
							<img className="faq-icon" src={chat} alt={textReplace(KEY.have_questions)} />
						</div>
						<div className="faq-question"><span>{textReplace(KEY.have_questions)}</span></div>
						<div className="faq-text">
							<span>
								{textReplace(KEY.call_us)} <br />
								<u><b>1-212-123-4567</b></u> {textReplace(KEY.review_faq)}
							</span>
						</div>
						<div className="faq-read" onClick={this.launchFAQs}>
							{/* <span> */}
							{/* {textReplace(KEY.read_faq)} */}
							<button className="title" onClick={this.props.browseAudiobooks}><h3>{textReplace(KEY.read_faq)} </h3>
								<img className="icon-right" src={rightArrow} alt={textReplace(KEY.right_arrow)} />
							</button>
							{/* </span> */}
						</div>
					</div>
				</div>
				<div></div>
			</React.Fragment>
		);
	}
}

export default AudiobookSubscriptionEPD


