import React, { Component } from 'react';
import ListOfListsContainer from '../containers/ListOfListsContainer'


class ListOfListsView extends Component {

  render() {
    return (
      <section className="shop-view shop-listoflists">
        <ListOfListsContainer
          listIds={ this.props.match.params.listIds }
          lolTitle={ this.props.match.params.lolTitle } />
      </section>);
  }
}

export default ListOfListsView;



