import { connect } from 'react-redux'
import actionTypes from '../lib/constants/actionTypes'
import Channels from '../components/Channels'

const mapStateToProps = (state, ownProps) => {
  return {
    customer: state.sessionData.customer,
    device: state.sessionData.device,
    eanData: state.eanData,
    data: state.channelsData
  }
}

const mapDispatchToProps = (dispatch, ownProps) => {

  const updateEanData = (eanData) => {
    dispatch({
      type: actionTypes.ADD_EAN_DATA,
      eans: eanData
    });
  };

  return {
    
    // Set Nook Channels data in redux
    setChannels: (channelsData) => {
      updateEanData(channelsData.eans);

      dispatch({
        type: actionTypes.SET_CHANNELS_ITEMS,
        affinityLists: channelsData.affinityLists,
        popularLists: channelsData.popularLists
      });
    },

    // Append additional channels lists
    updateChannels: (channelsData) => {
      updateEanData(channelsData.eans);
      
      dispatch({
        type: actionTypes.ADD_CHANNELS_ITEMS,
        affinityLists: channelsData.affinityLists,
        popularLists: channelsData.popularLists
      });
    }
  }
}

const ChannelsContainer = connect(
  mapStateToProps,
  mapDispatchToProps
)(Channels)

export default ChannelsContainer



