import React, { Component } from 'react'
import logo from '../../images/static/icons/logo_nook_audiobooks.svg'
import bannerMobile from '../../images/static/banner/mobile.png'
import bannerSmallTab from '../../images/static/banner/small_tab.png'
import BannerLargeTab from '../../images/static/banner/large_tab.png'
import landingImageMobile from '../../images/static/subscription_landing.svg'
import landingImageTab from '../../images/static/subscription_landing_large.svg'
import bank from '../../images/static/icons/bank.svg'
import credit from '../../images/static/icons/credit.svg'
import chat from '../../images/static/icons/icon_chat.svg'
import mark from '../../images/static/icons/mark.svg'
import stack from '../../images/static/icons/stack.svg'
import ListContainer from '../containers/ListContainer'
import AudiobookSubscriptionEPD from './AudiobookSubscriptionEPD'
import platforms from '../lib/constants/platforms'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'
require('intersection-observer');

require('../../style-dist/components/AudiobookSubscription.css')


class AudiobookSubscription extends Component {

	constructor(props) {
		super(props);
		this.state = {
			bannerImage: bannerMobile,
			landingImage: landingImageMobile,
			intersecting: false
		};
		this.isEPD = window.location.href.match(platforms.epd) || window.location.href.match(platforms.quill);

		// Bind methods
		this.startSubscription = this.startSubscription.bind(this);
		this.subscriptionListId = (window.SHOP_ENV === "qa" || window.SHOP_ENV === "qa2") ? "10000010360" : "10000014449"
	}

	startSubscription() {
		console.log('Subscription alert JSNI');
		window.Device.startYourSubscription();
	}

	browseAudiobooks() {
		console.log('Browse Audiobook JSNI')
		window.Device.launchBrowseAudiobooks();
	}

	launchFAQs() {
		window.Device.launchUrl({ url: 'http://www.nook.com/app/settings/faq' })
	}

	handleResize = (event) => {
		this.resize();
	}

	resize() {
		if (window.innerWidth <= 425) {
			this.setState({ bannerImage: bannerMobile })
			this.setState({ landingImage: landingImageMobile })
		}
		else if (window.innerWidth > 425 && window.innerWidth < 799) {
			this.setState({ bannerImage: bannerSmallTab })
		}
		else if (window.innerWidth > 800) {
			this.setState({ bannerImage: BannerLargeTab })
		}
		if (window.innerWidth > 425) {
			this.setState({ landingImage: landingImageTab })
		}
	}

	componentDidMount() {
		window.addEventListener('resize', this.handleResize)

		this.resize();

		//Intersection Observer
		const handler = (entries) => {
			this.setState({ intersecting: entries[0].isIntersecting });
		}
		const observer = new window.IntersectionObserver(handler)

		if(this.sentinal)
		observer.observe(this.sentinal)
	}

	componentWillUnmount() {
		window.removeEventListener('resize', this.handleResize)
	}

	render() {
		return (
			this.isEPD ? <AudiobookSubscriptionEPD
				startSubscription={this.startSubscription}
				browseAudiobooks={this.browseAudiobooks}
				subscriptionListId={this.subscriptionListId}
			/> :
				<div>
					<div className="header-container">
						<img src={logo} alt={ "NOOK " + textReplace(KEY.audiobooks)} className="banner-top" />
						<img src={this.state.bannerImage} alt={textReplace(KEY.audiobooks)} className="banner" />
					</div>
					<div className="subscription-container">
						<div className="subscription-getstarted">
							<div><h1>{textReplace(KEY.get_started_with)}  <br />{textReplace(KEY.your_free_audiobook)}</h1></div>
							<div> <p className="when-you-subscribe">{textReplace(KEY.when_you_subscribe)} </p></div>
							<div ref={elt => this.sentinal = elt} className="sentinal"></div>
						</div>
						<div ref={elt => this.stickydiv = elt} className={"stickydiv" + (!this.state.intersecting ? ' stick-bottom' : '')}>
							<button className="freetrial-button" onClick={this.startSubscription}>{textReplace(KEY.start_free_trial)}</button>
							<p> {window.oneCreditPrice + textReplace(KEY.cancel_text)}</p>
						</div>
					</div>
					<div className="perks-header">
						<h2>{textReplace(KEY.listening_perks)}</h2>
					</div>
					<div className="perks">
						<div className="perksContainer">
							<div className="blockSection">
								<div className="innerBox">
									<img className="innerBoxIcon" src={bank} alt={textReplace(KEY.everyday_savings)} />
									<div className="separator"></div>
									<strong>{textReplace(KEY.everyday_savings)}</strong>
									<p className="dash-separator">_____</p>
									<p>{textReplace(KEY.save_on_subscription)}</p>
								</div>
								<div className="innerBox">
									<img className="innerBoxIcon" src={credit} alt={textReplace(KEY.credit_1book)} />
									<div className="separator"></div>
									<strong>{textReplace(KEY.credit_1book)}</strong>
									<p className="dash-separator">_____</p>
									<div className="separator"></div>
									<p>{textReplace(KEY.credit_1book_text)}</p>
								</div>
							</div>
						</div>
						<div className="perksContainer">
							<div className="blockSection">
								<div className="innerBox">
									<img className="innerBoxIcon" src={mark} alt={textReplace(KEY.listen_anywhere)} />
									<div className="separator"></div>
									<strong>{textReplace(KEY.listen_anywhere)}</strong>
									<p className="dash-separator">_____</p>
									<div className="separator"></div>
									<p className="saving"> {textReplace(KEY.switch_devices)}
									</p>

								</div>
								<div className="innerBox">
									<img className="innerBoxIcon" src={stack} alt={textReplace(KEY.no_commitment)} />
									<div className="separator"></div>
									<strong>{textReplace(KEY.no_commitment)}</strong>
									<p className="dash-separator">_____</p>
									<div className="separator"></div>
									<p>{textReplace(KEY.free_subscription)}</p>
								</div>
							</div>
						</div>
					</div>
					<div className="viaContainer">
						<div className="via-image">
							<img src={this.state.landingImage} alt={textReplace(KEY.listen_anytime)} />
						</div>
						<div className="via-content">
							<div className="listenHeading">
								<span>{textReplace(KEY.listen_anytime)}</span> </div>
							<p className="para-bold">{textReplace(KEY.via)} BN.COM</p>
							<p> {textReplace(KEY.listen_your_purchased_books)} <br /><span><strong>{textReplace(KEY.account_library)}</strong></span>
							</p>
							<p className="para-bold">{textReplace(KEY.via_nook_app)}</p>
							<p >{textReplace(KEY.listen_offline)}</p>
						</div>
					</div>
					<div className="wantToBuyContainer">
						<div className="without-subscription">
							<span>{textReplace(KEY.interested_in_audiobooks)}</span></div>
						<div className="noProblem">{textReplace(KEY.browse_titles)}</div>
					</div>
					<div className="audioBookList">
						<h2>{textReplace(KEY.browse_audio_books)}</h2>
						<ListContainer noSortMenu={true} showSort={false} listId={this.subscriptionListId} onDemandLoad={false} subscription={true} />
						<div className="browse-container">
							<button className="browseButton" onClick={this.browseAudiobooks}><h3>{textReplace(KEY.browse_audio_books)} </h3></button>
						</div>
					</div>
					<div className="faqs-container">
						<div className="faq-icon-container">
							<img className="faq-icon" src={chat} alt={textReplace(KEY.have_questions)} />
						</div>
						<div className="faq-question"><span>{textReplace(KEY.have_questions)}</span></div>
						<div className="faq-text">
							<span>
								{textReplace(KEY.call_us)} <br />
								<u><b>1-212-123-4567</b></u> {textReplace(KEY.review_faq)}
							</span>
						</div>
						<div className="faq-read" onClick={this.launchFAQs}>
							<span>
							{textReplace(KEY.read_faq)}
							</span>
						</div>
					</div>

				</div>
		);
	}
}

export default AudiobookSubscription
