/**
 * Internationalization library
 * Currently supports English (en.js) and Spanish (es.js)
 **/
import en from './constants/languages/en';
import es from './constants/languages/es';
import platforms from '../lib/constants/platforms'

const textReplace = key => {
  let lang = (navigator.language || 'en-US').replace(/-.*/g, '');
  lang = window.location.href.match(platforms.ios) ? lang : "en"; //es is supported only for iOS

  let strings = '';
  switch(lang) {
    case 'en':
      strings = en;
      break;
    case 'es':
      strings = es;
      break;
    default:
      strings = en;
  }

  if(!strings[key]) {
    console.log("Key not found");
  }

  return strings[key] || '';
};

export default textReplace;


