import React, { Component } from 'react'
import storeCodes, { storeCodeMap, categoryStores } from '../lib/constants/storeCodes'
import StoreCategories from './StoreCategories'


class BrowseCategories extends Component {

  constructor(props) {
    super(props);

    // State for this component only, not shared with others
    this.state = {
      isFetchingCategories: false, // Control the loading animation
    }

    this.categoryStores = storeCodes.filter((store, idx) => categoryStores.indexOf(idx) >= 0 && categoryStores.indexOf(idx) <= storeCodeMap.digaud);
    
    // Children can only see kids categories
    if (this.props.customer.childProfile) {
      this.categoryStores = [storeCodes[storeCodeMap.kids]];
    }
    // Bind methods
    window.addEventListener('onScrollToPage', this.setSubCategoriesHeight);
  }

  setSubCategoriesHeight(e) {
    if (window.subCategories && !window.location.href.match("filter")) {
      const wrapper = document.getElementById('shop-wrapper');
      let landingHeight = wrapper.clientHeight - 50;
      window.subCategories.style.height = (landingHeight * e.detail.page) + "px";
      wrapper.scrollTop = (e.detail.page - 1) * landingHeight;
    }
  }


  componentWillMount() {

    // Display the native back button when in categories
    window.Device.modifyHeader({ enableBack: true });
    setTimeout(() => {
      window.Device.modifyHeader({ enableBack: true }); // this is required for few android devices
    }, 1000);

    // Always start at the top of the page
    var position = sessionStorage.getItem("categoriesPosition") || 0;
    window.scroll(0, position);
    sessionStorage.setItem("showCategoriesList", sessionStorage.getItem("showCategoriesList") || "list");
  }

  componentDidMount() {
    // Always start at the top of the list
    var position = sessionStorage.getItem("categoriesPosition") || 0;

    if (!this.props.noAnalytics) {
      window.scroll(0, 0);
    }

    let interval = setInterval(() => {
      if (document.readyState === 'complete') {
        clearInterval(interval);
        window.scroll(0, position);
      }
    }, 300);
    
    sessionStorage.removeItem('categoriesPosition');
  }

  componentWillUnmount() {
    // Store the last scroll position so we always come back to it
    sessionStorage.setItem('categoriesPosition', window.scrollY);
  }

  renderStoreCategories() {
    let currentStore = storeCodes[storeCodeMap[this.props.customer.childProfile
       ? (window.SHOW_AUDIOBOOKS ? "kidsdigaud" : "kids") : "nkbk"]] || null

    return <StoreCategories
      childProfile={this.props.customer.childProfile}
      currentStore={ currentStore }
      currentDref={ (!this.props.isInFavoriteCategoriesPage ? this.props.match.params.catDref : null) }
      isInFavoriteCategoriesPage={ this.props.isInFavoriteCategoriesPage || false }
      addOrRemoveItem={ this.props.addOrRemoveItem }
      selectedFavCategories={ this.props.selectedFavCategories }
      drefHash={ this.props.data.drefHash }
      isFetching={ this.state.isFetchingCategories}
      setStoreCategories={this.props.setStoreCategories} />;
  }


  render() {
    return this.renderStoreCategories();
  }
}

export default BrowseCategories;



