import React, { Component } from 'react'
import loadingBg from '../../images/e9e9e9.png'
import loadingSquareBg from '../../images/e9e9e9_square.png'
import defaultBook from '../../images/default_book.png'
import defaultMagazine from '../../images/default_magazine.png'
import defaultNews from '../../images/default_news.png'
import platforms from '../lib/constants/platforms'

import '../../style-dist/components/ItemCover.css'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'
if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/ItemCover.epd.css')
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/ItemCover.quill.css')


class ItemCover extends Component {

  constructor() {
    super();

    this.BACKUP_IMAGE_TEMPLATE = 'https://img1.nook.com/p/EAN_SIZE.jpg';
    // Keep the image source here as state for convenience
    this.state = {
      imageSrc: '',
      isImageEmpty: false
    };
  }


  //
  // Preload the cover image while showing a default image.
  // 
  // Image server documentation:
  //   https://pawiki.barnesandnoble.com/confluence/display/CT/Phantom+Image+Consumption+Specification
  // 
  componentWillMount() {
    this.preload(this.props.eanObj);
  }


  componentWillUpdate(nextProps, nextState) {
    // Make sure we change the image src if the component is updated with a new src
    if (nextProps.eanObj.ean !== this.props.eanObj.ean) {
      this.preload(nextProps.eanObj);
    }
  }


  componentWillUnmount() {

    // Don't set state after the component is un-mounted
    this.preloadImage.onload = undefined;
  }


  preload(eanObj) {
    const that = this;
    if (!eanObj) {
      return;
    }

    let src;
    if (!eanObj.images || !eanObj.ean) {
      // eanObj is malformed, probably still loading the data
    }
    else {
      let imageSize = window.Device.getMinDimension() < 500 ? 's600' : 's600';

      // Greyscale images for EPD
      if (window.location.href.match(platforms.epd)) {
        imageSize = 's240x360g';
      }

      // Greyscale images for quill
      if (window.location.href.match(platforms.quill)) {
        imageSize = 's240x360g';
      }

      if (eanObj.images.ProductPageBookCover && eanObj.images.ProductPageBookCover.path) {
        src = eanObj.images.ProductPageBookCover.path.replace('s600', imageSize).replace('http:', 'https:');
        src = src.replace('https://img', 'https://simg');
        // src = src.replace(/img\d.imagesbn.com/, 'img1.nook.com');
      }
      else {
        src = this.BACKUP_IMAGE_TEMPLATE.replace('EAN', eanObj.ean).replace('SIZE', imageSize);
      }

      // Grayscale image for EPD
      if (window.location.href.match(platforms.epd)) {
        src = src.replace(/_s\d.*_/, '_s240x360g_');
      }

      // Grayscale image for quill
      if (window.location.href.match(platforms.quill)) {
        src = src.replace(/_s\d.*_/, '_s240x360g_');
      }

    }

    this.preloadImage = new Image();
    if (eanObj.images && eanObj.images.ProductPageBookCover && eanObj.images.ProductPageBookCover.path === '') {
      this.setState({ isImageEmpty: true });
    }
    if (eanObj.images && eanObj.ean) {
      this.preloadImage.src = src;
      this.preloadImage.onload = () => {
        that.setState({ imageSrc: src });
      }
      this.preloadImage.onerror = () => {
        const defaultImage = that.getErrorDefault(eanObj);
        this.setState({ isImageEmpty: true });
        that.setState({ imageSrc: defaultImage });
      }
    }
    this.setState({ imageSrc: window.SHOW_AUDIOBOOKS ? loadingSquareBg : loadingBg });
  }


  getErrorDefault(eanObj) {
    const subType = eanObj.productSubType;
    switch (subType) {
      case 'BA':
      case 'B7':
      case 'BF':
      case 'BG':
        return defaultBook;
      case 'B8':
        return defaultNews;
      case 'B9':
        return defaultMagazine;
      default:
        return defaultBook;
    }
  }


  render() {
    let bookTitleAuthor = '';
    if (this.props.eanObj.title !== undefined) {
      bookTitleAuthor = `${this.props.eanObj.title} ${textReplace(KEY.by)} ${this.props.eanObj.author}`;
    }

    if (!this.state.isImageEmpty) {
      return (
        <img className="cover"
          src={this.state.imageSrc}
          alt={bookTitleAuthor} title="bookTitleAuthor" />
      );
    }
    else {
      return (
        <svg className="cover" height="190px" width="130px" >
          <foreignObject width="100%" height="100%" >
            <div className="title-container">
              <p className="title ellipses lines-2" >{this.props.eanObj.title}</p>
              <p className="author ellipses lines-1">{this.props.eanObj.author}</p>
            </div>
          </foreignObject>
        </svg>
      )
    }
  }
}

export default ItemCover;



