class locale {

  // constructor: () ->
  //   @symbols = {}


// THESE WERE ALL CALLED IN SESSION
  // setUILocale: (@localeUI) ->
  //   unless L10N.haveTranslations @normalizeLocale(@localeUI,'-')
  //     @localeUI = @defaultLocale @localeUI.slice(-2)
  //   L10N.switchTo @localeUI
  //   @localeUI


  // setAppLocale: (@locale) ->
  //   @country = @locale.slice(-2)
  //   @setSymbols()
  //   @locale


  // get: (property) => 
  //   @symbols[property]


  // setSymbols: () ->
  //   @symbols = 
  //     currencySymbol: if @country is "US" then '$' else '£'

  defaultLocale(country) {
    switch (country) {
      case "US":
        return "en_US";
      case "GB":
        return "en_GB";
      default:
        return "en_US";
    }
  }


  determineRetailer(country) {
    return country === "US" ? "BN2" : "NOK";
  }


  determineMerchandisingLocale(country, locale) {
    // locale is not used for US/GB but is needed
    // for multi-lingual countries like CA, CH, etc
    return this.defaultLocale(country);
  }


  normalizeLocale(locale, separator = "_") {
    let parts = locale.split(/[_-]/);
    if (parts[0] !== 'en') {
      parts[1] = parts[0].toUpperCase();
    }   
    parts.join(separator);
  }
}

export default new locale();


