import React, { Component } from 'react'
import platforms from '../lib/constants/platforms'
import CustomDropdown from './CustomDropdown.js'

import '../../style-dist/components/ListSort.css'
import textReplace from '../lib/i18n';
import KEY from '../lib/constants/languages/keys';
if (window.location.href.match(platforms.epd)) {
  require('../../style-dist/components/ListSort.epd.css');
  require('../../style-dist/loading.epd.css');
}
if (window.location.href.match(platforms.quill)) {
  require('../../style-dist/components/ListSort.quill.css');
  require('../../style-dist/loading.epd.css');
}

class ListSort extends Component {

  constructor(props) {
    super(props);

    this.dictionary = [];
    if (this.props.hideDefault !== true) {
      this.dictionary.push({ value: "", label: textReplace(KEY.default) });
    }
    this.dictionary.push(
      { value: "r", label: textReplace(KEY.top_matches) },
      { value: "sa", label: textReplace(KEY.best_sellers) },
      { value: "dd", label: textReplace(KEY.newest_oldest) },
      { value: "da", label: textReplace(KEY.oldest_newest) },
      { value: "pa", label: textReplace(KEY.price_low_high) },
      { value: "pd", label: textReplace(KEY.price_high_low) },
      { value: "ta", label: textReplace(KEY.title_a_z) },
      { value: "td", label: textReplace(KEY.title_z_a) }
    );

    // Bind Methods
    this.sortList = this.sortList.bind(this);
    this.filterList = this.filterList.bind(this);
    this.changeGrid = this.changeGrid.bind(this);
  }


  render() {
    const options = this.dictionary.map( (o, i) => <option key={i} value={o.value}>{o.label}</option> );
    return <div className="list-sort">
      <CustomDropdown
          showSort={ this.props.showSort }
          hasTopList={ this.props.hasTopLists }
          dictionary={ this.dictionary }
          value={ this.props.sortKey }
          audFilterValue={ this.props.audioFilter }
          title={ this.props.title }
          suffix={ this.props.suffix }
          onChange={ this.sortList }
          onChangeFilter={ this.filterList } //audiobook filter
          showGrid={ sessionStorage.getItem('showList') !== 'list' }
          onChangeGrid={ this.changeGrid }
          showAudFilter={ this.props.showAudFilter }
          ebookOptions={ options }>
        { options }
      </CustomDropdown>
    </div>
  }

  sortList(evt) {
    this.props.onChange(evt.target.value);
  }

  filterList(evt) {
    this.props.onChangeFilter(evt.target.value);
  }

  changeGrid(evt) {
    this.props.onChangeGrid(evt.target.value);
  }
}

export default ListSort;


