import { connect } from 'react-redux'
import actionTypes from '../lib/constants/actionTypes'
import ManageFavCategories from '../components/ManageFavCategories'

const mapStateToProps = (state, ownProps) => {
    return {
      customer: state.sessionData.customer,
      device: state.sessionData.device,
      data: state.favCategoriesData,
    }
  }
  
  
  const ManageFavCategoriesContainer = connect(
    mapStateToProps
  )(ManageFavCategories);

  export default ManageFavCategoriesContainer;


