import React, { Component } from 'react'
import { filterList } from '../lib/utils'
import platforms from '../lib/constants/platforms'
import ShopRetry from './ShopRetry'
import ShopSlider from './ShopSlider'
import MultiItemPromo from './MultiItemPromo'
import MultiItemSpreadPromo from './MultiItemSpreadPromo'
import SingleItemPromo from './SingleItemPromo'
import SubscriptionPromo from './SubscriptionPromo'

import '../../style-dist/components/F2Pods.css'

if (window.location.href.match(platforms.epd)) {
  require('../../style-dist/components/F2Pods.epd.css');
  require('../../style-dist/loading.epd.css');
}

if (window.location.href.match(platforms.quill)) {
  require('../../style-dist/components/F2Pods.quill.css');
}



class F2Pods extends Component {

  constructor() {
    super();

    // Define an empty pod to show during loading
    this.loadingPod = [{
      eanData: {},
      display_order: 0,
      html_template_name: 'audioPromotionSingle',
      data: {
        eans: {eans: [null]},
        action_label: ''
      }
    }];
    this.isEPD = window.location.href.match(platforms.epd) || window.location.href.match(platforms.quill);
    // Bind methods
    this.onClickMulti = this.onClickMulti.bind(this);
    this.onClickSingle = this.onClickSingle.bind(this);
    this.handleResize = this.handleResize.bind(this);
  }

  componentDidMount() {
    if (!this.isEPD) {
      this.handleResize();
    }
  }

  handleResize() {
    window.addEventListener('resize', function() { location.reload(); }, false);
  }
  // All pods boil down to either single or multi (for now at least)
  renderPod(pod, i) {
    let eanData = this.props.eanData;
    let filteredEANs = [];
    let filteredListOfListEANs = [];
    if (pod.data) {
      if (pod.data.content_property === 'list' && pod.data.list) {
        filteredEANs = filterList(pod.data.list, eanData)[0];
      } else if (pod.data.content_property === 'list_of_lists' && pod.data.list_of_lists.lists) {
        for (let i = 0; i < pod.data.list_of_lists.lists.length; i++) {
          // filter through each one of the list
          filteredListOfListEANs = filterList(pod.data.list_of_lists.lists[i], eanData)[0];
          // add only first element from the filtered array
          filteredEANs.push(filteredListOfListEANs[0]); 
        }
      }
    }
    let order = pod.display_order;
    pod.index = i+1;
    const isLCD = window.Device.shopPlatform === platforms.android || window.Device.shopPlatform === platforms.ios;
    const tncInfo = {
      list_cloud_id: pod.data.list ? pod.data.list.list_cloud_id : '',
      show_tnc: pod.flags ? pod.flags.show_tnc && isLCD : false,
      tnc: pod.tnc
    }
    const podCoverLimit = 6;

    if (filteredEANs && filteredEANs.length > podCoverLimit) {
      //remove duplicates
      filteredEANs = filteredEANs.filter(function (item, pos) {
        return filteredEANs.indexOf(item) == pos;
      })
      // slice off the eans greater than 6 to avoid display issues.
      filteredEANs = filteredEANs.slice(0, podCoverLimit);
    }
    switch(pod.html_template_name) {
      case 'multiItemSpreadPromo':
        return <MultiItemSpreadPromo key={ order } tnc={ tncInfo } pod={ pod } filteredEans={ filteredEANs } eanData={ eanData } onClick={ this.onClickMulti } />;
      case 'multiItemPromo':
        return <MultiItemPromo key={ order } tnc={ tncInfo } pod={ pod } filteredEans={ filteredEANs } eanData={ eanData } onClick={ this.onClickMulti } />;
      case 'singleItemPromo':
        return <SingleItemPromo key={ order } tnc={ tncInfo } pod={ pod } eanData={ eanData } onClick={ this.onClickSingle } />;
      case 'singleItemNoPricePromo':
          return <SingleItemPromo key={ order } tnc={ tncInfo } pod={ pod } eanData={ eanData } onClick={ this.onClickSingle } />;
      case 'audioPromotionSingle':
        return <SubscriptionPromo key={ order } tnc={ tncInfo } pod={ pod } eanData={ eanData } onClick={ this.onClickPromo }/>;
    }
  }


  render() {
    let loading = false;
    let f2Data = this.props.data;
    let data = f2Data.data;
    if (this.props.isFetchingF2 || !data) {
      loading = true;
      data = this.loadingPod;
    }
    const loadingAriaAttribs = {
      "aria-busy": true, "aria-live":"off"
    };
    // Render the retry button
    if (!loading && data && !data.length) {
      return <div className="f2-pods"><ShopRetry status={ f2Data.status } /></div>
    }

    this.setTnCSession(data);

    let f2s = data.map(this.renderPod, this);
    let f2PodPosition = sessionStorage.getItem('f2PodPosition') || 0;
    return <div  {...(loading ? loadingAriaAttribs : {"aria-live":"off"})} className={ 'f2-pods ' + (this.isTab ? 'tab' : '') + (loading ? ' loading' : '') }>
      <ShopSlider index={ f2PodPosition }>
        { f2s }
      </ShopSlider>
    </div>;
  }


  onClickMulti(pod) {
    const shopPlatform = window.Device.shopPlatform;
    const list = pod.data.list || pod.data.list_of_lists;
    if (!list) {
      return console.error('Error: Pod does not open a list.', pod.data);
    }
    const listId = list.list_cloud_id ? list.list_cloud_id.toString() : null;

    // Send analytics data
    this.props.onPodClick({
      f2Title: pod.data.title,
      f2Position: pod.index // (Added above)
    });

    // Load list view
    if (pod.data.content_property === "list_of_lists" && pod.data.list_of_lists) {
      window.location.hash = `/${shopPlatform}/listoflists/${pod.data.list_of_lists.list_ids.join(',')}/${pod.data.title}`
    } else if (listId) {
      window.location.hash = `/${shopPlatform}/list/${listId}`;
    }
  }


  onClickSingle(pod) {
    if (!pod.data.ean) {
      return console.error('Error: Pod does not have an ean field.');
    }
    const ean = pod.data.ean.ean;
    const eanObj = this.props.eanData[ean];


    // Send analytics data
    this.props.onPodClick({
      promotionEan: eanObj.ean,
      f2Title: pod.data.title,
      f2Position: pod.index // (Added above)
    });

    // Open the native PDP view for the clicked EAN
    window.Device.showProductDetails(eanObj.title, eanObj.ean);
  }

  onClickPromo(pod) {

    // switch to onClickSingle if singleItem promo
    if (pod.data.content_type === "single_item") {
      return this.onClickSingle(pod);
    }


    let url = null;
    if (pod.data && pod.data.url) {
      url = pod.data.url.trim();
      window.Device.launchUrl({url: url});
    }
  }

  setTnCSession(data) {
    let tncInfo = [];
      tncInfo = data.filter(eachPod => eachPod.flags)
        .map(eachPod => (
          {
            list_cloud_id: eachPod.data.list ? 
              eachPod.data.list.list_cloud_id : 
              eachPod.data.list_of_lists ? eachPod.data.list_of_lists.list_ids : '',
            show_tnc: eachPod.flags.show_tnc,
            tnc: eachPod.tnc
          }))

          if (tncInfo.length > 0) {
            try {
              localStorage.setItem('showTnC', JSON.stringify(tncInfo));
            } catch (e) {
              console.log(e)
            }
          }
  }

}

export default F2Pods;



