import actions from '../lib/constants/actionTypes';

const initialState = {
  currentPage: 1
}

const epdData = (state = initialState, action) => {
  let stateCopy = Object.assign({}, state);

  switch (action.type) {
    case actions.SET_EPD_PAGE:
      stateCopy.currentPage = action.currentPage;
      return stateCopy;
    
    default:
      return state;
  }
}

export default epdData


