import React, { Component } from 'react'
class ContainerFiller extends Component {
    props;
    constructor(props) {
        super(props);
        this.props = props;
    }
    render() {
        return(<div className={ `${this.props.fillContainer} filler` }></div>)
    }
}
export default ContainerFiller;


