import storeCodes, { storeCodeMap } from '../lib/constants/storeCodes'
import fileNames from './constants/dref_image_map.json'
import textReplace from './i18n'
import KEY from './constants/languages/keys'

/*
    Filter function for individual list.
    Lookup eans object array against the List based on below filter conditions.
        - Filter out eans which has price greater than list's maxPrice
        - Filter out eans whose available date is less than today's Date, if comingSoon is true for the list
        - Filter out eans based on listType ie., 3-way flag ('Mixed', 'eBook only', 'Audiobook only')
        - Filter freeList set to 'true' and all eans with ean.price === '0.00'
*/
const filterList = (listData, eanDataList) => {
  const currentDate = (Date.now() / 1000).toFixed(0);
  let eansArray = listData.eans;
  let filteredOutCount = 0;
  // Only F4 lists needs to go through filterList logic. Recommendations are
  // synthetic list (F1List) in SHOP perspective as they are derived from native
  // itemIsInLibrary()

  if (listData.listCloudId === 'rec_from_ract_pod') {
    return [eansArray, filteredOutCount]
  }

  if(!eansArray || !Array.isArray(eansArray)) {
    return [[], 0];
  }
  eansArray = eansArray.filter(ean => {
    let eanData = eanDataList ? eanDataList[ean] : null;
    if (!eanData) { filteredOutCount += 1; return false;  }

    const listMaxPrice = Number(listData.maxPrice ? listData.maxPrice : 0);
    //If no listformattype just bypass it otherwise check the type. for each type check if the available formats matches.
    const displayAllFormat = !listData.listFormatType ||
                                listData.listFormatType === 'Mixed';
    const displayAudioOnly = !listData.listFormatType ||
                                (listData.listFormatType === 'Audiobook only' && eanData.productType === 'AUDIOBOOK');
    const displayEbookOnly = !listData.listFormatType ||
                                (listData.listFormatType === 'eBook only' && eanData.productType !== 'AUDIOBOOK');
    // if mixed and has ER/FA -> show mixed eans in the list
    // const isMixedFormat = eanData && (!displayAllFormat || (displayAllFormat && !isOnlyFormatType));
    const isValidFormat = displayAllFormat || displayAudioOnly || displayEbookOnly;
    const isValidPrice = listMaxPrice === 0 || eanData.price <= listMaxPrice;
    const isValidComingSoon = !listData.comingSoon || listData.comingSoon === 'false' || (listData.comingSoon === 'true' && (eanData.availableDate && eanData.availableDate > currentDate));
    const isFreeList = listData.freeList === 'true' ? true : false;
    const isFreeListValidated = !isFreeList || (isFreeList && (eanData.price === 0 || eanData.price === '0.00'));
    let isValid = isValidPrice && isValidComingSoon && isValidFormat && isFreeListValidated;

    if (!isValid) { filteredOutCount += 1; }
    return isValid;

  });
  return [eansArray, filteredOutCount]
}

const kidsStoreCode = () => {
  return window.SHOW_AUDIOBOOKS ? storeCodes[storeCodeMap.kidsdigaud].code :
    storeCodes[storeCodeMap.kids].code;
}

