const getTitle = (isChildProfile) => {
  const isAudiobooks = window.SHOW_AUDIOBOOKS;
  let title = '';
  if (isChildProfile) {
    title = isAudiobooks ? constants.KIDS_AUDIOBOOKS : constants.KIDS_EBOOKS;
  }
  else {
    title = isAudiobooks ? constants.AUDIOBOOKS : constants.EBOOKS;
  }
  return title;
}

const arrangeF6inF4List = (f4Data, f6Data) => {

  if(f4Data && f4Data.lists) {
    if(f4Data.lists[0]) {
      f4Data.lists[0].type = constants.PERFECT_COVER
    }
    if(f4Data.lists[3]) {
      f4Data.lists[3].type = constants.TWO_ROWS
    }
  }

  if (f6Data && f6Data.data && f6Data.data.length > 0) {

    let reOrderedF6 = reOrder(f6Data.data); //sort based on position

    for (let i = 0; i < reOrderedF6.length; i++) {
      let f6 = reOrderedF6[i];
      f6.type = constants.F6;
      f4Data.lists.splice(Number(f6.position - 1), 0, f6);
      f4Data.eans = { ...f4Data.eans, ...f6Data.eans }
    }
  }

  return f4Data;
}

const getCurrentCategories = (response, isChildProfile, isAudiobook) => {
  if (isAudiobook) {
    return isChildProfile ? response.data.kids.audiobook : response.data.adult.audiobook;
  }
  else {
    return isChildProfile ? response.data.kids.ebook : response.data.adult.ebook;
  }
}

const getParentsFromAllCategories = (allCategories) => {
  let parentCats = [];
  if (allCategories) {
    let keys = Object.keys(allCategories);
    for (let i = 0; i < keys.length; i++) {
      let record = allCategories[keys[i]];
      if (!record.parent) {
        parentCats.push(record);
      }
    }
  }
  return parentCats;
}

const reOrder = (unOrderedList, custom) => {
  const temp = []
  for (let key in unOrderedList) {
    temp.push(unOrderedList[key]);
  }
  let ordered = [];
  
  if(custom === 'label') {
    ordered = temp.sort(function (a, b) {
      if (a.label < b.label) {
        return -1;
      }
      if (a.label > b.label) {
        return 1;
      }
      return 0;
    });
  }
  else{
    ordered = temp.sort((a, b) => a.position - b.position)
  }
  return ordered;
}

const isTab = () => {
    return window.innerWidth > 480;
}

const hrsUntilMidnight = () => {
  const date = new Date();
  date.setHours(23, 59, 59);
  return Math.ceil((date.getTime() - Date.now()) / 1000 / 60 / 60);
}

const getImageName = (arg, getFileName, categories) => {
  if(!arg) {
    return; // return if no category is passed.
  }

  let category = fileNames.find(a =>  (a.dref && a.dref.find(a => a == arg.id)) || a.name === arg.label);
  if (category) {
    getFileName(category.fileName, textReplace(category.key));
  }
  else if (categories) {
    // console.log(typeof value === 'object' ? value : JSON.parse(value))
    // const categories = getCurrentCategories(typeof value === 'object' ? value : JSON.parse(value), isChildProfile);
    const allCats = { ...categories.allCategories }
    let found = false;

    const getImage = (arg, visitedCategories = new Set()) => {
      visitedCategories.add(arg.id); // Add the current category to the visited set
      for (const id in allCats) {
        const cat = allCats[id];
        if (cat && cat.children && cat.children.find(a => a == arg.id)) {
          // Check if the child category has already been visited
          if (visitedCategories.has(cat.id)) {
            console.log("Cyclic redundancy detected!");
            return getFileName(constants.DEFAULT_SVG);
          }

          let fileNameObj = fileNames.find(a => (a.dref && a.dref.find(a => a == cat.id))  || a.name === cat.label);

          if (fileNameObj) {
            found = true;
            return getFileName(fileNameObj.fileName);
          }
          else {
            getImage(cat, visitedCategories); // Pass the visited set to the recursive call
          }
        }
      }
      visitedCategories.delete(arg.id); // Remove the current category from the visited set after the for loop is done
    }

    getImage(arg);
    if (!found) {
      return getFileName(constants.DEFAULT_SVG);
    }
  } else {
    getFileName(constants.DEFAULT_SVG);
  }
}

const getTextWidth = (text, font) => {
  const canvas = getTextWidth.canvas || (getTextWidth.canvas = document.createElement('canvas'));
  const context = canvas.getContext('2d');
  context.font = font;
  const metrics = context.measureText(text);
  return metrics.width;
}

const getCssStyle = (element, prop) => {
  return window.getComputedStyle(element, null).getPropertyValue(prop);
}

const getCanvasFontSize = (el = document.body) => {
  const fontWeight = getCssStyle(el, 'font-weight') || 'normal';
  const fontSize = getCssStyle(el, 'font-size') || '16px';
  const fontFamily = getCssStyle(el, 'font-family') || 'Times New Roman';

  return `${fontWeight} ${fontSize} ${fontFamily}`;
}

const getElementPadding = (element) => {
  let leftPaddingValue = getCssStyle(element, 'padding-left') || '0px';
  leftPaddingValue = Number(leftPaddingValue.replace("px", ""));
  let rightPaddingValue = getCssStyle(element, 'padding-right') || '0px';
  rightPaddingValue = Number(rightPaddingValue.replace("px", ""));
  return leftPaddingValue + rightPaddingValue;
}

// return sum of margin
const getElementMargin = (element) => {
  let leftMarginValue = getCssStyle(element,'margin-left') || '0px';
  leftMarginValue = Number(leftMarginValue.replace("px", ""));
  let rightMarginValue = getCssStyle(element,'margin-right') || '0px';
  rightMarginValue = Number(rightMarginValue.replace("px", ""));
  return leftMarginValue + rightMarginValue;
}


const addEllipsis = (text) => {
  let topCatsPadding = getElementPadding(text);
  let topCatsMargin = getElementMargin(text.parentNode.parentNode);
  let exploreCatsParentPadding = getElementPadding(text.parentNode);
  let isWrap = false;

  if (text) {
    let words = text.innerText.trim().split(" ");

    if (words.length > 1) {
      let word = text.innerText.trim().split(" ")[0];
      let textWidth = getTextWidth(word, getCanvasFontSize(text));
      let linkWidth = text.offsetWidth - topCatsPadding - topCatsMargin - exploreCatsParentPadding;
      let charWidth = getTextWidth("A", getCanvasFontSize(text)); //subtract a char from linkwidth for precision
      if (parseInt(textWidth, 10) >= (parseInt(linkWidth - charWidth, 10))) {
        isWrap = true;
      }
    }
    if (!isWrap) {
      text.classList.remove("lines-1");
      text.classList.add("lines-2");
    }
    if (words.length == 1 || isWrap) {
      text.classList.remove("lines-2");
      text.classList.add("lines-1");
    }
  }
};

const getRefinementType = (storeCode, defaultStoreCode) => {
  return (storeCode === storeCodes[storeCodeMap.kidsdigaud].code
    || storeCode === storeCodes[storeCodeMap.kids].code
     || (storeCode === storeCodes[storeCodeMap.enewsstand].code)) ? storeCode : defaultStoreCode;
}

const getAssemblePath = (path, httpMethod) => {
  if(path == '/categories/favorite' && httpMethod === 'get'){
    return 'assemble/rest/v2';
  }
  else if (path == "/f6" || path == "/categories/all"
    || path == "/shop/category/a10" || path == '/categories/favorite') {
    return 'assemble/rest/v1';
  }
  else {
    return 'assemble/v1';
  }
}

const isSameObject = (oldArray, newArray) => {
  //Sometimes the order of properties changes so we have to sort it before comparing 
  //the objects.
  const sortedOldArray = getSortedArrayObject(oldArray);
  const sortedNewArray = getSortedArrayObject(newArray);

  return JSON.stringify(sortedOldArray) === JSON.stringify(sortedNewArray)
}

const getSortedArrayObject = (arr) => {
  const sortedArray = [];

  for (let obj of arr) {
    const sortedKeys = Object.keys(obj).sort();
    const sortedObj = {};

    for (let key of sortedKeys) {
      sortedObj[key] = obj[key];
    }
    sortedArray.push(sortedObj);
  }
  return sortedArray;
}

export {
  filterList, kidsStoreCode, constants, getTitle, arrangeF6inF4List,
  getCurrentCategories, getParentsFromAllCategories, isTab, reOrder, hrsUntilMidnight, getImageName, 
  addEllipsis, getCanvasFontSize, getCssStyle, getTextWidth, getRefinementType, getAssemblePath,
  isSameObject
};



