//
// Redux actions to dispatch.
// Actions are the only way to modify the state (redux store).
// A reducer will update the state based on the "type" field returned by the action.
// 
import actionTypes from './constants/actionTypes'

//
// Synchronous
// 
const setFetchingAffinity = (isFetching) => {
  return {
    type: actionTypes.SET_FETCHING_AFFINITY,
    isFetching
  }
}


const setFetchingF1 = (isFetching) => {
  return {
    type: actionTypes.SET_FETCHING_F1,
    isFetching
  }
}


