import React, { PureComponent } from 'react'
import epdConstants, { categoryFilterHeight, quillCategoryFilterHeight } from '../lib/constants/epd'

import upArrow from '../../images/up_arrow.svg'
import downArrow from '../../images/down_arrow.svg'

import '../../style-dist/components/EpdControls.epd.css'

import platforms from '../lib/constants/platforms'
import { constants } from '../lib/utils'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/EpdControls.quill.css')


class EpdControls extends PureComponent {

  constructor() {
    super();

    this.startSwipe = {};
    this.initSwipeScrolling();

    // Update the total page count every two seconds in case content demand loads
    window.setInterval(() => this.countPages(), 2000);

    const platform = window.Device.shopPlatform;
    this.LANDING_PAGE_HEIGHT = platform === 'epd' ? epdConstants.LANDING_PAGE_HEIGHT : epdConstants.QUILL_LANDING_PAGE_HEIGHT;
    this.PAGE_HEIGHT = platform === 'epd' ? epdConstants.PAGE_HEIGHT : epdConstants.QUILL_PAGE_HEIGHT; 
    this.CATEGORY_FILTER_HEIGHT = platform === 'epd' ? categoryFilterHeight : quillCategoryFilterHeight;

    this.state = {
      totalPages: 1,
      pageNo: 1,
      btnUpClass: 'showbtn',
      btnDownClass: 'showbtn'
    };

    window.paginationComponent = this;

    // Bind methods
    this.initSwipeScrolling = this.initSwipeScrolling.bind(this);
    this.pageDown = this.pageDown.bind(this);
    this.pageUp = this.pageUp.bind(this);
    this.handleClassUpArrow = this.handleClassUpArrow.bind(this);
    this.handleClassDownArrow = this.handleClassDownArrow.bind(this);
  }


  componentWillUpdate(nextProps, nextState) {
    // Scroll when the page changes
    if (this.props.pageNo !== nextProps.currentPage) {
      this.scrollToPage(nextState.pageNo);
    }
    // Scroll to first page
    else if (this.props.screenLocation !== nextProps.screenLocation || window.resetPageNo) {
      window.resetPageNo = false;
      window.sortChanged = true;
      this.setState((prevState, props) => {
        return { pageNo: this.props.pageNo }
      })
      this.scrollToPage('1');
    }

    if (this.state.pageNo > this.state.totalPages) {
      this.scrollToPage(this.state.pageNo);
      this.setState((prevState, props) => {
        return {pageNo: this.state.pageNo}
      })
    }

    // Fire global events to the app knows when we're "scrolling"
    // (used for demand loading)
    if (nextState.pageNo > this.state.pageNo) {
      const event = new CustomEvent('onEPDPageDown', {detail: {page: nextState.pageNo}});
      window.dispatchEvent(event);
    }
    else {
      const event = new CustomEvent('onEPDPageUp', {detail: {page: nextState.pageNo}});
      window.dispatchEvent(event);
    }
    if (nextState.pageNo === 1) {
      const event = new CustomEvent('onEPDFirstPage', {detail: {page: nextState.pageNo}});
      window.dispatchEvent(event);
    }
    // prefetch list from last 3rd page
    if ((nextState.totalPages > this.state.pageNo) && (nextState.totalPages - this.state.pageNo) === 3) {
      const event = new CustomEvent('onEPDLastPage', {detail: {page: nextState.pageNo}});
      window.dispatchEvent(event);
    }

  }

  initSwipeScrolling() {

    const body = document.body;
    const swiped = (e) => {      
      if (e.timeStamp - this.startSwipe.timeStamp < 3000) {
        const evt = e;
        const deltaY = evt.changedTouches[0].pageY - this.startSwipe.y;
        const deltaX = evt.changedTouches[0].pageX - this.startSwipe.x;
        // Neglect swpies which are under 45 degree angle and 100 length
        try {
          if (Math.abs(deltaY) >= 10 && Math.abs(deltaY/deltaX) > 1) {
            return deltaY / Math.abs(deltaY);
          }
        } catch (e) {
          // do nothing
        }
      }
      return 0;
    }

    body.ontouchstart = (e) => {
      e = e.timeStamp ? e : window.event; // EPD sucks
      this.startSwipe = {
        timeStamp: e.timeStamp,
        y: e.changedTouches[0].pageY,
        x: e.changedTouches[0].pageX
      }
    };

    body.ontouchcancel = (e) => {
      e = e.timeStamp ? e : window.event; // EPD sucks
      const direction = swiped(e);
      switch (direction) {
        case -1: this.pageDown(); break;
        case 1: this.pageUp(); break;
        default: return;
      }
    };

    body.ontouchend = (e) => {
      e = e.timeStamp ? e : window.event; // EPD sucks
      const direction = swiped(e);
      switch (direction) {
        case -1: this.pageDown(); break;
        case 1: this.pageUp(); break;
        default: return;
      }
    };
  }

  handleClassUpArrow() {
    // this.setState({ btnUpClass: 'hide'});
  }

  handleClassDownArrow() {
    // this.setState({ btnDownClass: 'hide'});
  }

  render() {
    const firstPageCustomDown = this.props.firstPageCustomDown;
    const categoryType = this.props.categoryType;
    const firstPageNav = firstPageCustomDown ? 'Recommended' : 'SeeAll'
    let seeAllCustom = this.props.seeAllCustom
    //const currentPage = this.state.pageNo;
    if(this.state.pageNo === this.state.totalPages && seeAllCustom) {
      seeAllCustom = '';
    }
    const currentPage = (firstPageCustomDown || seeAllCustom) && this.state.pageNo === 1 ? firstPageNav : this.state.pageNo;
    console.log('currentPage',currentPage);

    return <div ref={elt => window.epdControls = elt} className={(categoryType === 'selected-favorites' ? (categoryType + '-') : '') + 'epd-controls ' + 
    (window.location.href.match('favCategories') ? 'edit-fav-categories ' : '') + (categoryType ? categoryType : '')}>
      {
        (this.props.firstPageCustomDown && (currentPage == 1 || currentPage === 'Recommended')) || (seeAllCustom && currentPage === 'SeeAll') ? 
          <div className={this.props.noborder ? "noborder" : "border"}>
            { this.props.firstPageCustomDown && (currentPage == 1 || currentPage === 'Recommended') && !seeAllCustom ?
              <div className="custom page-down" onClick={ this.pageDown }>
                { this.props.firstPageCustomDown }
                <img alt={textReplace(KEY.down_arrow)} src={ downArrow } />
              </div>
              :
              <div className="seeall page-down" onClick={ this.pageDown }>
                { seeAllCustom }
                <img alt={textReplace(KEY.down_arrow)} src={ downArrow } />
              </div>
            }
          </div>
          :
          <div className={this.props.noborder ? "noborder" : "border"}>
            <div className={ 'arrow ' + (currentPage === 1 ? ' disabled' : '') }
                onTouchStart={ this.handleClassUpArrow } onTouchEnd={ this.pageUp }>
              <img className={'imgleft' + (currentPage === 1 ? ' disabled' : '') + ' ' + this.state.btnUpClass } alt={textReplace(KEY.up_arrow)} src={ upArrow } />
              <div className='arrow page-up' onClick={ this.pageUp } />
            </div>
            <div className="page-count">
              { currentPage } {textReplace(KEY.of)} { this.state.totalPages }
            </div>
            <div className={ 'arrow ' + (currentPage >= this.state.totalPages ? ' disabled' : '') }
                onTouchStart={ this.handleClassDownArrow } onTouchEnd={ this.pageDown }>
              <img className={'imgRight' + (currentPage >= this.state.totalPages ? ' disabled' : '')+ ' ' + this.state.btnDownClass } alt={textReplace(KEY.down_arrow)} src={ downArrow } />
              <div className='arrow page-down' onClick={ this.pageDown } />
            </div>
          </div>
      }
    </div>;
  }

  gotoPage(pageNo) {
    this.setState({ pageNo });
    this.scrollToPage(pageNo);
  }

  getPageNo() {
    return this.state.pageNo;
  }

  scrollToPage(page) {
    // console.log('page',page);
    const elem = this.getScrollableElement();
    const isCategoryList = window.location.hash.match(/categories.*\/list$/);

    let landingHeight = this.props.firstPageCustomDown ?
      this.LANDING_PAGE_HEIGHT :
      this.PAGE_HEIGHT;

    if (isCategoryList) {
      landingHeight -= this.CATEGORY_FILTER_HEIGHT;
    }

    if (!window.subCategories && !this.props.categoryType) {
      elem.scrollTop = ((page - 1) * landingHeight - (window.forMe ? 50 : 0));
    }

    if (isCategoryList) {
      elem.scrollTop = (page - 1) * this.PAGE_HEIGHT;
    }

    //else { // If subcategories are loaded, need to call logic that includes setting subcategories div height and also to set correct scroll position
      //to avoid seeing duplicate values in subsequent pages.
      const event = new CustomEvent('onScrollToPage', { detail: { page, categoryType: this.props.categoryType }});
      event.stopPropagation();
      window.dispatchEvent(event);
    //}
  }


  pageDown(e) {
    if (e) {
      e.preventDefault();
    }
    let page = this.state.pageNo;
    this.setState({ btnDownClass: 'showbtn'});
    let nextPage = page+1 > this.countPages() ? page : page+1;
    this.props.setPage(nextPage);
    if (page > this.countPages()) {
      this.setState((prevState, props) => {
        return {pageNo: this.countPages()};
      });
    }
    if (page < this.countPages()) {
      this.setState((prevState, props) => {
        return {pageNo: (Number(prevState.pageNo) + 1)};
      });
    }
  }


  pageUp(e) {
    if (e) {
      e.preventDefault();
    }
    let page = this.state.pageNo;
    this.setState({ btnUpClass: 'showbtn'});
    let previousPage = page > this.countPages() ? page-1 : page;
    this.props.setPage(previousPage);
    if (page > 1) {
      this.setState((prevState, props) => {
        return {pageNo: (prevState.pageNo - 1)};
      });
    }
  }


  countPages() {
    let scrollHeight = this.getScrollHeight();
    let totalPages;

    //Added this if condition to perform this only for subcategories and not to affect rest of the pagination logics.
    //parseInt to Eliminate value after decimal. If result is 5.03, 03 should not be used to create new page as it may create empty page at the end. 
    //However, we need 5.10 to 5.99. so just multiplied by 10, and then converted to whole number and divided by 10.
    //Math.ceil is used to round off to 1 even if totalPages has values between 0.10 to 0.99.
    totalPages = Math.ceil(parseInt((scrollHeight / (this.PAGE_HEIGHT)) * 10, 10) / 10);

    //totalPages = Math.round(scrollHeight / (this.props.firstPageCustomDown ? this.LANDING_PAGE_HEIGHT : this.PAGE_HEIGHT));
    this.setState({ totalPages });

    if (this.state.pageNo > totalPages) {
      this.setState({ pageNo: totalPages });
    }

    return totalPages;
  }


  getScrollHeight() {
    const elem = this.getScrollableElement();
    return Math.max(((elem == null || undefined) ? 0 : elem.scrollHeight), (elem == null || undefined) ? 0 : elem.offsetHeight);
  }

  
  getScrollableElement() {
    let container = document.getElementById('shop-wrapper');

    if (this.props.categoryType === constants.SELECTED_FAVS) {
      container = window.selectedCats;
      if (!window.favInProfile) {
        this.PAGE_HEIGHT = window.Device.shopPlatform === 'epd' ? 440 : 650;
      } else {
        this.PAGE_HEIGHT = window.Device.shopPlatform === 'epd' ? 492 : 704;
      }
    } else if (this.props.categoryType === constants.ALL_FAVS) {
      container = window.allCats;
      this.PAGE_HEIGHT = window.Device.shopPlatform === 'epd' ? 492 : 704;
    }
    else if(window.favInProfile) {
      container = window.favInProfileContainer;
    }
    return container;
  }
}

export default EpdControls;



