/**
 * The "For Me" tab loads personalized content for the user as well as the F2 
 * and F3 pods/links. This tab loads content from the Books (nkbk) department
 */
import React, { Component } from 'react'
import { fetchF2, fetchF3, fetchF4, fetchChannels, favoriteCategories } from '../lib/fetch'
import F2Pods from './F2Pods'
import F3Links from './F3Links'
import F4Lists from './F4Lists'
import ExploreFavCategories from './ExploreFavCategories'
import analytics from '../classes/Analytics'
import platforms from '../lib/constants/platforms'
import { screens, events, attributes } from '../lib/constants/analytics'
import browse from '../../../public/images/Union.svg'

import { Link } from 'react-router-dom'
import '../../style-dist/components/ForMe.css'
import textReplace from '../lib/i18n'
import KEY from '../lib/constants/languages/keys'

if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/ForMe.epd.css')
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/ForMe.quill.css')

class ForMe extends Component {

  constructor(props) {
    super(props);

    this.POD_CLICK_POS_KEY = 'shop-category-pod-clicked';

    // State for this component only, not shared with others
    this.state = {
      isFetchingF2: false, // Control the loading animation
      isFetchingF3: false, // Control the loading animation
      isFetchingF4: false, // Control the loading animation
      isFetchingF5: false,  // Control the loading animation
      isFetchingFavCategories: false, // Control the loading
    }

    this.isEpd = window.location.href.match(platforms.epd) || window.location.href.match(platforms.quill)
    this.shopHome = window.SHOW_AUDIOBOOKS ? "audiobook" : "ebook";
    this.selectedFavCategoriesKey = `selectedFavCategories_${this.props.customer.accountId}-${this.props.customer.profileId}-${this.shopHome}`;
    this.blurEvent = window.location.href.match(platforms.ios) ? "blur" : "visibilitychange";
    // Bind Methods
    this.initAnalytics = this.initAnalytics.bind(this);
  }


  componentWillMount() {
    // Hide the native back button
    window.Device.modifyHeader({ enableBack: false });
    setTimeout(() => {
      window.Device.modifyHeader({ enableBack: false }); // this is required for few android devices
    }, 1000);

    // Remove the stored "last category pod clicked" number from sessionStorage
    window.sessionStorage.removeItem(this.POD_CLICK_POS_KEY);
  }

  componentDidMount() {
    var position = sessionStorage.getItem("forMePosition") || 0;
    let device = this.props.device;
    let customer = this.props.customer;
    let urlParams = window.Device.isGreaterOrEqualsAudioBookVersion() ? {
      countryCode: customer.country,
      accountId: customer.accountId,
      profileId: customer.profileId,
      productDeviceId: device.productDeviceId
    }: {
      countryCode: customer.country,
      accountId: customer.accountId,
      profileId: customer.profileId,
      productDeviceId: device.productDeviceId
    };
    const data = this.props.data;

    if(localStorage.getItem(this.selectedFavCategoriesKey)) {
      this.props.setFavCategories(JSON.parse(localStorage.getItem(this.selectedFavCategoriesKey)));
    }
    
    // Only load content if not already loaded
    if (!data.f2.length || !data.f3.links.length || !data.f4.length) {
      this.loadContent(urlParams, {
        childProfile: customer.childProfile,
        cachedResponse: true
      });
    }

    // Analytics
    analytics.enter(window.SHOW_AUDIOBOOKS ? screens.audioBooks.shop_home : screens.ebooks.shop_home);
    this.initAnalytics();

    //componentdidmount, document.onreadystatechange both didn't work to find the load completion of all the elements of current document.
    //so used this timeout method.
    let interval = setInterval(() => {
      if (document.readyState === 'complete') {
        clearInterval(interval);
        window.scroll(0,position);
        if (this.isEpd) {
          let forMePageNo = sessionStorage.getItem('forMePageNo') || 1;
          window.paginationComponent.gotoPage(forMePageNo);
        }
        sessionStorage.removeItem("forMePosition");
        sessionStorage.removeItem("forMePageNo");
        sessionStorage.removeItem('lolPosition');
        sessionStorage.removeItem('EPDLolPosition');
      }
    }, 500);
    window.addEventListener(this.blurEvent, this.preserveForMePositionOnBlur);
  }

  componentWillUnmount() {

    // Store the last scroll position so we always come back to it
    //sessionStorage.setItem('forMePosition', window.scrollY);
    
    // Localytics Event
    this.sendAnalytics();

    window.removeEventListener(this.blurEvent, this.preserveForMePositionOnBlur);
  }

  preserveForMePositionOnBlur = () => {
    sessionStorage.setItem('forMePosition', window.scrollY);
  }


  initAnalytics() {
    this.analyticsEvent = Object.assign({}, events.shopHomeViewed);
    this.analyticsEvent.data = Object.assign({}, events.shopHomeViewed.data);
  }
  

  //
  // Fetch all the data for the "For Me" page
  // 
  loadContent(urlParams, options) {
    const props = this.props;

    // Load Pods
    this.setState({ isFetchingF2: true });
    fetchF2(urlParams, (err, resp) => {
      if (err) {
        console.error('Error fetching F2 pods.');
        resp = {eans:[], pods:[]};
      }
      this.setState({ isFetchingF2: false });

      const data = {
        data: resp.pods,
        eanData: resp.eans,
        status: resp.status
      };
      props.setF2(data); // Trigger a redux dispatch
    }, options);

    // Load Explore favorites
    this.setState({ isFetchingFavCategories: true });
      urlParams.shopHome = this.shopHome;
      favoriteCategories('get', urlParams, null, (err, resp) => {
        if (err) {
          console.error('Error fetching Favorite Categories.');
          resp = {favorites: []};
        }
        this.setState({isFetchingFavCategories: false});
        if(resp && resp.data) {
          let responseData = this.shopHome === "ebook" ? resp.data.ebook : resp.data.audiobook;
          if(!responseData) { // Remove this check once NOOKCLOUD-5665 goes to prod
            responseData = resp.data.favorites;
          }

          if (responseData) {
            props.setFavCategories(responseData); // Trigger a redux dispatch
          } else if(localStorage.getItem(this.selectedFavCategoriesKey)) {
            props.setFavCategories(JSON.parse(localStorage.getItem(this.selectedFavCategoriesKey)));
          }
        }
      });

    // Load Links (and categories)
    this.setState({ isFetchingF3: true });
    fetchF3(urlParams, (err, resp) => {
      if (err) {
        console.error('Error fetching F3 data.');
        resp = {eans:[], links:[]};
      }
      this.setState({ isFetchingF3: false });

      const data = {
        data: resp,
        eanData: resp.eans,
        status: resp.status
      };
      props.setF3(data); // Trigger a redux dispatch
    }, options);

    // Load List Carousels
    this.setState({ isFetchingF4: true });
    fetchF4(urlParams, (err, resp) => {
      if (err) {
        console.error('Error fetching F4 lists.');
        resp = {eans:[], lists:[]};
      }
      this.setState({ isFetchingF4: false });

      const data = {
        data: resp.lists,
        eanData: resp.eans,
        status: resp.status
      };
      props.setF4(data); // Trigger a redux dispatch
    }, options);

    // Load List Carousels
    this.setState({ isFetchingF5: true });
    urlParams = Object.assign(urlParams, {size: 12});
    fetchChannels(urlParams, (err, resp) => {
      if (err) {
        console.error('Error fetching channels items.');
        resp = {affinityLists:[], popularLists:[], eans: []};
      }
      else {
        this.setState({ isFetchingF5: false });
      }

      props.setF5(resp); // Trigger a redux dispatch
    }, options);
  }


  render() {
    const data = this.props.data;
    const eanData = this.props.eanData;
    var position = sessionStorage.getItem("forMePosition") || 0;

    return <div ref={el => window.forMe = el} className="for-me">
      <Link to={`/${window.Device.shopPlatform}/home/categories`} className='browse-categories'>
        <img src={browse} alt={textReplace(KEY.browse_categories)} />
        <div className='title'>
          {textReplace(KEY.browse_categories).toUpperCase()}
        </div>
      </Link>
      <div className='scrollable-element'>
        <F2Pods
          isFetchingF2={this.state.isFetchingF2}
          data={data.f2}
          eanData={eanData}
          onPodClick={data => this.prepareAnalytics('pod', data)} />

        <ExploreFavCategories
          data={data.favCategories}
          customer={this.props.customer}
          isFetchingFavCategories={this.state.isFetchingFavCategories}
          childProfile={this.props.customer.childProfile}
          onFavCatClick={data => this.prepareAnalytics('favoriteCategories', data)} />

        <F4Lists
          childProfile={this.props.customer.childProfile}
          isFetchingF4={this.state.isFetchingF4}
          data={data.f4}
          eanData={eanData}
          position={position}
          onCoverClick={data => this.prepareAnalytics('cover', data)}
          onListClick={data => this.prepareAnalytics('list', data)} />

        <F3Links
          childProfile={this.props.customer.childProfile}
          isFetchingF3={this.state.isFetchingF3}
          data={data.f3}
          affinities={this.props.affinities} // This is only the user's top 5 affinities at most
          eanData={eanData}
          onLinkClick={data => this.prepareAnalytics('link', data)} />
      </div>  
    </div>;
  }


  prepareAnalytics(type, data) {
    sessionStorage.setItem('forMePosition', window.scrollY);

    this.analyticsEvent.data[attributes.shopType] = window.Device.getShopType();;

    if(this.isEpd) {
      sessionStorage.setItem('forMePageNo', window.paginationComponent.getPageNo());
    }
    switch (type) {
      case 'pod':
        sessionStorage.setItem('f2PodPosition', data.f2Position-1);
        this.analyticsEvent.data[attributes.f2Title] = data.f2Title;
        this.analyticsEvent.data[attributes.f2Position] = data.f2Position;
        this.analyticsEvent.data[attributes.shopSection] = 'F2';

        // Single EAN pod opens PDP
        if (data.promotionEan) {
          this.analyticsEvent.data[attributes.promotionEan] = data.promotionEan;
          this.sendAnalytics(); // The componented doesn't unmount; call manually
        }
        break;

      case 'favoriteCategories':
        sessionStorage.setItem('favCatPosition', data.favCatPosition-1);
        this.analyticsEvent.data[attributes.favCatTitle] = data.favCatTitle;
        this.analyticsEvent.data[attributes.favCatPosition] = data.favCatPosition;
        this.analyticsEvent.data[attributes.shopSection] = 'Favorite Categories';
        this.sendAnalytics();
        break;

      case 'link':
        this.analyticsEvent.data[attributes.f3Title] = data.f3Title;
        this.analyticsEvent.data[attributes.f3Position] = data.f3Position;
        this.analyticsEvent.data[attributes.shopSection] = 'F3';
        break;

      case 'cover':
        this.analyticsEvent.data[attributes.listEanPosition] = data.listEanPosition;
        this.analyticsEvent.data[attributes.listTitle] = data.listTitle;
        this.analyticsEvent.data[attributes.listViewAllClicked] = 'No';
        this.analyticsEvent.data[attributes.listPosition] = data.listPosition+1;
        this.analyticsEvent.data[attributes.shopSection] = 'F4';
        this.sendAnalytics(); // The componented doesn't unmount; call manually
        break;

      case 'list':
        this.analyticsEvent.data[attributes.listTitle] = data.listTitle;
        this.analyticsEvent.data[attributes.listViewAllClicked] = 'Yes';
        this.analyticsEvent.data[attributes.listPosition] = data.listPosition+1;
        this.analyticsEvent.data[attributes.shopSection] = 'F4';
        break;
      default:
        console.error('Invalid analytics type. What did you do wrong??');
    }
  }


  sendAnalytics() {
    
    // Send the event
    analytics.exit(this.analyticsEvent);

    // Reset the event data since we'll come back from PDP
    this.initAnalytics();
  }
}

export default ForMe;



