import React, { Component } from 'react'
import { constants } from '../lib/utils'
import UnSupportedPod from './UnSupportedPod'
import '../../style-dist/components/SubscriptionPromo.css'
import platforms from '../lib/constants/platforms'

class SubscriptionPromo extends Component {
  constructor(props) {
    super(props);
    this.title = React.createRef();
    this.description = React.createRef();
    this.state = {
      inLibrary: {},
      imageSrc: this.getImageSrc(),
      isSvg: this.props.pod.data && this.props.pod.data.params && this.props.pod.data.params.toLowerCase().indexOf(".svg") !== -1
    };
  }

  getImageSrc() {
    let url = this.props.pod.data.params;
    if (!url) { return; }
    let deviceInnerWidth = window.innerWidth;
    this.isEPD = window.Device.shopPlatform === 'epd' || window.Device.shopPlatform === 'quill';
    if (deviceInnerWidth <= 599 && !this.isEPD)
      return url;
    else if ((deviceInnerWidth >= 600 && deviceInnerWidth <= 999) || this.isEPD)
      return url.replace("@1x","@2x");
    else if (deviceInnerWidth >= 1000 && deviceInnerWidth <= 1199)
      return url.replace("@1x","@3x");
    else if (deviceInnerWidth >= 1200)
      return url.replace("@1x","@4x");  
  }

  componentDidUpdate() {
    this.ellipsizeTextBox(this.title);
    this.ellipsizeTextBox(this.description);
  }

  componentDidMount() {
    //AN-13074//
    //This could have been done in a nicer way in css but since the title and description
    //has to occupy the available blank spaces in div to the max dynamically (desc and cta are optional), we are calculating the position to add ellipsis and 
    //omit the remaining characters.
    this.ellipsizeTextBox(this.title);
    this.ellipsizeTextBox(this.description);
    window.addEventListener('resize', () => {
      this.ellipsizeTextBox(this.title);
      this.ellipsizeTextBox(this.description);
      this.setState({imageSrc: this.getImageSrc()})
    });
  }

  ellipsizeTextBox(el) {
    if (el) {
      let trimmedString = el.innerHTML;
      while (el.scrollHeight > el.clientHeight) {
        trimmedString = el.innerHTML;
        el.innerHTML = trimmedString.replace(/(?!(<[^>]*>))\W*\s(\S)*$/, '...');
        if (el.innerHTML.length < 25) {
          break;
        }
      }
    }
  }

  getTextPosition() {
    let textPosition = this.props.pod.textPosition || '';
    if (textPosition === constants.BILLBOARD_TEXT_POSITION.NOTEXT) {
      textPosition = 'billboardNoText';
    } else if (textPosition === constants.BILLBOARD_TEXT_POSITION.ABOVE) {
      textPosition = 'billboardTextAbove';
    } else if (textPosition === constants.BILLBOARD_TEXT_POSITION.OVERLAY) {
      textPosition = 'billboardOverlayText';
    }
    return textPosition;
  }
  
  render() {
    const pod = this.props.pod;
    let podTitle = pod.data.title;
    const podDescription = pod.data.description;
    const podCta = pod.data.action_label;
    let textPosition = this.props.pod.textPosition || '';
    if (pod.data && constants.SUPPORTED_PODS.indexOf(pod.html_template_name) < 0 || !pod.data.params) {
      return <UnSupportedPod />
    }

    return (
      <div>
      <div className={'subscription-promo pod deep-link ' + (textPosition === 'above' ? 'billboardTextAbove' : '')} onClick={() => this.props.onClick(pod)}>
        {/* { this.props.tnc.show_tnc &&
          <div className="subtitle ellipses lines-2 tnc"> {this.props.tnc.tnc.link} </div> //Commenting out this for billboard type of pods as the design won't fit this link.
        } */}
        <div className="pod-details">
          <div className="pod-content">
              {textPosition === 'above' && <div className="header-title-container ellipses line-1">{podTitle}</div>}
              {this.state.imageSrc && <img className={"pod-image" +
                (this.state.isSvg ? " svg-image" : "")}
                src={this.state.imageSrc} />}
              <div className="pod-desc" onClick={() => this.props.onClick(pod)}>
                {(textPosition !== 'above' || !textPosition || textPosition === '') && podTitle && <div ref={el => this.title = el}
                  className={'title deep-link-title ' + (textPosition !== 'above' ? this.getTextPosition() : '')}>{podTitle}</div>}
                {(textPosition !== 'above' || !textPosition || textPosition === '') && podDescription && <div ref={el => this.description = el}
                  className={'description ' + (textPosition !== 'above' ? this.getTextPosition() : '')}>{podDescription}</div>}
                {podCta && <div className={'cta ' + (textPosition !== 'above' || textPosition !== 'overlay' ? this.getTextPosition() : '')}>{podCta}</div>}
              </div>
          </div>
        </div>
      </div>
      </div>
    )
  }
}

export default SubscriptionPromo;


