/**
 * This component renders a List of Lists. Each list is redered as a 
 * carousel of covers. Clicking on any list title will open that list. 
 * Clicking on an item cover will open the item in Native PDP.
 */
import React, { Component } from 'react'
import Spinner from './Spinner'
import ListCarousel from './ListCarousel'
import ContainerFiller from "./ContainerFiller";
import platforms from '../lib/constants/platforms'

import '../../style-dist/components/Lol.css'
import textReplace from '../lib/i18n';
import KEY from '../lib/constants/languages/keys';
if (window.location.href.match(platforms.epd))
  require('../../style-dist/components/Lol.epd.css');
  require('../../style-dist/loading.epd.css');
if (window.location.href.match(platforms.quill))
  require('../../style-dist/components/Lol.quill.css');
  require('../../style-dist/loading.epd.css');


class Lol extends Component {

  constructor() {
    super();

    // Define empty lists to show while loading
    this.loadingLists = [
      { title: ' ', description: ' ', eans: [null, null, null, null, null, null] },
      { title: ' ', description: ' ', eans: [null, null, null, null, null, null] },
      { title: ' ', description: ' ', eans: [null, null, null, null, null, null] },
      { title: ' ', description: ' ', eans: [null, null, null, null, null, null] },
      { title: ' ', description: ' ', eans: [null, null, null, null, null, null] },
      { title: ' ', description: ' ', eans: [null, null, null, null, null, null] }
    ];
    this.isEpd = window.Device.shopPlatform === 'epd' || window.Device.shopPlatform === 'quill';
  }

  componentDidMount() {
    //set scroll position
    var position = sessionStorage.getItem("lolPosition") || 0;
    if (window.paginationComponent && this.isEpd) {
      let pageNo = sessionStorage.getItem("EPDLolPosition") || 1;
      window.paginationComponent.gotoPage(pageNo);
    }
    else {
      window.scroll(0, position);
      let interval = setInterval(() => {
        if (document.readyState === 'complete') {
          clearInterval(interval);
          window.scroll(0, position);
        }
      }, 500);
    }

    sessionStorage.removeItem('lolPosition');
    sessionStorage.removeItem('EPDLolPosition');
  }

  componentWillUnmount() {
    sessionStorage.setItem('lolPosition', window.scrollY);
    if (window.paginationComponent && this.isEpd) {
      let currentPage = window.paginationComponent.getPageNo();
      sessionStorage.setItem('EPDLolPosition', currentPage);
    }
  }

  generateFillers(data) {
    let fillers = [];
    const NO_ROWS_PER_PAGE = 3;
    if(data && data.length % NO_ROWS_PER_PAGE  !== 0) {
      let noOfFillers = NO_ROWS_PER_PAGE - (data.length % NO_ROWS_PER_PAGE);
      for (let i = 0; (i < noOfFillers && noOfFillers < NO_ROWS_PER_PAGE); i++) {
        fillers.push(<ContainerFiller key={i} fillContainer="list-carousel" />);
      }
    }
    return fillers;
  }
  render() {
    let loading = false;
    let data = this.props.lists;
    let spinner = <div/>;
    const loadingAriaAttribs = {
      "aria-busy": true, "aria-live":"off"
    };

    if (this.props.isFetching) {
      loading = true;
      data = this.loadingLists;
      spinner = <Spinner />;
    }
    else if (this.props.isFetchingMore) {
      spinner = <Spinner />
    }
    else if (this.props.lists && this.props.lists.length === 0) {
      return <div className='no-results'>{textReplace(KEY.no_results_found)}</div>
    }
    else {
      spinner = <React.Fragment></React.Fragment>
    }

    return <div {...(loading ? loadingAriaAttribs : {"aria-live":"off"})} className={ 'shop-lol' + (loading ? ' loading' : '') }>
      <div className="lists">
        { 
          data.map((list, i) => (
            <ListCarousel key={ i }
              noEPDSwipe
              index={ i+1 }
              loading={ loading }
              list={ list } 
              eanData={ this.props.eanData } 
              showDescription={ true }
              onCoverClick={ this.props.coverClicked }
              onListClick={ this.props.listClicked } />
          ))

        }
        {spinner}
        {this.isEpd && this.generateFillers(data)}
      </div>
    </div>;
  }
}

export default Lol;



